import client from '../helpers/HttpClient'
import { urlQueryFromObject, numericCompare } from '../helpers/UtilsHelper';

const endpoint = '/venues';
const formDataHeader = {
    headers: {
        'Content-Type': 'multipart/form-data'
    }
}
export default {
    async create(data) {
        let response = await client.post(endpoint, data, formDataHeader)
        return response.data;
    },
    async detail(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.get(path)
        return response.data;
    },
    async update(data, partial = true) {
        const { venueId, formData } = data;
        const path = `${endpoint}/${venueId}`;
        let response = partial ? await client.patch(path, formData, formDataHeader) : await client.put(path, formData, formDataHeader)
        return response.data;
    },
    async delete(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.delete(path)
        return response.data;
    },
    async list(filters) {
        const query = urlQueryFromObject(filters);
        const path = `${endpoint}${query}`;
        let response = await client.get(path)
        return response.data;
    },
    async spaces(venueId) {
        const path = `${endpoint}/${venueId}/spaces`;
        let response = await client.get(path)
        const compare = (x, y) => numericCompare(x.name, y.name)
        response.data.result.sort(compare)

        return response.data;
    }
}
