const actionTypes = {
    GET_BOOKING_LIST_TO_EXPORT: "GET_BOOKING_LIST_TO_EXPORT",
    GET_BOOKING_LIST_TO_EXPORT_SUCCESS: 'GET_BOOKING_LIST_TO_EXPORT_SUCCESS',
    GET_BOOKING_LIST_TO_EXPORT_FAIL: 'GET_BOOKING_LIST_TO_EXPORT_FAIL',

    CLEAR_BOOKING_DATA: "CLEAR_BOOKING_DATA",
}

export default actionTypes

export const getListBookingExport = (filters) => ({ type: actionTypes.GET_BOOKING_LIST_TO_EXPORT, payload: { filters } });
export const clearBookingData = () => ({ type: actionTypes.CLEAR_BOOKING_DATA})
