import { takeLatest, call, put } from "redux-saga/effects";
import { CREATE_PASSWORD_ACTIONS } from '../actions';
import passwordApi from '../../api/passwordApi';
import { toast } from 'react-toastify';

function* createPassword(data) {

    try {
        const { param, callback } = data.payload;
        const payload = yield call(_createPassword.bind(this, param));
        yield put({ type: CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD_SUCCESS, payload: payload })
        callback();
        toast.success("Create new password success! Please login again!");

    } catch (error) {
        yield put({ type: CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD_FAIL, payload: { error } })
        toast.error("Create password failed!");
    }
}

function _createPassword(payload) {
    return passwordApi.createPassword(payload).then(response => {
        return response;
    })
}

export function* watchCreatePasswordAsync() {
    yield takeLatest(CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD, createPassword);
}
