import {
    PROVIDER_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {},
    isAddNewProviderSuccess: false,
    addProviderAccountError: null,
};

export default function providerReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Provider List
        case PROVIDER_ACTIONS.GET_PROVIDER_LIST:
            return {
                ...state,
                loading: true,
            }
        case PROVIDER_ACTIONS.GET_PROVIDER_LIST_SUCCESS:
            return {
                ...state,
                data: payload,
                isAddNewProviderSuccess: false,
                loading: false,
            }
        case PROVIDER_ACTIONS.GET_PROVIDER_LIST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Provider Detail
        case PROVIDER_ACTIONS.GET_PROVIDER_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case PROVIDER_ACTIONS.GET_PROVIDER_DETAIL_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case PROVIDER_ACTIONS.GET_PROVIDER_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        case PROVIDER_ACTIONS.TICK_ON_CHECKBOX:
            const { checked, id } = payload;
            const providerList = state.data.result.map(item => {
                if (item.id === id) {
                    item.checked = checked;
                }
                return item;
            });
            return {
                ...state, data: { ...state.data, result: providerList }
            }
        case PROVIDER_ACTIONS.TICK_ON_ALL_CHECKBOX:
            const { isCheckAll } = payload;
            const providerListCheckAll = state.data.result.map(item => {
                item.checked = isCheckAll;
                return item;
            });
            return {
                ...state, data: { ...state.data, result: providerListCheckAll }
            }

        // Update
        case PROVIDER_ACTIONS.UPDATE_PROVIDER:
            return {
                ...state,
                loading: true,
            }
        case PROVIDER_ACTIONS.UPDATE_PROVIDER_SUCCESS:
            if (state.data.result) {
                const listProvider = [...state.data.result];
                let providerUpdate = listProvider.map(item => item.id === payload.id ? payload : item);
                return {
                    data: { ...state.data, result: providerUpdate },
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case PROVIDER_ACTIONS.UPDATE_PROVIDER_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update Status
        case PROVIDER_ACTIONS.UPDATE_PROVIDER_STATUS:
            return {
                ...state,
                loading: true,
            }
        case PROVIDER_ACTIONS.UPDATE_PROVIDER_STATUS_SUCCESS:
            if (state.data.result) {
                let listProvider = state.data.result;
                let providerUpdate = listProvider.map(item => {
                    if (item.id === payload.id) {
                        item.status = payload.status
                    }
                    return item
                });
                return {
                    data: { ...state.data, result: providerUpdate },
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case PROVIDER_ACTIONS.UPDATE_PROVIDER_STATUS_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Delete List
        case PROVIDER_ACTIONS.DELETE_LIST_PROVIDER:
            return {
                ...state,
                loading: true,
            }
        case PROVIDER_ACTIONS.DELETE_LIST_PROVIDER_SUCCESS:
            const listProvider = [...state.data.result];
            const newPostList = listProvider.filter(item => !payload.ids.includes(item.id));
            return {
                data: { ...state.data, result: newPostList },
                loading: false,
            }
        case PROVIDER_ACTIONS.DELETE_LIST_PROVIDER_FAIL:
            return {
                ...state,
                loading: false,
            }

        case PROVIDER_ACTIONS.CHANGE_PROVIDER_TYPES:
            const { providerTypes } = payload;
            const data = { ...state.data, providerTypes }
            return {
                ...state,
                data,
            }

        case PROVIDER_ACTIONS.CHANGE_PROVIDER_STATUS:
            const { status } = payload;
            const tempData = { ...state.data, status }
            return {
                ...state,
                data: tempData,
            }

        // Add new
        case PROVIDER_ACTIONS.ADD_PROVIDER_ACCOUNT:
            return {
                ...state,
                loading: true,
            }
        case PROVIDER_ACTIONS.ADD_PROVIDER_ACCOUNT_SUCCESS:
            return {
                ...state,
                isAddNewProviderSuccess: true,
                addProviderAccountError: null,
                loading: false,
            }
        case PROVIDER_ACTIONS.ADD_PROVIDER_ACCOUNT_FAIL:
            const { error } = payload;
            return {
                ...state,
                addProviderAccountError: error,
            }

        case PROVIDER_ACTIONS.DELETE_ERROR:
            const temp = state.addProviderAccountError;
            if (temp) {
                delete temp[payload.error]
            }
            return {
                ...state,
                addProviderAccountError: temp,
                loading: false,
            }

        default:
            return state;
    }
}
