import {
    APPOINTMENT_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {}
};

export default function appointmentReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        // Get APPOINTMENT List
        case APPOINTMENT_ACTIONS.GET_CLIENT_APPOINTMENT:
            return {
                ...state,
                loading: true,
            }
        case APPOINTMENT_ACTIONS.GET_CLIENT_APPOINTMENT_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case APPOINTMENT_ACTIONS.GET_CLIENT_APPOINTMENT_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
