import React, { Suspense } from "react";
// react library for routing
import { Route, Switch, Redirect } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import routes from "routes.js";

const loading = () => <div className="animated fadeIn pt-3 text-center">Loading...</div>;

class Auth extends React.Component {
  componentDidMount() {
    document.body.classList.add("bg-white");
  }
  componentWillUnmount() {
    document.body.classList.remove("bg-white");
  }
  componentDidUpdate(e) {
    if (e.history.pathname !== e.location.pathname) {
      document.documentElement.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
      this.refs.mainContent.scrollTop = 0;
    }
  }
  getRoutes = routes => {
    return routes.map((prop, key) => {
      if (prop.collapse) {
        return this.getRoutes(prop.views);
      }
      if (prop.layout === "/auth") {
        return (
          <Route
            path={prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  getMapRouter = (path) => {
    switch (path) {
      case "/admin":
        return routes.admin
      case "/provider":
        return routes.provider
      case "/client":
        return routes.client
      default:
        return routes.admin
    }
  }

  getRidirectRoute = (path) => {
    switch (path) {
      case "/admin":
        return <Redirect from="/admin" to="/admin/login" />
      case "/provider":
        return <Redirect from="/provider" to="/provider/login" />
      case "/client":
        return <Redirect from="/client" to="/client/login" />
      default:
        return null
    }
  }

  render() {
    const path = this.props.match && this.props.match.path;
    const inputRoutes = this.getMapRouter(path);
    return (
      <>
      <ToastContainer autoClose={2000} position={toast.POSITION.TOP_RIGHT} progressClassName="Toastify__progress-bar--rtl" />
        <div className="main-content main-content__bg-transparent" ref="mainContent">
          <Suspense fallback={loading()}>
            <Switch>
              {this.getRoutes(inputRoutes)}
              {this.getRidirectRoute(path)}
            </Switch>
          </Suspense>
        </div>
      </>
    );
  }
}

export default Auth;
