import { call, put, takeLatest } from "redux-saga/effects";
import { PROVIDER_ACTIONS } from "../actions";
import providerApi from '../../api/providerApi';
import providerBookingApi from '../../api/providerBookingApi';
import { toast } from 'react-toastify';

function* getList({ payload }) {
    const { filters } = payload;
    try {
        const data = yield call(_getList.bind(this, filters))
        yield put({ type: PROVIDER_ACTIONS.GET_PROVIDER_LIST_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: PROVIDER_ACTIONS.GET_PROVIDER_LIST_FAIL })
    }
}

function _getList(filters) {
    return providerBookingApi.list(filters).then(response => {
        return response
    })
}

function* getDetail({ payload }) {
    const { id } = payload;
    try {
        const data = yield call(_getDetail.bind(this, id))
        yield put({ type: PROVIDER_ACTIONS.GET_PROVIDER_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: PROVIDER_ACTIONS.GET_PROVIDER_DETAIL_FAIL })
    }
}

function _getDetail(id) {
    return providerApi.detail(id).then(response => {
        return response
    })
}

function* update({ payload }) {
    const { params } = payload
    try {
        const data = yield call(_update.bind(this, params))
        yield put({ type: PROVIDER_ACTIONS.UPDATE_PROVIDER_SUCCESS, payload: data })
        toast.success("Update Provider success !");

    } catch (error) {
        yield put({ type: PROVIDER_ACTIONS.UPDATE_PROVIDER_FAIL })
        toast.error("Update Provider fail !");
    }
}

function* updateStatus({ payload }) {
    const { params } = payload
    try {
        const data = yield call(_update.bind(this, params))
        yield put({ type: PROVIDER_ACTIONS.UPDATE_PROVIDER_STATUS_SUCCESS, payload: data })
        toast.success("Update Provider Status success !");

    } catch (error) {
        yield put({ type: PROVIDER_ACTIONS.UPDATE_PROVIDER_STATUS_FAIL })
        toast.error("Update Provider Status fail !");
    }
}

function _update(payload) {
    return providerApi.update(payload).then(response => {
        return response
    })
}

function* deleteList({ payload }) {
    const { params } = payload
    try {
        yield call(_deleteList.bind(this, params))
        yield put({ type: PROVIDER_ACTIONS.DELETE_LIST_PROVIDER_SUCCESS, payload: params })
        toast.success("Delete Provider success !");

    } catch (error) {
        yield put({ type: PROVIDER_ACTIONS.DELETE_LIST_PROVIDER_FAIL })
        toast.error("Delete Provider fail !");
    }
}

function _deleteList(payload) {
    return providerApi.delete(payload).then(response => {
        return response
    })
}

function* addProviderAccount({ payload }) {
    const { params } = payload
    try {
        const data = yield call(_addProviderAccount.bind(this, params))
        yield put({ type: PROVIDER_ACTIONS.ADD_PROVIDER_ACCOUNT_SUCCESS, payload: data })
        toast.success("Add new provider account success !");

    } catch (error) {
        let errorData = error.response.data;
        yield put({ type: PROVIDER_ACTIONS.ADD_PROVIDER_ACCOUNT_FAIL, payload: { error: errorData } })
        toast.error("Add new provider account fail !");
    }
}

function _addProviderAccount(payload) {
    return providerApi.providerRegister(payload).then(response => {
        return response
    })
}

export function* watchProviderAsync() {
    yield takeLatest(PROVIDER_ACTIONS.GET_PROVIDER_LIST, getList);
    yield takeLatest(PROVIDER_ACTIONS.GET_PROVIDER_DETAIL, getDetail);
    yield takeLatest(PROVIDER_ACTIONS.UPDATE_PROVIDER, update);
    yield takeLatest(PROVIDER_ACTIONS.UPDATE_PROVIDER_STATUS, updateStatus);
    yield takeLatest(PROVIDER_ACTIONS.DELETE_LIST_PROVIDER, deleteList);
    yield takeLatest(PROVIDER_ACTIONS.ADD_PROVIDER_ACCOUNT, addProviderAccount);

}
