const actionTypes = {
    GET_CLIENT_INVOICES: "GET_CLIENT_INVOICES",
    GET_CLIENT_INVOICES_SUCCESS: 'GET_CLIENT_INVOICES_SUCCESS',
    GET_CLIENT_INVOICES_FAIL: 'GET_CLIENT_INVOICES_FAIL',

    GET_BOOKING_INVOICES: "GET_BOOKING_INVOICES",
    GET_BOOKING_INVOICES_SUCCESS: 'GET_BOOKING_INVOICES_SUCCESS',
    GET_BOOKING_INVOICES_FAIL: 'GET_BOOKING_INVOICES_FAIL',

    CLIENT_ADD_PAYMENT: "CLIENT_ADD_PAYMENT",
    CLIENT_ADD_PAYMENT_SUCCESS: 'CLIENT_ADD_PAYMENT_SUCCESS',
    CLIENT_ADD_PAYMENT_FAIL: 'CLIENT_ADD_PAYMENT_FAIL',

    CREATE_APPOINTMENT_INVOICE: "CREATE_APPOINTMENT_INVOICE",
    CREATE_APPOINTMENT_INVOICE_SUCCESS: "CREATE_APPOINTMENT_INVOICE_SUCCESS",
    CREATE_APPOINTMENT_INVOICE_FAIL: "CREATE_APPOINTMENT_INVOICE_FAIL",

    UPDATE_INVOICE_STATUS: "UPDATE_INVOICE_STATUS",
    UPDATE_INVOICE_STATUS_SUCCESS: "UPDATE_INVOICE_STATUS_SUCCESS",
    UPDATE_INVOICE_STATUS_FAIL: "UPDATE_INVOICE_STATUS_FAIL",
}

export default actionTypes

export const getListInvoice = (filters) => ({ type: actionTypes.GET_CLIENT_INVOICES, payload: { filters } })
export const getBookingInvoices = (bookingId) => ({ type: actionTypes.GET_BOOKING_INVOICES, payload: { bookingId } })
export const createAppointmentInvoice = (appointment) => ({ type: actionTypes.CREATE_APPOINTMENT_INVOICE, payload: { appointment } })
export const updateInvoiceStatus = (status, invoiceId) => ({ type: actionTypes.UPDATE_INVOICE_STATUS, payload: { status, invoiceId } })

export const addPayment = (data, invoiceId) => ({ type: actionTypes.CLIENT_ADD_PAYMENT, payload: { data, invoiceId } })

