import React, { Component } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CardImg
} from 'reactstrap';

class ImageSlider extends Component {
  constructor(props) {
    super(props);
    this.state = { activeIndex: 0 };
  }

  onExiting = () => {
    this.animating = true;
  }

  onExited = () => {
    this.animating = false;
  }

  next = (length) => {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === length - 1 ? 0 : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous = (length) => {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === 0 ? length - 1 : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex = (newIndex) => {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

  componentWillReceiveProps(nextProp) {
    const currentLength = nextProp.listImage.length - 1;
    const lastLength = this.props.listImage.length - 1;
    const { activeIndex } = this.state;
    if (activeIndex > currentLength) {
      this.goToIndex(currentLength);
    }

    if (currentLength > lastLength) {
      this.goToIndex(0);
    }
  }

  render() {
    const { activeIndex } = this.state;
    const { handleDelete, listImage } = this.props;
    const length = listImage.length;
    const slides = listImage.map((item, index) => {
      return (
        <CarouselItem
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.id}
        >
          <div className="form__image-slider">
            <i className="ni ni-fat-remove" onClick={() => handleDelete(item.id, index)}/>
            <CardImg
              src={item.file}
              top
            />
          </div>
        </CarouselItem>
      );
    });

    return (
      <Carousel
        activeIndex={activeIndex}
        next={() => this.next(length)}
        previous={() =>this.previous(length)}
        interval={false}
      >
        {length > 1 && <CarouselIndicators items={listImage} activeIndex={activeIndex} onClickHandler={this.goToIndex} />}
        {slides}
        {length > 1 && <CarouselControl direction="prev" directionText="Previous" onClickHandler={() => this.previous(length)} />}
        {length > 1 && <CarouselControl direction="next" directionText="Next" onClickHandler={() => this.next(length)} />}
      </Carousel>
    );
  }
}


export default ImageSlider;
