import {
    BOOKING_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    loadBookingData: false,
    isOpenSideBar: false,

    bookingData: {},
    venueData: [],
    spaceData: [],
    spaceDataForModal: [],
    modalVenue: {},
    bookingError: null,
    bookingDataExport: [],
    providerData: {},
    clients: [],
    currentBooking: null
};

export default function bookingReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Booking List for export
        case BOOKING_ACTIONS.GET_BOOKING_LIST_EXPORT:
            return {
                ...state,
                loadBookingData: true,
            }
        case BOOKING_ACTIONS.GET_BOOKING_LIST_EXPORT_SUCCESS:
            return {
                ...state,
                loadBookingData: false,
            }
        case BOOKING_ACTIONS.GET_BOOKING_LIST_EXPORT_FAIL:
            return {
                ...state,
                loadBookingData: false,
            }

        // Get Booking List
        case BOOKING_ACTIONS.GET_BOOKING_LIST:
            return {
                ...state,
                loadBookingData: true,
            }
        case BOOKING_ACTIONS.GET_BOOKING_LIST_SUCCESS:
            return {
                ...state,
                bookingData: payload,
                loadBookingData: false,
            }
        case BOOKING_ACTIONS.GET_BOOKING_LIST_FAIL:
            return {
                ...state,
                loadBookingData: false,
            }

        // Get Venue list
        case BOOKING_ACTIONS.GET_VENUE_LIST:
            return {
                ...state,
                loading: true
            }
        case BOOKING_ACTIONS.GET_VENUE_LIST_SUCCESS:
            return {
                ...state,
                venueData: payload.result,
                loading: false
            }
        case BOOKING_ACTIONS.GET_VENUE_LIST_FAIL:
            return {
                ...state,
                loading: false
            }


        // Get space list
        case BOOKING_ACTIONS.GET_SPACE_LIST:
            return {
                ...state,
                loading: true
            }
        case BOOKING_ACTIONS.GET_SPACE_LIST_SUCCESS:
            return {
                ...state,
                spaceData: payload.result,
                loading: false
            }
        case BOOKING_ACTIONS.GET_SPACE_LIST_FAIL:
            return {
                ...state,
                loading: false
            }

        case BOOKING_ACTIONS.GET_SPACE_LIST_FOR_MODAL:
            return {
                ...state,
                loading: true
            }
        case BOOKING_ACTIONS.GET_SPACE_LIST_FOR_MODAL_SUCCESS:
            return {
                ...state,
                spaceDataForModal: payload[0].result,
                modalVenue: payload[1],
                loading: false
            }
        case BOOKING_ACTIONS.GET_SPACE_LIST_FOR_MODAL_FAIL:
            return {
                ...state,
                loading: false
            }

        case BOOKING_ACTIONS.GET_CLIENT_LIST:
            return {
                ...state,
                loading: true
            }
        case BOOKING_ACTIONS.GET_CLIENT_LIST_SUCCESS:
            // console
            return {
                ...state,
                clients: payload.clients,
                loading: false
            }
        case BOOKING_ACTIONS.GET_CLIENT_LIST_FAIL:
            return {
                ...state,
                loading: false
            }
        // Set Space
        case BOOKING_ACTIONS.SET_SPACE_LIST:
            return {
                ...state,
                spaceData: []
            }

        // Delete Booking
        case BOOKING_ACTIONS.DELETE_BOOKING:
            return {
                ...state,
                loadBookingData: true
            }
        case BOOKING_ACTIONS.DELETE_BOOKING_SUCCESS:
            const newBookingList = state.bookingData.result.filter(item => item.id !== payload);
            return {
                ...state,
                bookingData: { ...state.bookingData, result: newBookingList },
                loadBookingData: false,
                isOpenSideBar: false,
            }
        case BOOKING_ACTIONS.DELETE_BOOKING_FAIL:
            return {
                ...state,
                loadBookingData: false
            }

        // Cancel Booking
        case BOOKING_ACTIONS.CANCEL_BOOKING:
            return {
                ...state,
                loadBookingData: true
            }
        case BOOKING_ACTIONS.CANCEL_BOOKING_SUCCESS:
            const newList = state.bookingData.result.map(item => item.id === payload.id ? payload : item);
            return {
                ...state,
                bookingData: { ...state.bookingData, result: newList },
                loadBookingData: false,
                isOpenSideBar: false,
            }
        case BOOKING_ACTIONS.CANCEL_BOOKING_FAIL:
            return {
                ...state,
                loadBookingData: false
            }

        // Update Booking Appointment
        case BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT:
            return {
                ...state,
                loadBookingData: true
            }
        case BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT_SUCCESS:
            const { appointment } = payload;
            const tempList = state.bookingData.result
            let listUpdate = tempList.map(booking => {
                if (booking.id === appointment.booking) {
                    booking.appointments = booking.appointments.filter(app => app.id !== appointment.id)
                    booking.appointments.push(appointment)
                }
                return { ...booking }
            });
            return {
                ...state,
                bookingData: { ...state.bookingData, result: listUpdate },
                loadBookingData: false,
            }
        case BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT_FAIL:
            const { error } = payload;
            return {
                ...state,
                bookingError: error,
                loadBookingData: false
            }

        case BOOKING_ACTIONS.DELETE_ERROR:
            return {
                ...state,
                bookingError: null,
                loadBookingData: false
            }
        case BOOKING_ACTIONS.ADD_BOOKING:
            return {
                ...state,
                loadBookingData: true,
            }
        case BOOKING_ACTIONS.ADD_BOOKING_SUCCESS:
            return {
                ...state,
                loadBookingData: false,
            }
        case BOOKING_ACTIONS.ADD_BOOKING_FAIL:
            return {
                ...state,
                bookingError: payload.error,
                loadBookingData: false
            }

        case BOOKING_ACTIONS.TOGGLE_SIDE_BAR:
            const { isOpenSideBar } = payload
            return {
                ...state,
                isOpenSideBar: isOpenSideBar
            }

        // Get Provider Detail
        case BOOKING_ACTIONS.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR:
            return {
                ...state,
                getProviderLoading: true,
            }
        case BOOKING_ACTIONS.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_SUCCESS:
            return {
                ...state,
                providerData: payload,
                getProviderLoading: false,
            }
        case BOOKING_ACTIONS.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_FAIL:
            return {
                ...state,
                getProviderLoading: false,
            }

        default:
            return state;
    }
}
