import update from "immutability-helper";
import _ from "lodash";
import moment from "moment";
import actions from "../actions/providerDashboardActions";
import { bookingSteps } from "../../constants";

let initState = () => ({
  calendar: null,
  loading: false,
  tier: 1,
  isShowAddBooking: false,
  validSlotDuration: 1 * 60 * 60 * 1000,
  bookingError: null,
  appointmentError: null,
  venues: [],
  spaces: [],
  spacesForModal: null,
  bookings: [],
  globalBookings: [],
  clients: [],
  availableSlots: [],
  appointments: [],
  isSelectAllRoom: false,
  searchedSlots: false,

  modalVenue: {},
  selectedVenue: null,
  selectedSpaces: [],
  currentBooking: null,
  selectedBookingId: null,
  step: bookingSteps.BOOKING,

  activeStart: new Date(),
  activeEnd: new Date(),
  dateTitle: "",
  dayBookings: {
    date: "",
    bookings: [],
  },
});

export default function providerDashboardReducer(state = initState(), action) {
  let { type, payload } = action;
  switch (type) {
    case actions.SET_VENUES:
      return {
        ...state,
        venues: payload.venues,
      };
    case actions.SET_SPACES:
      return {
        ...state,
        spaces: payload.spaces,
      };
    case actions.INIT_DATA: {
      const { calendar } = payload;
      const { title, activeStart, activeEnd } = calendar.view;
      return {
        ...state,
        loading: true,
        activeStart,
        activeEnd,
        calendar,
        dateTitle: title,
      };
    }
    case actions.INIT_DATA_SUCCESS: {
      const { venues, spaces, bookings, globalBookings, clients } = payload;
      const selectedVenue = venues[0].id;
      return {
        ...state,
        venues,
        spaces,
        selectedVenue,
        bookings,
        globalBookings,
        clients,
        loading: false,
      };
    }
    case actions.CHANGE_SPACES_SUCCESS: {
      const { spaceIds } = payload;
      const isSelectAllRoom =
        spaceIds.length === 0 ? false : state.isSelectAllRoom;
      return {
        ...state,
        selectedSpaces: spaceIds,
        isSelectAllRoom,
      };
    }
    case actions.CHANGE_VENUE:
    case actions.CHANGE_VENUE_FOR_MODAL: {
      return {
        ...state,
        loading: true,
      };
    }
    case actions.CHANGE_VENUE_SUCCESS: {
      const { venueId, spaces } = payload;

      return {
        ...state,
        spaces,
        selectedVenue: venueId,
        loading: false,
        availableSlots: [],
        selectedSpaces: [],
        searchedSlots: false,
        isSelectAllRoom: false,
      };
    }
    case actions.CHANGE_VENUE_FOR_MODAL_SUCCESS: {
      const { spaces, venue } = payload;

      return {
        ...state,
        spacesForModal: spaces,
        modalVenue: venue,
        loading: false,
      };
    }

    case actions.ADD_NEW_BOOKING: {
      // TODO: Fix for when opening from calendar month view
      const { dateTimeInfo } = payload;
      let startTime = dateTimeInfo.startStr;
      let endTime = dateTimeInfo.endStr;
      const ONE_HOUR = 60 * 60 * 1000;
      if (dateTimeInfo.allDay) {
        endTime = startTime + "T17:00:00";
        startTime = startTime + "T09:00:00";
      } else if (dateTimeInfo.end - dateTimeInfo.start < ONE_HOUR) {
        endTime = moment(startTime).add("hour", 1).format();
      }

      const currentBooking = { startTime, endTime };
      return {
        ...state,
        currentBooking,
        isShowAddBooking: true,
      };
    }
    case actions.ADD_NEW_BOOKING_CANCEL: {
      return {
        ...state,
        isShowAddBooking: false,
        appointments: [],
        step: bookingSteps.BOOKING,
        bookingError: null,
      };
    }
    case actions.SET_USER_TIER: {
      const { tier } = payload;
      return {
        ...state,
        tier,
      };
    }
    case actions.BOOK_SPACE: {
      return {
        ...state,
        loading: true,
      };
    }
    case actions.BOOK_SPACE_SUCCESS: {
      const { newBooking } = payload;
      newBooking.appointments = [];
      return update(state, {
        bookings: { $push: [newBooking] },
        // isShowAddBooking: { $set: false },
        selectedBookingId: { $set: newBooking.id },
        loading: { $set: false },
        step: { $set: bookingSteps.APPOINTMENT },
        currentBooking: { $set: newBooking },
      });
    }
    case actions.BOOK_SPACE_FAIL: {
      const { error } = payload;
      return {
        ...state,
        bookingError: error,
        loading: false,
      };
    }
    case actions.CHANGE_AVAILABLE_SLOT_SUCCESS: {
      const { availableSlots } = payload;
      return {
        ...state,
        availableSlots,
        searchedSlots: true,
      };
    }
    case actions.LOOKUP_AVAILABLE_SLOT: {
      const { duration = state.validSlotDuration } = payload || {};
      return {
        ...state,
        validSlotDuration: duration,
        loading: true,
      };
    }
    case actions.LOOKUP_AVAILABLE_SLOT_SUCCESS: {
      const { availableSlots } = payload;
      return {
        ...state,
        availableSlots,
        loading: false,
        searchedSlots: true,
      };
    }
    case actions.CLEAR_LOOKUP_AVAILABLE_SLOT: {
      return {
        ...state,
        availableSlots: [],
        searchedSlots: false,
      };
    }
    case actions.ON_CALENDAR_CHANGE: {
      const { title, activeStart, activeEnd } = state.calendar.view;
      return {
        ...state,
        activeStart,
        activeEnd,
        dateTitle: title,
        searchedSlots: false,
      };
    }
    case actions.RELOAD_BOOKINGS: {
      return {
        ...state,
        loading: true,
      };
    }
    case actions.RELOAD_BOOKINGS_SUCCESS: {
      const { bookings, globalBookings } = payload;
      return {
        ...state,
        bookings,
        globalBookings,
        loading: false,
        searchedSlots: false,
      };
    }
    case actions.RELOAD_BOOKINGS_FAIL: {
      // TODO
      return {
        ...state,
        loading: false,
        searchedSlots: false,
      };
    }
    case actions.ADD_APPOINTMENT_SUCCESS: {
      const { newAppointment } = payload;
      let newState = update(state, {
        appointments: { $push: [newAppointment] },
      });
      const [cbg, bk, gbk] = manageAppointmentToBookings(
        state,
        newAppointment,
        "add"
      );

      newState = update(newState, {
        currentBooking: { $set: cbg },
        bookings: { $set: bk },
        globalBookings: { $set: gbk },
      });

      const clients = state.clients;
      const newClientId = _.get(newAppointment, "client.id", null);
      if (newClientId && !clients.some((client) => client.id === newClientId)) {
        newState = update(newState, {
          clients: { $push: [newAppointment.client] },
        });
      }

      return newState;
    }
    case actions.ADD_APPOINTMENT_FAIL: {
      const { error } = payload;
      return {
        ...state,
        appointmentError: error,
      };
    }
    case actions.CLEAR_APPOINTMENt_ERROR: {
      return {
        ...state,
        appointmentError: null,
      };
    }
    case actions.DELETE_APPOINTMENT_SUCCESS: {
      const { appointment } = payload;
      // const appointmentId = appointment.id;
      // const appointmentIndex = state.appointments.findIndex(ap => ap.id === appointmentId);
      const [cbk, bks, gbks] = manageAppointmentToBookings(
        state,
        appointment,
        "delete"
      );
      return update(state, {
        // appointments: { $splice: [[appointmentIndex, 1]] }
        currentBooking: { $set: cbk },
        bookings: { $set: bks },
        globalBookings: { $set: gbks },
      });
    }
    case actions.TOGGLE_ALL_ROOM: {
      const isSelectAllRoom = !state.isSelectAllRoom;
      let selectedSpaces = [];
      if (isSelectAllRoom) {
        selectedSpaces = (state.spaces || []).map((s) => s.id);
      }

      return {
        ...state,
        isSelectAllRoom,
        selectedSpaces,
      };
    }

    case actions.EDIT_APPOINTMENT_MODAL: {
      return {
        ...state,
        isShowAddBooking: true,
        step: bookingSteps.APPOINTMENT,
        currentBooking: action.payload.data,
        selectedBookingId: action.payload.data.id,
      };
    }

    case actions.EDIT_APPOINTMENT_SUCCESS: {
      const { editedAppointment } = action.payload;

      const [cb, bkings, gBkings] = manageAppointmentToBookings(
        state,
        editedAppointment,
        "edit"
      );

      return {
        ...state,
        currentBooking: cb,
        selectedBookingId: action.payload.editedAppointment.id,
        bookings: bkings,
        globalBookings: gBkings,
        // appointments: apps
      };
    }

    case actions.EDIT_APPOINTMENT_FAIL: {
      const { error } = payload;
      return {
        ...state,
        appointmentError: error,
      };
    }

    case actions.DAY_BOOKINGS_SUCCESS: {
      return {
        ...state,
        dayBookings: {
          bookings: payload.bookings,
          date: payload.date,
        },
      };
    }
    case actions.DAY_BOOKINGS_FAILED: {
      return {
        ...state,
        appointmentError: payload.error,
      };
    }

    default:
      return state;
  }
}

function manageAppointmentToBookings(state, lookingAppointment, action) {
  function manageAppointment(appointments, action) {
    if (!appointments) {
      return [];
    }
    if (action === "add") {
      appointments.push(lookingAppointment);
      return appointments;
    }
    const bAppIndex = appointments.findIndex(
      (appointment) => appointment.id === lookingAppointment.id
    );
    if (bAppIndex >= 0) {
      switch (action) {
        case "edit":
          appointments.splice(bAppIndex, 1, lookingAppointment);
          break;
        case "delete":
          appointments.splice(bAppIndex, 1);
          break;
        default:
          break;
      }
    }
    return appointments;
  }

  function findBooking(bookings, action) {
    const bkIndex = bookings.findIndex(
      (b) => b.id === lookingAppointment.booking
    );
    if (bkIndex >= 0) {
      bookings[bkIndex].appointments = manageAppointment(
        bookings[bkIndex].appointments,
        action
      );
    }
    return bookings;
  }
  //need to finish including appointments into bookings
  const currentBooking = JSON.parse(JSON.stringify(state.currentBooking));
  // let apps = JSON.parse(JSON.stringify(state.appointments));
  let bookings = JSON.parse(JSON.stringify(state.bookings));
  let globalBookings = JSON.parse(JSON.stringify(state.globalBookings));
  // apps = findAppointment(apps);
  currentBooking.appointments = manageAppointment(
    currentBooking.appointments,
    action
  );
  bookings = findBooking(bookings, action);
  globalBookings = findBooking(globalBookings, action);
  return [currentBooking, bookings, globalBookings];
}
