import client from '../helpers/HttpClient'
const endpoint = '/connect-stripe-account';

export default {
    async connectInfo() {
        const path = `${endpoint}`;
        let response = await client.get(path);
        return response.data;
    },
    async connectAccount(code) {
        const path = `${endpoint}`;
        let response = await client.post(path, { code });
        return response.data;
    },
    async disconnectAccount() {
        const path = `${endpoint}`;
        let response = await client.delete(path);
        return response.data;
    },
    async getCustomer() {
        const path = '/stripe-customer'
        let response = await client.get(path);
        return response.data;
    },
    async addCard(cardToken) {
        const path = '/cards';
        let response = await client.post(path, { cardToken });
        return response.data
    },
    async deleteCard(cardId) {
        const path = `/cards/${cardId}`;
        let response = await client.delete(path);
        return response.data
    },
    async updateDefaultCard(cardId) {
        const path = `/stripe-customer`;
        let response = await client.put(path, { defaultSource: cardId });
        return response.data
    }
}
