const actionTypes = {
    GET_PROVIDER_NOTIFICATIONS: "GET_PROVIDER_NOTIFICATIONS",
    GET_PROVIDER_NOTIFICATIONS_SUCCESS: 'GET_PROVIDER_NOTIFICATIONS_SUCCESS',
    GET_PROVIDER_NOTIFICATIONS_FAIL: 'GET_PROVIDER_NOTIFICATIONS_FAIL',

    UPDATE_PROVIDER_NOTIFICATIONS: "UPDATE_PROVIDER_NOTIFICATIONS",
    UPDATE_PROVIDER_NOTIFICATIONS_SUCCESS: 'UPDATE_PROVIDER_NOTIFICATIONS_SUCCESS',
    UPDATE_PROVIDER_NOTIFICATIONS_FAIL: 'UPDATE_PROVIDER_NOTIFICATIONS_FAIL',

    MARK_ALL_READ_NOTIFICATIONS: "MARK_ALL_READ_NOTIFICATIONS",
    MARK_ALL_READ_NOTIFICATIONS_SUCCESS: 'MARK_ALL_READ_NOTIFICATIONS_SUCCESS',
    MARK_ALL_READ_NOTIFICATIONS_FAIL: 'MARK_ALL_READ_NOTIFICATIONS_FAIL',

}

export default actionTypes

export const getListNotification = () => ({ type: actionTypes.GET_PROVIDER_NOTIFICATIONS })
export const updateNotification = (id, data) => ({ type: actionTypes.UPDATE_PROVIDER_NOTIFICATIONS, payload: { id, data } })
export const markAllReadNotification = (id, data) => ({ type: actionTypes.MARK_ALL_READ_NOTIFICATIONS, payload: { id, data } })