import { CREATE_PASSWORD_ACTIONS } from '../actions';

let initState = {
    loading: false,
}

export default function commonReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // --------------- CREATE PASSWORD WITH TOKEN----------------------
        case CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD:
            return Object.assign({}, state, {
                isAuthenticated: false,
                haveError: false,
                loading: true,
            });
        case CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD_SUCCESS:
            return Object.assign({}, state, {
                isAuthenticated: false,
                data: payload,
                haveError: false,
                loading: false,
            });
        case CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD_FAIL:
            return Object.assign({}, state, {
                isAuthenticated: false,
                data: payload,
                haveError: true,
                loading: false,
            });
        default:
            return state;
    }
}
