import { takeLatest, select } from "redux-saga/effects";
import { COMMON_ACTIONS } from '../actions';
import { setPrivacy } from '../../helpers/StoreHelper';

function* togglePrivacy() {
    const { privacy } = yield select(state => state.common)
    setPrivacy(privacy);
}

export function* watchCommonAsync() {
    yield takeLatest(COMMON_ACTIONS.TOGGLE_PRIVACY, togglePrivacy);
}
