import {
    NAV_BARS_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {}
};

export default function navBarsReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        // Get Admin Detail
        case NAV_BARS_ACTIONS.NAV_BARS_GET_ADMIN_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case NAV_BARS_ACTIONS.NAV_BARS_GET_ADMIN_DETAIL_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case NAV_BARS_ACTIONS.NAV_BARS_GET_ADMIN_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Provider Detail
        case NAV_BARS_ACTIONS.NAV_BARS_GET_PROVIDER_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case NAV_BARS_ACTIONS.NAV_BARS_GET_PROVIDER_DETAIL_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case NAV_BARS_ACTIONS.NAV_BARS_GET_PROVIDER_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Client Detail
        case NAV_BARS_ACTIONS.NAV_BARS_GET_CLIENT_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case NAV_BARS_ACTIONS.NAV_BARS_GET_CLIENT_DETAIL_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case NAV_BARS_ACTIONS.NAV_BARS_GET_CLIENT_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
