import { call, put, takeLatest } from "redux-saga/effects";
import { EXPORT_BOOKING_ACTIONS } from "../actions";
import bookingApi from '../../api/bookingApi';

function* getList({ payload }) {
    const { filters } = payload;
    try {
        const data = yield call(_getList.bind(this, filters))
        yield put({ type: EXPORT_BOOKING_ACTIONS.GET_BOOKING_LIST_TO_EXPORT_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: EXPORT_BOOKING_ACTIONS.GET_BOOKING_LIST_TO_EXPORT_FAIL })
    }
}

function _getList(filters) {
    return bookingApi.list(filters).then(response => {
        return response
    })
}

export function* watchExportBookingAsync() {
    yield takeLatest(EXPORT_BOOKING_ACTIONS.GET_BOOKING_LIST_TO_EXPORT, getList);
}
