import React from 'react'
import _ from 'lodash'
import classNames from 'classnames/bind'
import cons from '../../constants'

const ErrorSummary = (props) => {
    const { error, className } = props;
    if (_.isEmpty(error)) {
        return ""
    }
    try {
        let errors = []
        for (let field in error) {
            const mapError = cons.ERRORS_MESSAGE[field];
            if (mapError) {
                errors.push(<li><b>{mapError.nameDisplay}{mapError.nameDisplay ? ":" : ""}</b> {mapError.message || error[field][0]}</li>);
            } else {
                errors.push(<p>{error[field][0]}</p>);
            }
        }
        return <div className={classNames("common-error text-danger", className)}>
            {errors}
        </div>
    } catch (error) {
        return <div className={classNames("common-error text-danger", className)}>
            {String(error)}
        </div>
    }
}

export default ErrorSummary