import client from '../helpers/HttpClient'
import { urlQueryFromObject } from '../helpers/UtilsHelper';
const endpoint = '/invoices';

export default {
    async list(filters) {
        const query = urlQueryFromObject(filters);
        let path = `${endpoint}${query}`;
        let response = await client.get(path);
        return response.data;
    },
    async create() {
        let path = `${endpoint}-create`;
        let response = await client.post(path)
        return response.data;
    },
    async detail(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.get(path)
        return response.data;
    },
    async update(data, partial = true) {
        const { id } = data;
        const path = `${endpoint}/${id}`;
        let response = partial ? await client.patch(path, data) : await client.put(path, data)
        return response.data;
    },
    async delete(data) {
        const path = `${endpoint}/bulk-delete`;
        let response = await client.post(path, data)
        return response.data;
    },
    async tier() {
        let path = `${endpoint}/tiers/my-tier`;
        let response = await client.get(path);
        return response.data;
    },
    async triggerCronJob(data) {
        const triggerDate = data.payload;
        let path = `/charge-invoices?date=${triggerDate}`;
        let response = await client.get(path);
        return response.data;
    },

    async previewInvoice() {
        let path = `/preview-invoice`;
        let response = await client.get(path);
        return response.data;
    }
}
