import {
    BILLING_ACTIONS
} from "../actions";
import cons from '../../constants'

const initState = {
    loading: false,
    data: {},
    detailData: {},
    manualInvoiceList: {}
};

export default function billingReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Booking Invoice List
        case BILLING_ACTIONS.GET_BOOKING_INVOICE_LIST:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.GET_BOOKING_INVOICE_LIST_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case BILLING_ACTIONS.GET_BOOKING_INVOICE_LIST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Booking Invoice Detail
        case BILLING_ACTIONS.GET_BOOKING_INVOICE_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.GET_BOOKING_INVOICE_DETAIL_SUCCESS:
            return {
                ...state,
                detailData: payload,
                loading: false,
            }
        case BILLING_ACTIONS.GET_BOOKING_INVOICE_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update
        case BILLING_ACTIONS.UPDATE_BOOKING_INVOICE:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_SUCCESS:
            if (state.data.result) {
                const listBilling = [...state.data.result];
                let billingUpdate = listBilling.map(item => item.id === payload.id ? payload : item);
                return {
                    data: { ...state.data, result: billingUpdate },
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update Status
        case BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_STATUS:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_STATUS_SUCCESS:
            if (state.data.result) {
                let listBilling = state.data.result;
                let billingUpdate = listBilling.map(item => {
                    if (item.id === payload.id) {
                        item.status = payload.status
                    }
                    return item
                });
                return {
                    data: { ...state.data, result: billingUpdate },
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_STATUS_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Trigger cron job
        case BILLING_ACTIONS.TRIGGER_CRON_JOB:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.TRIGGER_CRON_JOB_SUCCESS:
            return {
                ...state,
                loading: false,
            }
        case BILLING_ACTIONS.TRIGGER_CRON_JOB_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Manual Invoice List
        case BILLING_ACTIONS.GET_MANUAL_INVOICE_LIST:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.GET_MANUAL_INVOICE_LIST_SUCCESS:
            return {
                ...state,
                manualInvoiceList: payload,
                loading: false,
            }
        case BILLING_ACTIONS.GET_MANUAL_INVOICE_LIST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Booking Invoice Detail
        case BILLING_ACTIONS.GET_MANUAL_INVOICE_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.GET_MANUAL_INVOICE_DETAIL_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case BILLING_ACTIONS.GET_MANUAL_INVOICE_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update Manual Invoice Status
        case BILLING_ACTIONS.UPDATE_MANUAL_INVOICE_STATUS:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.UPDATE_MANUAL_INVOICE_STATUS_SUCCESS:
            if (state.manualInvoiceList.result) {
                let listManualInvoice = state.manualInvoiceList.result;
                let manualInvoiceUpdate = listManualInvoice.map(item => {
                    if (item.id === payload.id) {
                        item.status = payload.status
                    }
                    return item
                });
                return {
                    manualInvoiceList: { ...state.manualInvoiceList, result: manualInvoiceUpdate },
                    data: state.data,
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case BILLING_ACTIONS.UPDATE_MANUAL_INVOICE_STATUS_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update Manual Invoice Status
        case BILLING_ACTIONS.PROVIDER_CHARGE_INVOICE:
            return {
                ...state,
                loading: true,
            }
        case BILLING_ACTIONS.PROVIDER_CHARGE_INVOICE_SUCCESS:
            const temp = state.data
            temp.status = cons.INVOICE_STATUS.PAID
            return {
                ...state,
                data: temp,
                loading: false,
            }
        case BILLING_ACTIONS.PROVIDER_CHARGE_INVOICE_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
