import {
    EXPORT_BOOKING_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {},
};

export default function exportBookingReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Booking List To Export ICal
        case EXPORT_BOOKING_ACTIONS.GET_BOOKING_LIST_TO_EXPORT:
            return {
                ...state,
                loading: true,
            }
        case EXPORT_BOOKING_ACTIONS.GET_BOOKING_LIST_TO_EXPORT_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case EXPORT_BOOKING_ACTIONS.GET_BOOKING_LIST_TO_EXPORT_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Clearn Booking Data
        case EXPORT_BOOKING_ACTIONS.CLEAR_BOOKING_DATA:
            return {
                ...state,
                data: [],
                loading: false,
            }

        default:
            return state;
    }
}
