import { call, put, takeLatest } from "redux-saga/effects";
import { POST_ACTIONS } from "../actions";
import postApi from '../../api/postApi';
import { toast } from 'react-toastify';

function* getPostList({ payload }) {
    try {
        const data = yield call(_getPostList.bind(this, payload))
        yield put({ type: POST_ACTIONS.GET_POST_LIST_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: POST_ACTIONS.GET_POST_LIST_FAIL })
    }
}

function _getPostList(payload) {
    return postApi.list(payload).then(response => {
        return response
    })
}

function* addPost({ payload }) {
    try {
        const data = yield call(_addPost.bind(this, payload))
        yield put({ type: POST_ACTIONS.ADD_POST_SUCCESS, payload: data })
        toast.success("Create Post success !");
    } catch (error) {
        yield put({ type: POST_ACTIONS.ADD_POST_FAIL })
        toast.error("Create Post fail !");
    }
}

function _addPost(payload) {
    return postApi.addPost(payload).then(response => {
        return response
    })
}

function* updatePost({ payload }) {
    try {
        const data = yield call(_updatePost.bind(this, payload))
        yield put({ type: POST_ACTIONS.UPDATE_POST_SUCCESS, payload: data })
        toast.success("Update Post success !");
    } catch (error) {
        yield put({ type: POST_ACTIONS.UPDATE_POST_FAIL })
        toast.error("Update Post fail !");
    }
}

function _updatePost(payload) {
    return postApi.updatePost(payload).then(response => {
        return response
    })
}

function* deletePost({ payload }) {
    try {
        yield call(_deletePost.bind(this, payload));
        yield put({ type: POST_ACTIONS.DELETE_POST_SUCCESS, payload: payload })
        toast.success("Delete Post success !");
    } catch (error) {
        yield put({ type: POST_ACTIONS.DELETE_POST_FAIL })
        toast.error("Delete Post fail !");
    }
}

function _deletePost(payload) {
    return postApi.deletePost(payload).then(response => {
        return response
    })
}

// ==============
