function* loadMore({ payload }) {
    try {
        const data = yield call(_getPostList.bind(this, payload))
        yield put({ type: POST_ACTIONS.LOAD_MORE_POST_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: POST_ACTIONS.LOAD_MORE_POST_FAIL })
    }
}

export function* watchPostAsync() {
    yield takeLatest(POST_ACTIONS.GET_POST_LIST, getPostList);
    yield takeLatest(POST_ACTIONS.ADD_POST, addPost);
    yield takeLatest(POST_ACTIONS.UPDATE_POST, updatePost);
    yield takeLatest(POST_ACTIONS.DELETE_POST, deletePost);

    yield takeLatest(POST_ACTIONS.ADD_POST_COMMENT, addComment);
    yield takeLatest(POST_ACTIONS.UPDATE_POST_COMMENT, updateComment);
    yield takeLatest(POST_ACTIONS.DELETE_POST_COMMENT, deleteComment);
    yield takeLatest(POST_ACTIONS.LOAD_MORE_POST, loadMore);
}
