import { call, put, takeLatest } from "redux-saga/effects";
import { VENUES_ACTIONS } from '../actions'
import venueApi from '../../api/venueApi'

function* requestGetVenues(action) {
    const { filters } = action.payload
    try {
        const data = yield call(getVenues.bind(this, filters))
        yield put({ type: VENUES_ACTIONS.GET_VENUES_SUCCESS, payload: { data } })
    } catch (error) {
        yield put({ type: VENUES_ACTIONS.GET_VENUES_FAIL, payload: { error } })
    }
}

async function getVenues(filters) {
    return await venueApi.list(filters);
}

export function* watchVenuesAsync() {
    yield takeLatest(VENUES_ACTIONS.GET_VENUES, requestGetVenues);
}
