import { call, put, takeLatest } from "redux-saga/effects";
import { NOTIFICATION_ACTIONS } from "../actions";
import notificationApi from '../../api/notificationApi';
import { toast } from 'react-toastify'

function* getListNotification({ payload }) {
    try {
        const data = yield call(_getListNotification.bind(this))
        yield put({ type: NOTIFICATION_ACTIONS.GET_PROVIDER_NOTIFICATIONS_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: NOTIFICATION_ACTIONS.GET_PROVIDER_NOTIFICATIONS_FAIL })
    }
}

function _getListNotification() {
    return notificationApi.listNotification().then(response => {
        return response
    })
}

function* updateNotification({ payload }) {
    try {
        const data = yield call(_updateNotification.bind(this, payload))
        yield put({ type: NOTIFICATION_ACTIONS.UPDATE_PROVIDER_NOTIFICATIONS_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: NOTIFICATION_ACTIONS.UPDATE_PROVIDER_NOTIFICATIONS_FAIL })
    }
}

function _updateNotification(payload) {
    return notificationApi.updateNotification(payload).then(response => {
        return response
    })
}

function* markAllReadNotification({ payload }) {
    try {
        const data = yield call(_updateNotification.bind(this, payload))
        yield put({ type: NOTIFICATION_ACTIONS.MARK_ALL_READ_NOTIFICATIONS_SUCCESS, payload: data })
        toast.success("Mark all as read success !");
    } catch (error) {
        yield put({ type: NOTIFICATION_ACTIONS.MARK_ALL_READ_NOTIFICATIONS_FAIL })
        toast.error("Mark all as read fail !");
    }
}

export function* watchNotificationAsync() {
    yield takeLatest(NOTIFICATION_ACTIONS.GET_PROVIDER_NOTIFICATIONS, getListNotification);
    yield takeLatest(NOTIFICATION_ACTIONS.UPDATE_PROVIDER_NOTIFICATIONS, updateNotification);
    yield takeLatest(NOTIFICATION_ACTIONS.MARK_ALL_READ_NOTIFICATIONS, markAllReadNotification);
}
