import client from '../helpers/HttpClient'
import { urlQueryFromObject } from '../helpers/UtilsHelper';
const endpoint = '/providers';

export default {
    async providerRegister(data) {
        let response = await client.post(endpoint, data)
        return response.data;
    },

    async create(data) {
        let response = await client.post(endpoint, data)
        return response.data;
    },

    async detail(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.get(path)
        return response.data;
    },

    async update(data, partial = true) {
        const { id } = data;
        const path = `${endpoint}/${id}`;
        let formData = new FormData();
        Object.keys(data).forEach(key => {
            formData.append([key], data[key]);
        });
        let response = partial ? await client.patch(path, formData) : await client.put(path, formData)
        return response.data;
    },

    async delete(data) {
        const path = `${endpoint}/bulk-delete`;
        let response = await client.post(path, data)
        return response.data;
    },

    async list(filters) {
        const query = urlQueryFromObject(filters);
        let path = `${endpoint}${query}`;
        let response = await client.get(path);
        return response.data;
    },

    async tier() {
        let path = `${endpoint}/tiers/my-tier`;
        let response = await client.get(path);
        return response.data;
    },

    async me() {
        let path = `${endpoint}/me`;
        let response = await client.get(path);
        return response.data;
    },
}
