const actionTypes = {
    GET_BOOKING_LIST_EXPORT: "GET_BOOKING_LIST_EXPORT",
    GET_BOOKING_LIST_EXPORT_SUCCESS: 'GET_BOOKING_LIST_EXPORT_SUCCESS',
    GET_BOOKING_LIST_EXPORT_FAIL: 'GET_BOOKING_LIST_EXPORT_FAIL',


    GET_CLIENT_LIST: "bookings.GET_CLIENT_LIST",
    GET_CLIENT_LIST_SUCCESS: 'bookings.GET_CLIENT_LIST_SUCCESS',
    GET_CLIENT_LIST_FAIL: 'bookings.GET_CLIENT_LIST_FAIL',

    GET_BOOKING_LIST: "GET_BOOKING_LIST",
    GET_BOOKING_LIST_SUCCESS: 'GET_BOOKING_LIST_SUCCESS',
    GET_BOOKING_LIST_FAIL: 'GET_BOOKING_LIST_FAIL',

    GET_VENUE_LIST: "GET_VENUE_LIST",
    GET_VENUE_LIST_SUCCESS: 'GET_VENUE_LIST_SUCCESS',
    GET_VENUE_LIST_FAIL: 'GET_VENUE_LIST_FAIL',

    GET_SPACE_LIST: "GET_SPACE_LIST",
    GET_SPACE_LIST_SUCCESS: 'GET_SPACE_LIST_SUCCESS',
    GET_SPACE_LIST_FAIL: 'GET_SPACE_LIST_FAIL',


    GET_SPACE_LIST_FOR_MODAL: "GET_SPACE_LIST_FOR_MODAL",
    GET_SPACE_LIST_FOR_MODAL_SUCCESS: 'GET_SPACE_LIST_FOR_MODAL_SUCCESS',
    GET_SPACE_LIST_FOR_MODAL_FAIL: 'GET_SPACE_LIST_FOR_MODAL_FAIL',

    SET_SPACE_LIST: 'SET_SPACE_LIST',

    ADD_BOOKING: "ADD_BOOKING",
    ADD_BOOKING_SUCCESS: 'ADD_BOOKING_SUCCESS',
    ADD_BOOKING_FAIL: 'ADD_BOOKING_FAIL',

    DELETE_BOOKING: "DELETE_BOOKING",
    DELETE_BOOKING_SUCCESS: 'DELETE_BOOKING_SUCCESS',
    DELETE_BOOKING_FAIL: 'DELETE_BOOKING_FAIL',

    CANCEL_BOOKING: "CANCEL_BOOKING",
    CANCEL_BOOKING_SUCCESS: 'CANCEL_BOOKING_SUCCESS',
    CANCEL_BOOKING_FAIL: 'CANCEL_BOOKING_FAIL',

    UPDATE_BOOKING_APPOINTMENT: "UPDATE_BOOKING_APPOINTMENT",
    UPDATE_BOOKING_APPOINTMENT_SUCCESS: "UPDATE_BOOKING_APPOINTMENT_SUCCESS",
    UPDATE_BOOKING_APPOINTMENT_FAIL: "UPDATE_BOOKING_APPOINTMENT_FAIL",

    ADD_APPOINTMENT: "bookings.ADD_APPOINTMENT",
    ADD_APPOINTMENT_SUCCESS: "bookings.ADD_APPOINTMENT_SUCCESS",
    ADD_APPOINTMENT_FAIL: "bookings.ADD_APPOINTMENT_FAIL",

    EDIT_APPOINTMENT: "bookings.EDIT_APPOINTMENT",
    EDIT_APPOINTMENT_SUCCESS: "bookings.EDIT_APPOINTMENT_SUCCESS",
    EDIT_APPOINTMENT_FAIL: "bookings.EDIT_APPOINTMENT_FAIL",

    DELETE_APPOINTMENT: "bookings.DELETE_APPOINTMENT",
    DELETE_APPOINTMENT_SUCCESS: "bookings.DELETE_APPOINTMENT_SUCCESS",
    DELETE_APPOINTMENT_FAIL: "bookings.DELETE_APPOINTMENT_FAIL",

    DELETE_ERROR: "DELETE_ERROR",

    TOGGLE_SIDE_BAR: "TOGGLE_SIDE_BAR",

    GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR: "GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR",
    GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_SUCCESS: 'GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_SUCCESS',
    GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_FAIL: 'GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_FAIL',

}

export default actionTypes
export const getListExport = (filters, callback) => ({ type: actionTypes.GET_BOOKING_LIST_EXPORT, payload: { filters, callback } });
export const getList = (filters) => ({ type: actionTypes.GET_BOOKING_LIST, payload: { filters } });
export const getClients = () => ({ type: actionTypes.GET_CLIENT_LIST });
export const getVenue = (filters) => ({ type: actionTypes.GET_VENUE_LIST, payload: { filters } });
export const getSpace = (venueId) => ({ type: actionTypes.GET_SPACE_LIST, payload: { venueId } });
export const getSpaceForModal = (venueId) => ({ type: actionTypes.GET_SPACE_LIST_FOR_MODAL, payload: { venueId } });
export const setSpace = () => ({ type: actionTypes.SET_SPACE_LIST });
export const addBooking = (booking, filters) => ({ type: actionTypes.ADD_BOOKING, payload: { booking, filters } });
export const deleteBooking = (bookingId) => ({ type: actionTypes.DELETE_BOOKING, payload: { bookingId } });
export const cancelBooking = (bookingId, data) => ({ type: actionTypes.CANCEL_BOOKING, payload: { bookingId, data } });
export const deleteErr = () => ({ type: actionTypes.DELETE_ERROR })

export const toggleSideBar = (isOpenSideBar) => ({ type: actionTypes.TOGGLE_SIDE_BAR, payload: { isOpenSideBar } });
export const getProviderData = (id) => ({ type: actionTypes.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR, payload: { id } })

export const addAppointment = (appointmentData, filters, callback) => ({ type: actionTypes.ADD_APPOINTMENT, payload: { appointmentData, filters, onSuccess: callback } });
export const editAppointment = (appointmentData, filters, callback) => ({ type: actionTypes.EDIT_APPOINTMENT, payload: { appointmentData, filters, onSuccess: callback } });
export const deleteAppointment = (appointmentData, filters, callback) => ({ type: actionTypes.DELETE_APPOINTMENT, payload: { appointmentData, filters, onSuccess: callback } });

export const getSingleVenue = id => ({type: actionTypes.GET_VENUE, payload: {id}});