const venuesActions = {
    GET_VENUES: "GET_VENUES",
    GET_VENUES_SUCCESS: "GET_VENUES_SUCCESS",
    GET_VENUES_FAIL: "GET_VENUES_FAIL",
    REMOVE_VENUE: 'REMOVE_VENUE',
}

export default venuesActions

export const getVenues = (filters) => ({ type: venuesActions.GET_VENUES, payload: { filters } })
export const removeVenue = (venueId) => ({ type: venuesActions.REMOVE_VENUE, payload: { venueId } })
