import { call, put, takeLatest } from "redux-saga/effects";
import { NAV_BARS_ACTIONS, AUTH_ACTIONS } from "../actions";
import adminApi from '../../api/adminApi';
import providerApi from '../../api/providerApi';
import clientApi from '../../api/clientApi';

function* getAdminDetail({ payload }) {
    const { adminId } = payload;
    try {
        const data = yield call(_getAdminDetail.bind(this, adminId))
        yield put({ type: NAV_BARS_ACTIONS.NAV_BARS_GET_ADMIN_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: NAV_BARS_ACTIONS.NAV_BARS_GET_ADMIN_DETAIL_FAIL })
    }
}

function _getAdminDetail(adminId) {
    return adminApi.getAdminDetail(adminId).then(response => {
        return response
    })
}

function* getProviderDetail({ payload }) {
    const { providerId } = payload;
    try {
        const data = yield call(_getProviderDetail.bind(this, providerId))
        yield put({ type: NAV_BARS_ACTIONS.NAV_BARS_GET_PROVIDER_DETAIL_SUCCESS, payload: data })
        yield put({ type: AUTH_ACTIONS.AUTH_UPDATE_FIRST_LOGIN_STATUS, payload: {status: data.isFirstLogin} })
    } catch (error) {
        yield put({ type: NAV_BARS_ACTIONS.NAV_BARS_GET_PROVIDER_DETAIL_FAIL })
    }
}

function _getProviderDetail(providerId) {
    return providerApi.detail(providerId).then(response => {
        return response
    })
}


function* getClientDetail({ payload }) {
    const { clientId } = payload;
    try {
        const data = yield call(_getClientDetail.bind(this, clientId))
        yield put({ type: NAV_BARS_ACTIONS.NAV_BARS_GET_CLIENT_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: NAV_BARS_ACTIONS.NAV_BARS_GET_CLIENT_DETAIL_FAIL })
    }
}

function _getClientDetail(clientId) {
    return clientApi.detail(clientId).then(response => {
        return response
    })
}

export function* watchNavBarsAsync() {
    yield takeLatest(NAV_BARS_ACTIONS.NAV_BARS_GET_ADMIN_DETAIL, getAdminDetail);
    yield takeLatest(NAV_BARS_ACTIONS.NAV_BARS_GET_PROVIDER_DETAIL, getProviderDetail);
    yield takeLatest(NAV_BARS_ACTIONS.NAV_BARS_GET_CLIENT_DETAIL, getClientDetail);

}
