import { AUTH_ACTIONS } from '../actions';
import authHelper from '../../helpers/AuthHelper'
const token = authHelper.getToken()
let initState = {
    haveError: false,
    token,
    isAuthenticated: !!token,
    loading: false,
    isShowExpireAlert: false,
    isFirstLogin: false,
}
export default function authReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        case AUTH_ACTIONS.LOGIN:
        case AUTH_ACTIONS.LOGIN_AFTER_REGISTER:
            return Object.assign({}, state, {
                isAuthenticated: false,
                haveError: false,
                loading: true
            });
        case AUTH_ACTIONS.LOGIN_SUCCESS:
            return Object.assign({}, state, {
                isAuthenticated: true,
                data: payload,
                haveError: false,
                loading: false,
                token: payload.token,
                isFirstLogin: payload.isFirstLogin
            });

        case AUTH_ACTIONS.REFRESH_TOKEN_SUCCESS:
            return {
                ...state,
                token: payload.token
            };
        case AUTH_ACTIONS.LOGIN_FAIL:
            return Object.assign({}, state, {
                isAuthenticated: false,
                haveError: true,
                loading: false
            });
        case AUTH_ACTIONS.LOGOUT:
            return Object.assign({}, state, {
                isAuthenticated: false
            });
        case AUTH_ACTIONS.LOGOUT_EXPIRE:
            return Object.assign({}, state, {
                isAuthenticated: false,
                isShowExpireAlert: true,
            });
        case AUTH_ACTIONS.TURN_OFF_EXPIRE_ALERT:
            return Object.assign({}, state, {
                isShowExpireAlert: false,
            });
        case AUTH_ACTIONS.AUTH_UPDATE_FIRST_LOGIN_STATUS:
            const { status } = payload
            return Object.assign({}, state, {
                isFirstLogin: status,
            });
        default:
            return state;
    }
}
