import { call, put, takeLatest, takeEvery } from "redux-saga/effects";
import { NOTE_ACTIONS, PROVIDER_DASHBOARD_ACTIONS } from "../actions";
import noteApi from "../../api/noteApi";
import { toast } from "react-toastify";
import bookingApi from "../../api/bookingApi";
import venueApi from "../../api/venueApi";

function* getNoteList({ payload }) {
  try {
    const data = yield call(_getNoteList.bind(this, payload));
    yield put({ type: NOTE_ACTIONS.GET_NOTE_LIST_SUCCESS, payload: data });
  } catch (error) {
    yield put({ type: NOTE_ACTIONS.GET_NOTE_LIST_FAIL });
  }
}

function _getNoteList(payload) {
  return noteApi.list(payload).then((response) => {
    return response;
  });
}

function* addNote({ payload }) {
  try {
    const data = yield call(_addNote.bind(this, payload));
    yield put({ type: NOTE_ACTIONS.ADD_NOTE_SUCCESS, payload: data });
    toast.success("Create Note success !");
  } catch (error) {
    yield put({ type: NOTE_ACTIONS.ADD_NOTE_FAIL });
    toast.error("Create Note fail !");
  }
}

function _addNote(payload) {
  return noteApi.create(payload).then((response) => {
    return response;
  });
}

function* loadMore({ payload }) {
  try {
    const data = yield call(_getNoteList.bind(this, payload));
    yield put({ type: NOTE_ACTIONS.LOAD_MORE_NOTE_SUCCESS, payload: data });
  } catch (error) {
    yield put({ type: NOTE_ACTIONS.LOAD_MORE_NOTE_FAIL });
  }
}

function* deleteNote({ payload }) {
  try {
    yield call(_deleteNote.bind(this, payload));
    yield put({ type: NOTE_ACTIONS.DELETE_NOTE_SUCCESS, payload: payload });
    toast.success("Delete Note success !");
  } catch (error) {
    yield put({ type: NOTE_ACTIONS.DELETE_NOTE_FAIL });
    toast.error("Delete Note fail !");
  }
}

function _deleteNote(payload) {
  return noteApi.delete(payload).then((response) => {
    return response;
  });
}

function* getBooking({ payload }) {
  const { appointment } = payload;
  try {
    const booking = yield call(
      bookingApi.detail.bind(this, appointment.booking)
    );
    yield put({
      type: NOTE_ACTIONS.GET_BOOKING_SUCCESS,
      payload: { booking, appointment },
    });
  } catch (e) {
    yield put({ type: NOTE_ACTIONS.GET_BOOKING_FAIL, error: e });
    toast.error("Couldn't get booking for this appointment");
  }
}

function* getVenues() {
  try {
    const venues = yield call(venueApi.list.bind(this));
    yield put({ type: NOTE_ACTIONS.GET_VENUES_SUCCESS, payload: { venues } });
  } catch (e) {
    yield put({ type: NOTE_ACTIONS.GET_VENUES_FAIL, error: e });
    toast.error("Couldn't get location list");
  }
}

function* addAppointment({ payload }) {
  const { appointmentData, onSuccess } = payload;
  try {
    const newAppointment = yield call(
      bookingApi.addAppointment.bind(this, appointmentData)
    );
    yield put({
      type: NOTE_ACTIONS.ADD_APPOINTMENT_SUCCESS,
      payload: { newAppointment },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.ADD_APPOINTMENT_SUCCESS,
      payload: { newAppointment },
    });
    toast.success("Add appointment successfully");
    onSuccess && onSuccess(newAppointment);
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: NOTE_ACTIONS.ADD_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.ADD_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

function* editAppointment({ payload }) {
  const { appointmentData, onSuccess } = payload;
  try {
    const editedAppointment = yield call(
      bookingApi.editAppointment.bind(this, appointmentData)
    );
    yield put({
      type: NOTE_ACTIONS.EDIT_APPOINTMENT_SUCCESS,
      payload: { editedAppointment },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.EDIT_APPOINTMENT_SUCCESS,
      payload: { editedAppointment },
    });
    toast.success("Edited appointment successfully");
    onSuccess && onSuccess(editedAppointment);
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: NOTE_ACTIONS.EDIT_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.EDIT_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

function* deleteAppointment({ payload }) {
  const { appointmentData, onSuccess } = payload;
  try {
    yield call(
      bookingApi.deleteAppointment.bind(
        this,
        appointmentData.booking,
        appointmentData.id
      )
    );
    yield put({
      type: NOTE_ACTIONS.DELETE_APPOINTMENT_SUCCESS,
      payload: { appointmentData },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.DELETE_APPOINTMENT_SUCCESS,
      payload: { appointment: appointmentData },
    });
    toast.success("Delete appointment successfully");
    onSuccess && onSuccess(appointmentData);
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: NOTE_ACTIONS.DELETE_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.DELETE_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

export function* watchNoteAsync() {
  yield takeLatest(NOTE_ACTIONS.GET_NOTE_LIST, getNoteList);
  yield takeLatest(NOTE_ACTIONS.ADD_NOTE, addNote);
  yield takeLatest(NOTE_ACTIONS.LOAD_MORE_NOTE, loadMore);
  yield takeLatest(NOTE_ACTIONS.DELETE_NOTE, deleteNote);
  yield takeEvery(NOTE_ACTIONS.GET_BOOKING, getBooking);
  yield takeLatest(NOTE_ACTIONS.GET_VENUES, getVenues);
  yield takeLatest(NOTE_ACTIONS.ADD_APPOINTMENT, addAppointment);
  yield takeLatest(NOTE_ACTIONS.EDIT_APPOINTMENT, editAppointment);
  yield takeLatest(NOTE_ACTIONS.DELETE_APPOINTMENT, deleteAppointment);
}
