const actionTypes = {
    GET_PROVIDER_MESSAGE_LIST: "GET_PROVIDER_MESSAGE_LIST",
    GET_PROVIDER_MESSAGE_LIST_SUCCESS: 'GET_PROVIDER_MESSAGE_LIST_SUCCESS',
    GET_PROVIDER_MESSAGE_LIST_FAIL: 'GET_PROVIDER_MESSAGE_LIST_FAIL',

    CLIENT_SEND_MESSAGE: "CLIENT_SEND_MESSAGE",
    CLIENT_SEND_MESSAGE_SUCCESS: 'CLIENT_SEND_MESSAGE_SUCCESS',
    CLIENT_SEND_MESSAGE_FAIL: 'CLIENT_SEND_MESSAGE_FAIL',
}

export default actionTypes

export const getProviderMessage = (filters, callback) => ({ type: actionTypes.GET_PROVIDER_MESSAGE_LIST, payload: { filters, callback } })
export const clientSendMessage = (data) => ({ type: actionTypes.CLIENT_SEND_MESSAGE, payload: { data } })
