
const END_POINT = `${window.location.hostname}:${process.env.REACT_APP_SERVER_PORT}`
const usePort = process.env.REACT_APP_USE_PORT
const HTTP_END_POINT = usePort ? END_POINT : process.env.REACT_APP_SERVER
const API_END_POINT = `${HTTP_END_POINT}/api`
const WS_END_POINT = `ws://${END_POINT}`
const TOKEN_LIFE_TIME = 100000 * 60 * 30; // 300 minutes
const APP_VERSION = `${process.env.REACT_APP_VERSION}`
const STRIPE_PUBLIC_KEY = process.env.REACT_APP_STRIPE_PUBLIC_KEY
const STRIPE_CONNECT_URL = process.env.REACT_APP_STRIPE_CONNECT_URL

export default {
    END_POINT,
    HTTP_END_POINT,
    API_END_POINT,
    WS_END_POINT,
    TOKEN_LIFE_TIME,
    APP_VERSION,
    STRIPE_PUBLIC_KEY,
    STRIPE_CONNECT_URL
}
