import client from "../helpers/HttpClient";
import { urlQueryFromObject } from "../helpers/UtilsHelper";
const endpoint = "/clients";

export default {
  async create(payload) {
    const { clientId, data } = payload;
    const path = `${endpoint}/${clientId}/notes`;
    let formData = new FormData();
    Object.keys(data).forEach((key) => {
      if (key === "attachments") {
        data[key].forEach((item) => {
          formData.append("attachments", item);
        });
      } else {
        formData.append([key], data[key]);
      }
    });
    let response = await client.post(path, formData);
    return response.data;
  },
  async list(payload) {
    const { clientId, filters } = payload;
    const query = urlQueryFromObject(filters);
    let path = `${endpoint}/${clientId}/notes${query}`;
    let response = await client.get(path);
    return response.data;
  },
  async exportCsv(payload) {
    const { clientId } = payload;
    let path = `${endpoint}/${clientId}/notes/export-csv`;
    let response = await client.get(path);

    return response.data;
  },
  async delete(payload) {
    const { clientId, noteId } = payload;
    const path = `${endpoint}/${clientId}/notes/${noteId}`;
    let response = await client.delete(`${path}`);
    return response.data;
  },
};
