import { RESET_PASSWORD_ACTIONS } from '../actions';

let initState = {
    loading: false,
}

export default function commonReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // --------------- SEND MAIL TO RESET PASSWORD ----------------------
        case RESET_PASSWORD_ACTIONS.SEND_MAIL_RESET_PASSWORD:
            return Object.assign({}, state, {
                isAuthenticated: false,
                haveError: false,
                loading: true,
            });
        case RESET_PASSWORD_ACTIONS.SEND_MAIL_RESET_PASSWORD_SUCCESS:
            return Object.assign({}, state, {
                isAuthenticated: true,
                data: payload,
                haveError: false,
                loading: false,
            });
        case RESET_PASSWORD_ACTIONS.SEND_MAIL_RESET_PASSWORD_FAIL:
            return Object.assign({}, state, {
                isAuthenticated: true,
                data: payload,
                haveError: true,
                loading: false,
            });

        // --------------- RESET PASSWORD WITH TOKEN----------------------
        case RESET_PASSWORD_ACTIONS.RESET_PASSWORD:
            return Object.assign({}, state, {
                isAuthenticated: false,
                haveError: false,
                loading: true,
            });
        case RESET_PASSWORD_ACTIONS.RESET_PASSWORD_SUCCESS:
            return Object.assign({}, state, {
                isAuthenticated: true,
                data: payload,
                haveError: false,
                loading: false,
            });
        case RESET_PASSWORD_ACTIONS.RESET_PASSWORD_FAIL:
            return Object.assign({}, state, {
                isAuthenticated: true,
                data: payload,
                haveError: true,
                loading: false,
            });
        default:
            return state;
    }
}
