import axios from "axios";
import appConfig from "../config";
import auth from "./AuthHelper";
import { store } from "../redux";
import { AUTH_ACTIONS, APP_ACTIONS } from "../redux/actions";
import { getPortalType } from "./UtilsHelper";

const HttpClient = axios.create({
  baseURL: appConfig.API_END_POINT,

  timeout: 600000,
});

HttpClient.interceptors.request.use(
  function (config) {
    addRequestHeader(config);

    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

HttpClient.interceptors.response.use(
  function (response) {
    saveLastRequestTime(response);
    return response;
  },
  function (error) {
    handleError(error);
    return Promise.reject(error);
  }
);

function saveLastRequestTime(response) {
  if (response.config.url.indexOf("refresh-token") >= 0) {
    window.lastRequest = new Date();
  }
}

function addRequestHeader(config) {
  addToken(config);
  addPrivacy(config);
}

function addToken(config) {
  let token = auth.getToken();
  if (token) {
    config.headers["Authorization"] = `Bearer ${token}`;
  }
}

function addPrivacy(config) {
  if (getPortalType() !== "provider") {
    return;
  }

  const privacy = store.getState().common.privacy;
  if (privacy) {
    config.headers["Privacy"] = String(privacy);
  }
}

function handleError(error) {
  if (!error.response) {
    store.dispatch({ type: APP_ACTIONS.DISCONNECT_ON });
  }
  const status = error.response && error.response.status;
  if (status === 401) {
    forceLogout();
  }
}

function forceLogout() {
  auth.destroyToken();
  store.dispatch({ type: AUTH_ACTIONS.LOGOUT_EXPIRE });
}

export default HttpClient;
