import client from '../helpers/HttpClient'
import { urlQueryFromObject } from '../helpers/UtilsHelper';

const endpoint = '/venues/{venueId}/locks';

const getUrl = (venueId, lockId) => {
    let url = endpoint.replace('{venueId}', venueId);
    if (lockId) {
        url += `/${lockId}`;
    }
    return url;
}

export default {
    async create(data) {
        const {venue} = data;
        let response = await client.post(getUrl(venue), data)
        return response.data;
    },
    async update(data, partial = true) {
        const { currentVenueId, id } = data;
        const path = getUrl(currentVenueId, id);
        let response = partial ? await client.patch(path, data) : await client.put(path, data)
        return response.data;
    },
    async delete(venue_id, lockId) {
        let response = await client.delete(getUrl(venue_id, lockId));
        return response.data;
    },
    async list(venue_id, filters) {
        const query = urlQueryFromObject(filters);
        const path = `${getUrl(venue_id)}${query}`;
        let response = await client.get(path)
        return response.data;
    },
    async unlock(venue_id, lockId) {
        const path = `${getUrl(venue_id, lockId)}/unlock`;
        let response = await client.post(path)
        return response.data;
    },
    async test(venue_id, token) {
        const path = `${getUrl(venue_id)}/test`;
        let response = await client.post(path, {token});
        return response.data;
    }
}
