import client from '../helpers/HttpClient'
const endpoint = '/update-password';
const sendMailToResetPasswordEndpoint = '/reset-password-request';
const resetPasswordEndpoint = '/reset-password-confirm';
const createPasswordEndpoint = '/create-password-confirm';

export default {
    async update(data) {
        let response = await client.post(endpoint, data)
        return response.data;
    },
    async sendMailResetPassword(data) {
        let response = await client.post(sendMailToResetPasswordEndpoint, data)
        return response.data;
    },
    async resetPassword(data) {
        let response = await client.post(resetPasswordEndpoint, data)
        return response.data;
    },
    async createPassword(data) {
        let response = await client.post(createPasswordEndpoint, data)
        return response.data;
    },
}
