const actionTypes = {
    NAV_BARS_GET_CLIENT_DETAIL: "NAV_BARS_GET_CLIENT_DETAIL",
    NAV_BARS_GET_CLIENT_DETAIL_SUCCESS: 'NAV_BARS_GET_CLIENT_DETAIL_SUCCESS',
    NAV_BARS_GET_CLIENT_DETAIL_FAIL: 'NAV_BARS_GET_CLIENT_DETAIL_FAIL',

    NAV_BARS_GET_PROVIDER_DETAIL: "NAV_BARS_GET_PROVIDER_DETAIL",
    NAV_BARS_GET_PROVIDER_DETAIL_SUCCESS: 'NAV_BARS_GET_PROVIDER_DETAIL_SUCCESS',
    NAV_BARS_GET_PROVIDER_DETAIL_FAIL: 'NAV_BARS_GET_PROVIDER_DETAIL_FAIL',

    NAV_BARS_GET_ADMIN_DETAIL: "NAV_BARS_GET_ADMIN_DETAIL",
    NAV_BARS_GET_ADMIN_DETAIL_SUCCESS: 'NAV_BARS_GET_ADMIN_DETAIL_SUCCESS',
    NAV_BARS_GET_ADMIN_DETAIL_FAIL: 'NAV_BARS_GET_ADMIN_DETAIL_FAIL',

    UPDATE_FIRST_LOGIN_STATUS: 'UPDATE_FIRST_LOGIN_STATUS',
}

export default actionTypes

export const getClientDetail = (clientId) => ({ type: actionTypes.NAV_BARS_GET_CLIENT_DETAIL, payload: { clientId } })
export const getProviderDetail = (providerId) => ({ type: actionTypes.NAV_BARS_GET_PROVIDER_DETAIL, payload: { providerId } })
export const getAdminDetail = (adminId) => ({ type: actionTypes.NAV_BARS_GET_ADMIN_DETAIL, payload: { adminId } })
export const updateFirstLoginStatus = () => ({ type: actionTypes.UPDATE_FIRST_LOGIN_STATUS })
