const actionTypes = {
    GET_PROVIDER_LIST: "GET_PROVIDER_LIST",
    GET_PROVIDER_LIST_SUCCESS: 'GET_PROVIDER_LIST_SUCCESS',
    GET_PROVIDER_LIST_FAIL: 'GET_PROVIDER_LIST_FAIL',

    GET_PROVIDER_DETAIL: "GET_PROVIDER_DETAIL",
    GET_PROVIDER_DETAIL_SUCCESS: 'GET_PROVIDER_DETAIL_SUCCESS',
    GET_PROVIDER_DETAIL_FAIL: 'GET_PROVIDER_DETAIL_FAIL',

    UPDATE_PROVIDER: "UPDATE_PROVIDER",
    UPDATE_PROVIDER_SUCCESS: 'UPDATE_PROVIDER_SUCCESS',
    UPDATE_PROVIDER_FAIL: 'UPDATE_PROVIDER_FAIL',

    UPDATE_PROVIDER_STATUS: "UPDATE_PROVIDER_STATUS",
    UPDATE_PROVIDER_STATUS_SUCCESS: 'UPDATE_PROVIDER_STATUS_SUCCESS',
    UPDATE_PROVIDER_STATUS_FAIL: 'UPDATE_PROVIDER_STATUS_FAIL',

    DELETE_LIST_PROVIDER: "DELETE_LIST_PROVIDER",
    DELETE_LIST_PROVIDER_SUCCESS: 'DELETE_LIST_PROVIDER_SUCCESS',
    DELETE_LIST_PROVIDER_FAIL: 'DELETE_LIST_PROVIDER_FAIL',

    TICK_ON_CHECKBOX: 'TICK_ON_CHECKBOX',
    TICK_ON_ALL_CHECKBOX: 'TICK_ON_ALL_CHECKBOX',

    CHANGE_PROVIDER_TYPES: 'CHANGE_PROVIDER_TYPES',

    CHANGE_PROVIDER_STATUS: 'CHANGE_PROVIDER_STATUS',

    ADD_PROVIDER_ACCOUNT: "ADD_PROVIDER_ACCOUNT",
    ADD_PROVIDER_ACCOUNT_SUCCESS: "ADD_PROVIDER_ACCOUNT_SUCCESS",
    ADD_PROVIDER_ACCOUNT_FAIL: "ADD_PROVIDER_ACCOUNT_FAIL",

    DELETE_ERROR: "DELETE_ERROR",

}

export default actionTypes

export const getList = (filters) => ({ type: actionTypes.GET_PROVIDER_LIST, payload: { filters } })
export const getDetail = (id) => ({ type: actionTypes.GET_PROVIDER_DETAIL, payload: { id } })
export const update = (params) => ({ type: actionTypes.UPDATE_PROVIDER, payload: { params } })
export const updateStatus = (params) => ({ type: actionTypes.UPDATE_PROVIDER_STATUS, payload: { params } })
export const deleteList = (params) => ({ type: actionTypes.DELETE_LIST_PROVIDER, payload: { params } })
export const tickOnCheckbox = (checked, id) => ({ type: actionTypes.TICK_ON_CHECKBOX, payload: { checked, id } })
export const tickOnAllCheckbox = (isCheckAll) => ({ type: actionTypes.TICK_ON_ALL_CHECKBOX, payload: { isCheckAll } })
export const changeProviderTypes = (providerTypes) => ({ type: actionTypes.CHANGE_PROVIDER_TYPES, payload: { providerTypes } })

export const changeStatus = (status) => ({ type: actionTypes.CHANGE_PROVIDER_STATUS, payload: { status } })

export const addProviderAccount = (params) => ({ type: actionTypes.ADD_PROVIDER_ACCOUNT, payload: { params } })

export const deleteError = (error) => ({ type: actionTypes.DELETE_ERROR, payload: { error } })