import { all, fork } from "redux-saga/effects";
import { watchCommonAsync } from "./commonSaga";
import { watchAuthAsync } from "./auth.saga";
import { watchPostAsync } from "./postSaga";
import { watchVenuesAsync } from "./venuesSaga";
import { watchProviderDashboardAsync } from "./providerDashboardSaga";
import { watchAdminDashboardAsync } from "./adminDashboardSaga";
import { watchClientAsync } from "./clientSaga";
import { watchProviderAsync } from "./providerSaga";
import { watchBookingAsync } from "./bookingSaga";
import { watchExportBookingAsync } from "./exportBookingSaga";
import { watchNoteAsync } from "./noteSaga";
import { watchBillingAsync } from "./billingSaga";
import { watchNavBarsAsync } from "./navBarsSaga";
import { watchInvoiceAsync } from "./invoiceSaga";
import { watchAppointmentAsync } from "./appointmentSaga";
import { watchNotificationAsync } from "./notificationSaga";
import { watchMessageAsync } from "./messageSaga";
import { watchClientInvoiceAsync } from "./clientInvoiceSaga"
import { watchLocksAsync } from './locksSaga';
import { watchResetPasswordAsync } from "./resetPasswordSaga";
import { watchCreatePasswordAsync } from "./createPasswordSaga";

export default function* sagas() {
  yield all([
    fork(watchCommonAsync),
    fork(watchAuthAsync),
    fork(watchPostAsync),
    fork(watchVenuesAsync),
    fork(watchProviderDashboardAsync),
    fork(watchAdminDashboardAsync),
    fork(watchClientAsync),
    fork(watchProviderAsync),
    fork(watchBookingAsync),
    fork(watchExportBookingAsync),
    fork(watchNoteAsync),
    fork(watchBillingAsync),
    fork(watchNavBarsAsync),
    fork(watchInvoiceAsync),
    fork(watchAppointmentAsync),
    fork(watchNotificationAsync),
    fork(watchMessageAsync),
    fork(watchClientInvoiceAsync),
    fork(watchLocksAsync),
    fork(watchResetPasswordAsync),
    fork(watchCreatePasswordAsync),
  ]);
}
