import { COMMON_ACTIONS } from '../actions';
import {getPrivacy} from '../../helpers/StoreHelper'

let initState = {
    privacy: getPrivacy(),
}

export default function commonReducer(state = initState, action) {
    let { type } = action;
    switch (type) {
        case COMMON_ACTIONS.TOGGLE_PRIVACY:
            return {
                ...state,
                privacy: !state.privacy
            }
        default:
            return state;
    }
}
