import React from "react";
import { connect } from "react-redux";
import { Calendar } from "@fullcalendar/core";
import dayGridPlugin from "@fullcalendar/daygrid";

import appointmentApi from '../../api/appointmentApi';
import cons from '../../constants'
import './ClientCalendar.scss'

let calendar: Calendar;

class ClientCalendar extends React.Component {

    calendar = null;

    componentDidMount() {
        this.createCalendar();
        appointmentApi.list().then(res => {
            this.addEvent(res)
        }).catch(err => {
            console.error(err);
        })
    }

    addEvent = (res) => {
        const listAppointment = res.result
        if (listAppointment && listAppointment.length > 0) {
            listAppointment.forEach(item => {
                const { status } = item.booking
                if (status !== cons.BOOKING_STATUS.CANCELED) {
                    this.calendar.addEvent({
                        title: 'Appointment',
                        start: `${item.startDate} ${item.startTime}`,
                        end: `${item.startDate} ${item.endTime}`,
                        id: item.id,
                        className: `${status === cons.BOOKING_STATUS.CANCELED ? "canceled" : status === cons.BOOKING_STATUS.COMPLETE ? "complete" : "active"}`
                    });
                }
            })
        }
    }

    createCalendar = () => {
        calendar = new Calendar(this.refs.calendar, {
            header: {
                left: 'prev',
                center: 'title',
                right: 'next'
            },
            theme: true,
            plugins: [dayGridPlugin],
            defaultView: 'dayGridMonth',
            events: [],
            schedulerLicenseKey: '0127232346-fcs-1571929973',
        });
        calendar.render();
        this.calendar = calendar;
        this.setState({
            currentDate: calendar.view.title
        });
        return calendar;
    };

    render() {
        return (
            <div className="nav-calendar">
                <div
                    className="calendar"
                    data-toggle="calendar"
                    id="calendar"
                    ref="calendar"
                />
            </div>
        );
    }
}

export default connect(null, null)(ClientCalendar);
