const postActions = {
    GET_POST_LIST: 'GET_POST_LIST',
    GET_POST_LIST_SUCCESS: 'GET_POST_LIST_SUCCESS',
    GET_POST_LIST_FAIL: 'GET_POST_LIST_FAIL',

    ADD_POST: 'ADD_POST',
    ADD_POST_SUCCESS: 'ADD_POST_SUCCESS',
    ADD_POST_FAIL: 'ADD_POST_FAIL',

    UPDATE_POST: 'UPDATE_POST',
    UPDATE_POST_SUCCESS: 'UPDATE_POST_SUCCESS',
    UPDATE_POST_FAIL: 'UPDATE_POST_FAIL',

    DELETE_POST: 'DELETE_POST',
    DELETE_POST_SUCCESS: 'DELETE_POST_SUCCESS',
    DELETE_POST_FAIL: 'DELETE_POST_FAIL',

    ADD_POST_COMMENT: 'ADD_POST_COMMENT',
    ADD_POST_COMMENT_SUCCESS: 'ADD_POST_COMMENT_SUCCESS',
    ADD_POST_COMMENT_FAIL: 'ADD_POST_COMMENT_FAIL',

    UPDATE_POST_COMMENT: 'UPDATE_POST_COMMENT',
    UPDATE_POST_COMMENT_SUCCESS: 'UPDATE_POST_COMMENT_SUCCESS',
    UPDATE_POST_COMMENT_FAIL: 'UPDATE_POST_COMMENT_FAIL',

    DELETE_POST_COMMENT: 'DELETE_POST_COMMENT',
    DELETE_POST_COMMENT_SUCCESS: 'DELETE_POST_COMMENT_SUCCESS',
    DELETE_POST_COMMENT_FAIL: 'DELETE_POST_COMMENT_FAIL',

    LOAD_MORE_POST: 'LOAD_MORE_POST',
    LOAD_MORE_POST_SUCCESS: 'LOAD_MORE_POST_SUCCESS',
    LOAD_MORE_POST_FAIL: 'LOAD_MORE_POST_FAIL',
}

export default postActions

export const getPostList = (filters) => ({ type: postActions.GET_POST_LIST, payload: { filters } })

export const addPost = (content, files) => ({ type: postActions.ADD_POST, payload: { content, files } })

export const updatePost = (postId, content) => ({ type: postActions.UPDATE_POST, payload: { postId, content } })

export const deletePost = (postId) => ({ type: postActions.DELETE_POST, payload: { postId } })

export const addComment = (postId, comment) => ({ type: postActions.ADD_POST_COMMENT, payload: { postId, comment } })

export const updateComment = (commentId, postId, comment) => ({ type: postActions.UPDATE_POST_COMMENT, payload: { commentId, postId, comment } })

export const deleteComment = (commentId, postId) => ({ type: postActions.DELETE_POST_COMMENT, payload: { commentId, postId } })

export const loadMore = (filters) => ({ type: postActions.LOAD_MORE_POST, payload: { filters } })