import { call, put, takeLatest } from "redux-saga/effects";
import { CLIENT_ACTIONS } from "../actions";
import clientApi from '../../api/clientApi';

function* getList({ payload }) {
    const { filters } = payload;
    try {
        const data = yield call(_getList.bind(this, filters))
        yield put({ type: CLIENT_ACTIONS.GET_CLIENT_LIST_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: CLIENT_ACTIONS.GET_CLIENT_LIST_FAIL })
    }
}

function _getList(filters) {
    return clientApi.list(filters).then(response => {
        return response
    })
}

function* getClientDetail({ payload }) {
    const { clientId } = payload;
    try {
        const data = yield call(_getClientDetail.bind(this, clientId))
        yield put({ type: CLIENT_ACTIONS.GET_CLIENT_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: CLIENT_ACTIONS.GET_CLIENT_DETAIL_FAIL })
    }
}

function _getClientDetail(clientId) {
    return clientApi.detail(clientId).then(response => {
        return response
    })
}

export function* watchClientAsync() {
    yield takeLatest(CLIENT_ACTIONS.GET_CLIENT_LIST, getList);
    yield takeLatest(CLIENT_ACTIONS.GET_CLIENT_DETAIL, getClientDetail);

}
