import React from "react";
import { connect } from "react-redux";
import { ToastContainer, toast } from "react-toastify";
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Form,
  Container,
  Row,
  Col,
  Label,
} from "reactstrap";
import { FormInput } from "components/Form";
import ErrorSummary from "components/ErrorSummary";
import AuthHeader from "components/Headers/AuthHeader.jsx";
import Loading from "components/Loading";

import { CREATE_PASSWORD_ACTIONS } from "redux/actions";

import "./CreateNewPassword.scss";

const misMatchPasswordMessage = "Password does not match!";
const notConfirmPolicies = "Please agree to the Terms and Conditions!";

class CreateNewPassword extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password: "",
      confirmPassword: "",
      confirmPolicies: false,
      errors: {},
      isProvider: this.getConfigObject().headerTitle === "Provider",
    };
  }

  handleChange = (e) => {
    const { name, value } = e.target;
    delete this.state.errors[name];
    delete this.state.errors["nonFieldErrors"];
    if (name === "confirmPolicies") {
      this.setState({
        [name]: e.target.checked,
      });
    } else {
      this.setState(
        {
          [name]: value,
        },
        () => {
          if (name === "confirmPassword" || name === "password") {
            const { password, confirmPassword } = this.state;
            if (password && confirmPassword && password !== confirmPassword) {
              this.setState({
                errors: {
                  ...this.state.errors,
                  confirmPassword: [misMatchPasswordMessage],
                },
              });
            } else {
              const tempError = this.state.errors;
              delete tempError["confirmPassword"];
              this.setState({ errors: tempError });
            }
          }
        }
      );
    }
  };

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState({
      isFormReady: {
        ...this.state.isFormReady,
        [field]: status,
        check: check && status,
      },
    });
  };

  createNewPassword = (e) => {
    e.preventDefault();
    const { password, confirmPassword, confirmPolicies } = this.state;
    const token = this.getTokenCreatePwd();

    if (password && confirmPassword && confirmPolicies) {
      this.props.createPassword(
        { password, token, confirmPolicies },
        this.redirectToLoginPage
      );
    }
  };

  // When create password success, using this function by call back action
  redirectToLoginPage = () => {
    this.props.history.push(this.getConfigObject().loginPath);
  };

  getConfigObject = () => {
    const query = new URLSearchParams(this.props.location.search);
    const userType = query.get("user_type");
    switch (userType) {
      case "admin":
        return {
          headerTitle: "Admin",
          loginPath: "admin/login",
        };
      case "provider":
        return {
          headerTitle: "Provider",
          loginPath: "provider/login",
        };
      case "client":
        return {
          headerTitle: "Client",
          loginPath: "client/login",
        };
      default:
        return {
          headerTitle: "Unknown identity",
          loginPath: "/",
        };
    }
  };

  getTokenCreatePwd = () => {
    const urlParams = new URLSearchParams(this.props.location.search);

    return urlParams.get("token") || "";
  };

  componentDidMount() {
    document.body.classList.add("bg-white");
  }

  render() {
    const { password, confirmPassword, errors, confirmPolicies, isProvider } =
      this.state;
    const { loading } = this.props;
    const { data } = this.props;

    // Get errors from server
    const errorFromServer = data && data.error ? data.error.response.data : {};
    return (
      <div className="create-new-password">
        <ToastContainer
          autoClose={2000}
          position={toast.POSITION.TOP_RIGHT}
          progressClassName="Toastify__progress-bar--rtl"
        />
        <div
          className="main-content main-content__bg-transparent"
          ref="mainContent"
        ></div>
        <AuthHeader
          xl={6}
          lg={7}
          className="create-password-header"
          title="Welcome to Ethera"
          lead={`Ethera ${this.getConfigObject().headerTitle} Portal`}
          subtitle="Please create a new password to get started"
        />
        <Container className="mt--8 pb-5">
          {loading && <Loading />}
          <Row className="justify-content-center">
            <Col lg="5" md="7">
              <Card className="bg-secondary border-0 mb-0">
                <CardBody className="px-lg-5 py-lg-5">
                  <Form role="form" onSubmit={this.createNewPassword}>
                    <FormGroup>
                      <Row>
                        <Label className="form-control-label">
                          Password
                          <sup className="text-required">*</sup>
                        </Label>
                      </Row>
                      <Row className="d-block">
                        <FormInput
                          type="password"
                          name="password"
                          handleError={this.handleError}
                          errors={errors["password"]}
                          onChange={this.handleChange}
                          regex="password"
                          minLength={8}
                          required={true}
                        />
                      </Row>
                    </FormGroup>
                    <FormGroup>
                      <Row>
                        <Label className="form-control-label">
                          Confirm Password
                          <sup className="text-required">*</sup>
                        </Label>
                      </Row>
                      <Row className="d-block">
                        <FormInput
                          type="password"
                          name="confirmPassword"
                          handleError={this.handleError}
                          errors={errors["confirmPassword"]}
                          onChange={this.handleChange}
                          required={true}
                        />
                      </Row>
                    </FormGroup>
                    <FormGroup>
                      <Row className="d-block">
                        <div className="custom-control custom-checkbox policy-checkbox">
                          <input
                            className="custom-control-input"
                            id="confirmPolicies"
                            name="confirmPolicies"
                            type="checkbox"
                            onChange={this.handleChange}
                          />
                          <div
                            className={`form__errors ${
                              confirmPolicies ? "d-none" : ""
                            }`}
                          >
                            <div className="form__errors__list">
                              <div className="form__errors__item">
                                {notConfirmPolicies}
                              </div>
                            </div>
                          </div>
                          <label
                            className="custom-control-label policy-text"
                            htmlFor="confirmPolicies"
                          >
                            {isProvider ? <ProviderPolicy /> : <ClientPolicy />}
                            <sup className="text-required">*</sup>
                          </label>
                        </div>
                      </Row>
                    </FormGroup>
                    <ErrorSummary error={errorFromServer} className="mt-3" />
                    <div className="text-center">
                      <Button
                        className="mt-4"
                        color="info"
                        type="submit"
                        disabled={
                          !password ||
                          !confirmPassword ||
                          !confirmPolicies ||
                          password !== confirmPassword
                        }
                      >
                        Agree
                      </Button>
                    </div>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

const ProviderPolicy = () => {
  return (
    <>
      By checking this box and creating your Ethera account, you acknowledge
      that you have read, understood, and agree to the&nbsp;
      <a
        href="https://www.myethera.com/baa/"
        target="_blank"
        rel="noopener noreferrer"
      >
        Business Associate Agreement
      </a>
      , the&nbsp;
      <a
        href="https://www.myethera.com/tos/"
        target="_blank"
        rel="noopener noreferrer"
      >
        Terms of Service
      </a>
      , the&nbsp;
      <a
        href="https://www.myethera.com/pp/"
        target="_blank"
        rel="noopener noreferrer"
      >
        Privacy Policy
      </a>
      , and the&nbsp;
      <a
        href="https://www.myethera.com/membership-policies/"
        target="_blank"
        rel="noopener noreferrer"
      >
        Membership and Office Services Agreements
      </a>
    </>
  );
};

const ClientPolicy = () => {
  return (
    <>
      By checking this box and creating your Ethera account, you acknowledge
      that you have read, understood, and agree to the&nbsp;
      <a
        href="https://www.myethera.com/client-tos"
        target="_blank"
        rel="noopener noreferrer"
      >
        Terms of Service
      </a>{" "}
      and&nbsp;
      <a
        href="https://www.myethera.com/client-pp"
        target="_blank"
        rel="noopener noreferrer"
      >
        Privacy Policy
      </a>
      .
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuthenticated: state.createPassword.isAuthenticated,
    haveError: state.createPassword.haveError,
    loading: state.createPassword.loading,
    data: state.createPassword.data,
  };
};

const mapActionToProps = (dispatch) => {
  return {
    createPassword: (param, callback) =>
      dispatch({
        type: CREATE_PASSWORD_ACTIONS.CREATE_PASSWORD,
        payload: { param, callback },
      }),
  };
};

export default connect(mapStateToProps, mapActionToProps)(CreateNewPassword);
