import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Form,
  Container,
  Row,
  Col,
  Label,
} from "reactstrap";
import { FormInput } from "components/Form";
import ErrorSummary from "components/ErrorSummary";
import AuthHeader from "components/Headers/AuthHeader.jsx";
import Loading from "components/Loading";

import { RESET_PASSWORD_ACTIONS } from "redux/actions";
import "./ResetPassword.scss";


const misMatchPasswordMessage = "Password does not match";

class ResetPassword extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      password: '',
      confirmPassword: '',
      errors: {},
    }
  }

  handleChange = e => {
    const { name, value } = e.target;
    delete this.state.errors[name];
    delete this.state.errors["nonFieldErrors"];
    this.setState({
      [name]: value
    },
      () => {
        if (name === 'confirmPassword' || name === "password") {
          const { password, confirmPassword } = this.state;
          if (password && confirmPassword && password !== confirmPassword) {
            this.setState({ errors: { ...this.state.errors, confirmPassword: [misMatchPasswordMessage] } })
          } else {
            const tempError = this.state.errors;
            delete tempError['confirmPassword'];
            this.setState({ errors: tempError });
          }
        }
      }
    )
  }

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState(
      { isFormReady: { ...this.state.isFormReady, [field]: status, check: check && status } }
    );
  }

  resetPassword = e => {
    e.preventDefault();
    const { password, confirmPassword } = this.state;
    const token = this.getTokenResetPwd();

    if (password && confirmPassword) {
      this.props.resetPassword({ password, token }, this.redirectToLoginPage);
    }
  }

  // When reset password success, using this function by call back action
  redirectToLoginPage = () => {
    this.props.history.push(this.getConfigObject().loginPath);
  }

  getConfigObject = () => {
    const userType = localStorage.getItem('USER_TYPE');
    switch (userType) {
      case 'admin':
        return {
          headerTitle: 'Admin',
          loginPath: 'admin/login',
        }
      case 'provider':
        return {
          headerTitle: 'Provider',
          loginPath: 'provider/login',
        }
      case 'client':
        return {
          headerTitle: 'Client',
          loginPath: 'client/login',
        }
      default:
        return {
          headerTitle: 'Unknown identity',
          loginPath: '/'
        }
    }
  }

  getTokenResetPwd = () => {
    const urlParams = new URLSearchParams(this.props.location.search);
    return urlParams.get('token') || "";
  }

  componentDidMount() {
    document.body.classList.add("bg-white");
  }

  render() {

    const { password, confirmPassword, errors } = this.state;
    const { loading } = this.props;
    const { data } = this.props;

    // Get errors from server
    const errorFromServer = data && data.error ? data.error.response.data : {};
    return (
      <>
        <ToastContainer autoClose={2000} position={toast.POSITION.TOP_RIGHT} progressClassName="Toastify__progress-bar--rtl" />
          <div className="main-content main-content__bg-transparent" ref="mainContent">
          </div>
        <AuthHeader
          title=""
          lead={`${this.getConfigObject().headerTitle} Portal`}
        />
        <Container className="mt--8 pb-5 reset-password">
          {loading && <Loading />}
          <Row className="justify-content-center">
            <Col lg="5" md="7">
              <Card className="bg-secondary border-0 mb-0">
                <CardBody className="px-lg-5 py-lg-5">
                  <Form role="form" onSubmit={this.resetPassword}>
                    <FormGroup>
                      <Row>
                        <Label className="form-control-label" >
                          Password
                                        <sup className="text-required">*</sup>
                        </Label>
                      </Row>
                      <Row className="d-block">
                        <FormInput
                          type="password"
                          name="password"
                          handleError={this.handleError}
                          errors={errors['password']}
                          onChange={this.handleChange}
                          regex="password"
                          minLength={8}
                          required={true}
                        />
                      </Row>
                    </FormGroup>
                    <FormGroup>
                      <Row>
                        <Label className="form-control-label" >
                          Confirm Password
                                        <sup className="text-required">*</sup>
                        </Label>
                      </Row>
                      <Row className="d-block">
                        <FormInput
                          type="password"
                          name="confirmPassword"
                          handleError={this.handleError}
                          errors={errors['confirmPassword']}
                          onChange={this.handleChange}
                          required={true}
                        />
                      </Row>
                    </FormGroup>
                    <ErrorSummary error={errorFromServer} className="mt-3" />
                    <div className="text-center submit-button">
                      <Button className="mt-4" color="info" type="submit" disabled={(!password || !confirmPassword) || (password !== confirmPassword)}>
                        Submit
                      </Button>
                    </div>
                    <div className="text-center mt-4">
                      <Link to={this.getConfigObject().loginPath}>
                        <small className="back">
                          <i className="fas fa-chevron-circle-left" /> Go login page.
                        </small>
                      </Link>
                    </div>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    isAuthenticated: state.resetPassword.isAuthenticated,
    haveError: state.resetPassword.haveError,
    loading: state.resetPassword.loading,
    data: state.resetPassword.data,
  };
};

const mapActionToProps = dispatch => {
  return {
    resetPassword: (param, callback) => dispatch({ type: RESET_PASSWORD_ACTIONS.RESET_PASSWORD, payload: {param, callback} }),
  };
};

export default connect(
  mapStateToProps,
  mapActionToProps
)(ResetPassword);
