import {
    CLIENT_INVOICE_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {},
    invoiceDetail: {}
};

export default function clientInvoiceReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Invoice List
        case CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES:
            return {
                ...state,
                loading: true,
            }
        case CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Invoice List
        case CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_DETAIL:
            return {
                ...state,
                loading: true,
            }
        case CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_DETAIL_SUCCESS:
            return {
                ...state,
                invoiceDetail: payload,
                loading: false,
            }
        case CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_DETAIL_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Reload Data
        case CLIENT_INVOICE_ACTIONS.PORTAL_CLIENT_ADD_PAYMENT:
            return {
                ...state,
                loading: true,
            }
        case CLIENT_INVOICE_ACTIONS.PORTAL_CLIENT_ADD_PAYMENT_SUCCESS:
            const list = [...state.data.result];
            let listUpdate = list.map(item => {
                if (item.id === payload.invoiceId) {
                    item.status = 0
                    item.priceRemain = 0
                }
                return item
            });
            return {
                data: { ...state.data, result: listUpdate },
                loading: false,
            }
        case CLIENT_INVOICE_ACTIONS.PORTAL_CLIENT_ADD_PAYMENT_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
