import actions from '../actions/locksActions'

let initState = {
    loading: false,
    data: {}
}

export default function locksReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        case actions.GET_LOCKS:
            return {
                ...state,
                loading: true
            }
        case actions.GET_LOCKS_SUCCESS:
            return {
                data: payload.data,
                loading: false
            }
        case actions.GET_LOCKS_FAIL:
            return {
                ...state,
                loading: false
            }
        default:
            return state;
    }
}
