import React, { Component } from "react";
import { Input } from "reactstrap";
import _ from "lodash";

const messages = {
  required: "This field is required",
  email: "Invalid email address",
  phoneNumber: "Invalid phone number",
  number: "This field must be number",
  minLength: ["This field must be at least ", " characters"],
  maxlengths: ["This field must be the most ", " characters"],
  password: "Password including UPPER, lowercase, symbol and numbers."
}

const regexList = {
  // eslint-disable-next-line
  email: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
  // eslint-disable-next-line
  phoneNumber: /^[+]*[(]{0,1}[0-9]{1,4}[)]{0,1}[-\s\./0-9]*$/,
  // eslint-disable-next-line
  number: /^-?[0-9,\.]+$/,
  // eslint-disable-next-line
  password: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,32}$/,

}

export default class FormInput extends Component {
  constructor(props) {
    super(props);
    this.state = {
      internalErrors: {}
    }
  }

  handleOnChange(event) {
    const errors = this.state.internalErrors;
    const { regex, minLength, maxlengths } = this.props;
    const value = event.target.value;
    //Required
    delete errors['required'];

    //Regex
    if (regex) {
      value ? (regexList[regex].test(value) ? delete errors['regex'] : errors['regex'] = messages[regex])
        : delete errors['regex'];
    }

    //Minlength
    if (minLength) {
      value && value.length < minLength ? errors['minLength'] = messages['minLength'][0] + minLength + messages['minLength'][1]
        : delete errors['minLength'];
    }

    //maxlengths
    if (maxlengths) {
      value && value.length > maxlengths ? errors['maxlengths'] = messages['maxlengths'][0] + maxlengths + messages['maxlengths'][1]
        : delete errors['maxlengths'];
    }

    this.setState({ internalErrors: errors });
    this.props.onChange && this.props.onChange(event);
  }

  async handleOnBlur(event) {
    if (this.props.required && !event.target.value) {
      await this.setState({ internalErrors: { ...this.state.internalErrors, required: messages.required } });
    }
    if (this.props.handleError) {
      this.props.handleError(this.props.name, _.isEmpty(this.state.internalErrors))
    }
  }

  getArrayError(internalErrors, externalErrors) {
    const tempArr = [];
    for (let key in internalErrors) {
      tempArr.push(internalErrors[key]);
    }
    return [...tempArr, ...externalErrors];
  }

  render() {
    const { type, name, errors, defaultValue, disabled, maxlengths, autoComplete, placeholder, value } = this.props;
    const { internalErrors } = this.state;
    const fieldErrors = this.getArrayError(internalErrors, errors || []);
    const isError = fieldErrors && fieldErrors.length > 0;
    return (
      <div className={`form__controller ${isError ? 'form__controller--error' : ''}`}>
        {
          value ?
            <Input
              type={type}
              name={name}
              onChange={e => this.handleOnChange(e)}
              onBlur={e => this.handleOnBlur(e)}
              value={value}
              disabled={disabled}
              maxlengths={maxlengths}
              autoComplete={autoComplete || "off"}
              placeholder={placeholder}
            /> :
            <Input
              type={type}
              name={name}
              onChange={e => this.handleOnChange(e)}
              onBlur={e => this.handleOnBlur(e)}
              defaultValue={defaultValue}
              disabled={disabled}
              maxlengths={maxlengths}
              autoComplete={autoComplete || "off"}
              placeholder={placeholder}
            />
        }

        {
          isError &&
          <div className="form__errors">
            <div className="form__errors__list">
              <div className="form__errors__item">
                {fieldErrors[0]}
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}
