import Moment from 'moment';
import _ from 'lodash';
import { extendMoment } from 'moment-range';
import { getDate, getTimeFull } from './UtilsHelper';
import booking from '../constants/booking';

const moment = extendMoment(Moment);
const WEEKDAY_TIME = 'ETHH:mm:ss';

function getAvailableSlots({ bookings, globalBookings, spaces, selectedSpaces, activeStart, activeEnd, validSlotDuration, venues, selectedVenue }) {
    const venue = venues.find(v => v.id === selectedVenue);
    const myBookings = bookings;
    const selectedSpacesInfo = spaces.filter(s => selectedSpaces.includes(s.id));
    const spaceBookingsMap = _getSpaceBookingMap(spaces, globalBookings);
    const baseStep = 15 * 60 * 1000; // 15 minutes

    let validSlots = [];
    const calendarRange = moment(activeEnd).diff(moment(activeStart));
    const slotCount = calendarRange / baseStep;

    for (let slotIndex = 0; slotIndex < slotCount; slotIndex++) {
        const slotStart = moment(activeStart).add(baseStep * slotIndex);
        if (slotStart.diff(moment()).valueOf() <= 0) {
            continue;
        }

        const slotEnd = moment(slotStart).add(validSlotDuration);
        const slotRange = moment.range(slotStart, slotEnd);
        if (_isOverLapBookings(myBookings, slotRange)) {
            continue;
        }

        if (!_slotValidInAnySpace(slotRange, selectedSpacesInfo, spaceBookingsMap, venue)) {
            continue;
        }

        if (!_checkAndMergeWithExist(slotRange, validSlots)) {
            validSlots.push(slotRange);
        }
    }

    return validSlots;
}

function getAvailableSpacesInRange(spaces, venue, bookings, startTime, endTime, currentBookingId = null) {
    if (!venue) {
        return [];
    }
    const range = moment.range(startTime, endTime);
    const spaceBookingsMap = _getSpaceBookingMap(spaces, bookings);
    let availableSpaces = spaces.filter(space => {
        if (_isInBlackoutDate(range, space, venue)) {
            return false;
        }

        if (!_isInSpaceOperationHours(space, range)) {
            return false;
        }

        const spaceBookings = spaceBookingsMap[space.id];
        if (_isOverLapBookings(spaceBookings, range, currentBookingId)) {
            return false;
        }

        return true;
    });
    return availableSpaces;

}

export function _isOverLapBookings(bookings, range, currentBookingId = null) {
    for (const booking of bookings) {
        const bookingRange = _getBookingRange(booking);
        if (bookingRange.overlaps(range)) {
            if (currentBookingId && currentBookingId === booking.id) {
                continue;
            }

            return true;
        }
    }

    return false;
}

/**
 * Returns an object which the key is the space id and the value is an array with all its bookings
 */
export function _getSpaceBookingMap(spaces, bookings) {
    let map = {};
    for (let space of spaces) {
        map[space.id] = bookings.filter(b => (b.space && b.space.id) === space.id);
    }
    return map;
}

function _checkAndMergeWithExist(slot, existSlots) {
    for (const i in existSlots) {
        const existSlot = existSlots[i];
        if (slot.overlaps(existSlot, { adjacent: true })) {
            existSlots[i] = existSlot.add(slot, { adjacent: true });
            return true;
        }
    }

    return false;
}

function _slotValidInAnySpace(slot, spaces, spaceBookingsMap, venue) {
    for (const space of spaces) {
        if (_isInBlackoutDate(slot, space, venue)) {
            continue;
        }

        if (!_isInSpaceOperationHours(space, slot)) {
            continue;
        }

        const spaceBookings = spaceBookingsMap[space.id];
        if (_isOverLapBookings(spaceBookings, slot)) {
            continue
        }
        return true;
    }

    return false;
}

function _isInBlackoutDate(slot, space, venue) {
    const slotDate = getDate(slot.start);
    const inSpace = _isInBlackoutDateList(slotDate, space.blackoutDates);
    const inVenue = _isInBlackoutDateList(slotDate, venue.blackoutDates);
    return inSpace || inVenue;
}

function _isInBlackoutDateList(slotDate, blackoutDates) {
    for (const date of blackoutDates) {
        if (slotDate === date.date) {
            return true;
        }
    }
    return false;
}

function _isInSpaceOperationHours(space, range) {
    const { operatingHours } = space;
    const bookingStart = range.start;
    const bookingEnd = range.end;

    const bookingWeekDay = bookingStart.isoWeekday();
    const bookingStartTime = getTimeFull(bookingStart);
    const bookingEndTime = getTimeFull(bookingEnd);

    const tempRangeStart = moment(`${bookingWeekDay}T${bookingStartTime}`, WEEKDAY_TIME);
    const tempRangeEnd = moment(`${bookingWeekDay}T${bookingEndTime}`, WEEKDAY_TIME);
    for (const hour of operatingHours) {
        if (!hour.isUsable) {
            continue;
        }
        const spaceOpenTime = moment(`${hour.dayOfWeek}T${hour.startTime}`, WEEKDAY_TIME);
        const spaceCloseTime = moment(`${hour.dayOfWeek}T${hour.closeTime}`, WEEKDAY_TIME);
        const spaceRange = moment.range(spaceOpenTime, spaceCloseTime);

        if (spaceRange.contains(tempRangeStart) && spaceRange.contains(tempRangeEnd)) {
            return true;
        }
    }
    return false;
}

function _getBookingRange(booking) {
    const start = _getBookingStart(booking);
    const end = _getBookingEnd(booking);
    return moment.range(start, end);
}

function _getBookingStart(booking) {
    return _getDateTime(booking.startDate, booking.startTime);
}

function _getBookingEnd(booking) {
    return _getDateTime(booking.startDate, booking.endTime);
}

function _getDateTime(date, time) {
    return moment(`${date}T${time}`);
}

export {
    getAvailableSlots,
    getAvailableSpacesInRange,
}