import client from '../helpers/HttpClient'
import { urlQueryFromObject } from '../helpers/UtilsHelper';
const endpoint = '/appointments';

export default {
    async list(filters) {
        const query = urlQueryFromObject(filters);
        let path = `${endpoint}${query}`;
        let response = await client.get(path);
        return response.data;
    },
    async update(payload) {
        const { bookingId, appointmentId, data } = payload
        let path = `bookings/${bookingId}${endpoint}/${appointmentId}`;
        let response = await client.patch(path, data);
        return response.data;
    }
}
