import { call, put, takeLatest } from "redux-saga/effects";
import { BILLING_ACTIONS } from "../actions";
import billingApi from '../../api/billingApi';
import invoiceApi from '../../api/invoiceApi';
import { toast } from 'react-toastify';

function* getList({ payload }) {
    const { filters } = payload;
    try {
        const data = yield call(_getList.bind(this, filters))
        yield put({ type: BILLING_ACTIONS.GET_BOOKING_INVOICE_LIST_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: BILLING_ACTIONS.GET_BOOKING_INVOICE_LIST_FAIL })
    }
}

function _getList(filters) {
    return billingApi.list(filters).then(response => {
        return response
    })
}

function* getDetail({ payload }) {
    const { id } = payload;
    try {
        const data = yield call(_getDetail.bind(this, id))
        yield put({ type: BILLING_ACTIONS.GET_BOOKING_INVOICE_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: BILLING_ACTIONS.GET_BOOKING_INVOICE_DETAIL_FAIL })
    }
}

function _getDetail(id) {
    if (id === "preview-invoice") {
        return billingApi.previewInvoice().then(response => {
            return response
        })
    }
    else {
        return billingApi.detail(id).then(response => {
            return response
        })
    }
}

function* update({ payload }) {
    const { params } = payload
    try {
        const data = yield call(_update.bind(this, params))
        yield put({ type: BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_SUCCESS, payload: data })
        toast.success("Update Invoice success !");

    } catch (error) {
        yield put({ type: BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_FAIL })
        toast.error("Update Invoice fail !");
    }
}

function* updateStatus({ payload }) {
    try {
        const data = yield call(_update.bind(this, payload))
        yield put({ type: BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_STATUS_SUCCESS, payload: data })
        toast.success("Update Invoice Status success !");

    } catch (error) {
        yield put({ type: BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_STATUS_FAIL })
        toast.error("Update Invoice Status fail !");
    }
}

function _update(payload) {
    return billingApi.update(payload).then(response => {
        return response
    })
}

function* triggerCronJob(payload) {
    try {
        yield call(_triggerCronJob.bind(this, payload))
        yield put({ type: BILLING_ACTIONS.TRIGGER_CRON_JOB_SUCCESS })
        toast.success("Trigger the cron job charge-invoices success!");

    } catch (error) {
        yield put({ type: BILLING_ACTIONS.TRIGGER_CRON_JOB_FAIL })
        if (error.response.status === 406) {
            toast.error(error.response.data.message);
        } else {
            toast.error("Trigger the cron job charge-invoices fail!");
        }
    }
}

function _triggerCronJob(payload) {
    return billingApi.triggerCronJob(payload).then(response => {
        return response
    })
}

function* getManualInvoiceList({ payload }) {
    const { filters } = payload;
    try {
        const data = yield call(_getManualInvoiceList.bind(this, filters))
        yield put({ type: BILLING_ACTIONS.GET_MANUAL_INVOICE_LIST_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: BILLING_ACTIONS.GET_MANUAL_INVOICE_LIST_FAIL })
    }
}

function _getManualInvoiceList(filters) {
    return invoiceApi.listManualInvoice(filters).then(response => {
        return response
    })
}

function* getManualInvoiceDetail({ payload }) {
    const { id } = payload;
    try {
        const data = yield call(_getManualInvoiceDetail.bind(this, id))
        yield put({ type: BILLING_ACTIONS.GET_MANUAL_INVOICE_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: BILLING_ACTIONS.GET_MANUAL_INVOICE_DETAIL_FAIL })
    }
}

function _getManualInvoiceDetail(id) {
    return invoiceApi.manualInvoiceDetail(id).then(response => {
        return response
    })
}

function* updateManualInvoiceStatus({ payload }) {
    try {
        const data = yield call(_updateManualInvoice.bind(this, payload))
        yield put({ type: BILLING_ACTIONS.UPDATE_MANUAL_INVOICE_STATUS_SUCCESS, payload: data })
        toast.success("Update Invoice Status success !");

    } catch (error) {
        yield put({ type: BILLING_ACTIONS.UPDATE_MANUAL_INVOICE_STATUS_FAIL })
        toast.error("Update Invoice Status fail !");
    }
}

function _updateManualInvoice(payload) {
    return invoiceApi.update(payload).then(response => {
        return response
    })
}

function* chargeInvoice({ payload }) {
    const { invoiceId } = payload;
    try {
        const data = yield call(_chargeInvoice.bind(this, invoiceId))
        yield put({ type: BILLING_ACTIONS.PROVIDER_CHARGE_INVOICE_SUCCESS, payload: data })
        toast.success("Charge invoice success !");
    } catch (error) {
        yield put({ type: BILLING_ACTIONS.PROVIDER_CHARGE_INVOICE_FAIL })
        toast.error("Charge invoice fail !");
    }
}

function _chargeInvoice(id) {
    return invoiceApi.chargeInvoice(id).then(response => {
        return response
    })
}

export function* watchBillingAsync() {
    yield takeLatest(BILLING_ACTIONS.GET_BOOKING_INVOICE_LIST, getList);
    yield takeLatest(BILLING_ACTIONS.GET_BOOKING_INVOICE_DETAIL, getDetail);
    yield takeLatest(BILLING_ACTIONS.UPDATE_BOOKING_INVOICE, update);
    yield takeLatest(BILLING_ACTIONS.UPDATE_BOOKING_INVOICE_STATUS, updateStatus);
    yield takeLatest(BILLING_ACTIONS.TRIGGER_CRON_JOB, triggerCronJob);

    yield takeLatest(BILLING_ACTIONS.GET_MANUAL_INVOICE_LIST, getManualInvoiceList);
    yield takeLatest(BILLING_ACTIONS.GET_MANUAL_INVOICE_DETAIL, getManualInvoiceDetail);
    yield takeLatest(BILLING_ACTIONS.UPDATE_MANUAL_INVOICE_STATUS, updateManualInvoiceStatus);
    yield takeLatest(BILLING_ACTIONS.PROVIDER_CHARGE_INVOICE, chargeInvoice);
}
