import { takeLatest, call, put } from "redux-saga/effects";
import { RESET_PASSWORD_ACTIONS } from '../actions';
import passwordApi from '../../api/passwordApi';
import { toast } from 'react-toastify';

function* sendMailResetPassword(payload) {
    try {
        const data = yield call(_sendMailResetPassword.bind(this, payload))
        yield put({ type: RESET_PASSWORD_ACTIONS.SEND_MAIL_RESET_PASSWORD_SUCCESS, payload: data })
        toast.success("Please check your email!");

    } catch (error) {
        yield put({ type: RESET_PASSWORD_ACTIONS.SEND_MAIL_RESET_PASSWORD_FAIL, payload: { error }  })
        toast.error("Send mail to reset password failed!");
    }
}

function _sendMailResetPassword(payload) {
    return passwordApi.sendMailResetPassword(payload.data).then(response => {
        return response
    })
}

function* resetPassword(data) {

    try {
        const { param, callback } = data.payload;
        const payload = yield call(_resetPassword.bind(this, param));
        yield put({ type: RESET_PASSWORD_ACTIONS.RESET_PASSWORD_SUCCESS, payload: payload })
        callback();
        toast.success("Reset password success! Please login again!");

    } catch (error) {
        yield put({ type: RESET_PASSWORD_ACTIONS.RESET_PASSWORD_FAIL, payload: { error } })
        toast.error("Reset password failed!");
    }
}

function _resetPassword(payload) {
    return passwordApi.resetPassword(payload).then(response => {
        return response;
    })
}

export function* watchResetPasswordAsync() {
    yield takeLatest(RESET_PASSWORD_ACTIONS.SEND_MAIL_RESET_PASSWORD, sendMailResetPassword);
    yield takeLatest(RESET_PASSWORD_ACTIONS.RESET_PASSWORD, resetPassword);
}
