import { call, put, takeLatest } from "redux-saga/effects";
import { CLIENT_INVOICE_ACTIONS } from "../actions";
import invoiceApi from '../../api/invoiceApi';

function* clientGetInvoice({ payload }) {
    try {
        const data = yield call(_clientGetInvoice.bind(this, payload))
        yield put({ type: CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_FAIL })
    }
}

function _clientGetInvoice(payload) {
    return invoiceApi.listInvoice(payload).then(response => {
        return response
    })
}

function* clientGetInvoiceDetail({ payload }) {
    const { id } = payload
    try {
        const data = yield call(_clientGetInvoiceDetail.bind(this, id))
        yield put({ type: CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_DETAIL_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_DETAIL_FAIL })
    }
}

function _clientGetInvoiceDetail(id) {
    return invoiceApi.invoiceDetail(id).then(response => {
        return response
    })
}

export function* watchClientInvoiceAsync() {
    yield takeLatest(CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES, clientGetInvoice);
    yield takeLatest(CLIENT_INVOICE_ACTIONS.CLIENT_GET_INVOICES_DETAIL, clientGetInvoiceDetail);
}
