const actionTypes = {
    GET_BOOKING_INVOICE_LIST: "GET_BOOKING_INVOICE_LIST",
    GET_BOOKING_INVOICE_LIST_SUCCESS: "GET_BOOKING_INVOICE_LIST_SUCCESS",
    GET_BOOKING_INVOICE_LIST_FAIL: "GET_BOOKING_INVOICE_LIST_FAIL",

    GET_BOOKING_INVOICE_DETAIL: "GET_BOOKING_INVOICE_DETAIL",
    GET_BOOKING_INVOICE_DETAIL_SUCCESS: "GET_BOOKING_INVOICE_DETAIL_SUCCESS",
    GET_BOOKING_INVOICE_DETAIL_FAIL: "GET_BOOKING_INVOICE_DETAIL_FAIL",

    UPDATE_BOOKING_INVOICE: "UPDATE_BOOKING_INVOICE",
    UPDATE_BOOKING_INVOICE_SUCCESS: "UPDATE_BOOKING_INVOICE_SUCCESS",
    UPDATE_BOOKING_INVOICE_FAIL: "UPDATE_BOOKING_INVOICE_FAIL",

    UPDATE_BOOKING_INVOICE_STATUS: "UPDATE_BOOKING_INVOICE_STATUS",
    UPDATE_BOOKING_INVOICE_STATUS_SUCCESS: "UPDATE_BOOKING_INVOICE_STATUS_SUCCESS",
    UPDATE_BOOKING_INVOICE_STATUS_FAIL: "UPDATE_BOOKING_INVOICE_STATUS_FAIL",

    TRIGGER_CRON_JOB: "TRIGGER_CRON_JOB",
    TRIGGER_CRON_JOB_SUCCESS: "TRIGGER_CRON_JOB_SUCCESS",
    TRIGGER_CRON_JOB_FAIL: "TRIGGER_CRON_JOB_FAIL",

    // SEND_INVOICE: "SEND_INVOICE",
    // SEND_INVOICE_SUCCESS: 'SEND_INVOICE_SUCCESS',
    // SEND_INVOICE_FAIL: 'SEND_INVOICE_FAIL',

    GET_MANUAL_INVOICE_LIST: "GET_MANUAL_INVOICE_LIST",
    GET_MANUAL_INVOICE_LIST_SUCCESS: "GET_MANUAL_INVOICE_LIST_SUCCESS",
    GET_MANUAL_INVOICE_LIST_FAIL: "GET_MANUAL_INVOICE_LIST_FAIL",

    GET_MANUAL_INVOICE_DETAIL: "GET_MANUAL_INVOICE_DETAIL",
    GET_MANUAL_INVOICE_DETAIL_SUCCESS: "GET_MANUAL_INVOICE_DETAIL_SUCCESS",
    GET_MANUAL_INVOICE_DETAIL_FAIL: "GET_MANUAL_INVOICE_DETAIL_FAIL",

    UPDATE_MANUAL_INVOICE_STATUS: "UPDATE_MANUAL_INVOICE_STATUS",
    UPDATE_MANUAL_INVOICE_STATUS_SUCCESS: "UPDATE_MANUAL_INVOICE_STATUS_SUCCESS",
    UPDATE_MANUAL_INVOICE_STATUS_FAIL: "UPDATE_MANUAL_INVOICE_STATUS_FAIL",

    GET_INVOICE_PREVIEW_DETAIL: "GET_INVOICE_PREVIEW_DETAIL",
    GET_INVOICE_PREVIEW_DETAIL_SUCCESS: "GET_INVOICE_PREVIEW_DETAIL_SUCCESS",
    GET_INVOICE_PREVIEW_DETAIL_FAIL: "GET_INVOICE_PREVIEW_DETAIL_FAIL",

    PROVIDER_CHARGE_INVOICE: "PROVIDER_CHARGE_INVOICE",
    PROVIDER_CHARGE_INVOICE_SUCCESS: "PROVIDER_CHARGE_INVOICE_SUCCESS",
    PROVIDER_CHARGE_INVOICE_FAIL: "PROVIDER_CHARGE_INVOICE_FAIL",
}

export default actionTypes

export const getList = (filters) => ({ type: actionTypes.GET_BOOKING_INVOICE_LIST, payload: { filters } })
export const getInvoiceDetail = (id) => ({ type: actionTypes.GET_BOOKING_INVOICE_DETAIL, payload: { id } })
export const update = (params) => ({ type: actionTypes.UPDATE_BOOKING_INVOICE, payload: { params } })
export const updateStatus = (status, id) => ({ type: actionTypes.UPDATE_BOOKING_INVOICE_STATUS, payload: { status, id } })
export const triggerCronJob = (date) => ({ type: actionTypes.TRIGGER_CRON_JOB, payload: date })

export const getManualInvoiceList = (filters) => ({ type: actionTypes.GET_MANUAL_INVOICE_LIST, payload: { filters } })
export const getManualInvoiceDetail = (id) => ({ type: actionTypes.GET_MANUAL_INVOICE_DETAIL, payload: { id } })
export const updateManualInvoiceStatus = (status, id) => ({ type: actionTypes.UPDATE_MANUAL_INVOICE_STATUS, payload: { status, id } })

export const chargeInvoice = (invoiceId) => ({ type: actionTypes.PROVIDER_CHARGE_INVOICE, payload: { invoiceId } })