import React, { Component } from 'react';

import {
    Modal,
    ModalBody,
    ModalFooter,
    Button,
} from 'reactstrap';
import { Link } from 'react-router-dom';

import stripeApi from '../../api/stripeApi';

class EnterPaymentMethodModal extends Component {
    constructor(prop) {
        super(prop);
        this.state = {
            isOpen: false,
        }
    }

    componentDidMount() {
        // Check stripe data, to show welcome modal
        stripeApi.getCustomer().then(data => {
            // When empty creadit card, then set state show popup welcome
            if (data && data.sources.data.length === 0) {
                this.toggleModal();
            };
        }).catch(e => {
            // Not found Credit card, then set state show popup welcome
            if (!e.response) {
                console.error(e);
                return;
            }
            if (e.response.status === 404) {
                this.toggleModal();
            }
        })
    }

    toggleModal = () => {
        this.setState({ isOpen: !this.state.isOpen });
    }

    render() {
        const { data } = this.props;
        const { isOpen } = this.state;
        return (
            <Modal isOpen={isOpen} size="md">
                <div className="modal-header">
                    <button aria-label="Close" className="close" data-dismiss="modal" type="button" onClick={this.toggleModal}>
                        <span aria-hidden={true}></span>
                    </button>
                </div>
                <ModalBody>
                    <p>Hi {data.fullName},</p>
                    <p>Please enter a payment method to start booking rooms. 
                        You also have the option of accepting credit card payments from your clients by using a Stripe Connect account.
                    </p>
                    <p> 
                        If you have questions about how to create/connect your Stripe account, please contact us at <a href="mailto:support@myethera.com">support@myethera.com</a>.
                    </p>
                </ModalBody>
                <ModalFooter>
                    <Link to={`/provider/billing`} className="d-block w-100 text-center">
                        <Button color="primary" onClick={this.toggleModal}>Set up Billing</Button>
                    </Link>
                </ModalFooter>
            </Modal>
        );
    }
}

export default EnterPaymentMethodModal;