import { INVOICE_ACTIONS } from "../actions";
import update from 'immutability-helper';

const initState = {
    loading: false,
    data: {},
    appointmentInvoiceList: []
};

export default function invoiceReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        // Get Invoice List
        case INVOICE_ACTIONS.GET_CLIENT_INVOICES:
            return {
                ...state,
                loading: true,
            }
        case INVOICE_ACTIONS.GET_CLIENT_INVOICES_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case INVOICE_ACTIONS.GET_CLIENT_INVOICES_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Add Payment
        case INVOICE_ACTIONS.CLIENT_ADD_PAYMENT:
            return {
                ...state,
                loading: true,
            }
        case INVOICE_ACTIONS.CLIENT_ADD_PAYMENT_SUCCESS:
            const list = state.data.result.map(item => {
                if (item.id === payload.invoice) {
                    item.priceRemain = Number(item.priceRemain) - Number(payload.price)
                    item.clientPayments.push(payload)
                }
                if (0 > Number(item.priceRemain) - Number(payload.price)) {
                    item.status = 0
                }
                return item
            })
            return {
                data: { ...state.data, result: list },
                loading: false,
            }
        case INVOICE_ACTIONS.CLIENT_ADD_PAYMENT_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Get Booking Invoice List
        case INVOICE_ACTIONS.GET_BOOKING_INVOICES:
            return {
                ...state,
                loading: true,
            }
        case INVOICE_ACTIONS.GET_BOOKING_INVOICES_SUCCESS:
            return {
                ...state,
                appointmentInvoiceList: payload.result.map(item => item.appointment.id),
                loading: false,
            }
        case INVOICE_ACTIONS.GET_BOOKING_INVOICES_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Create Booking Invoice
        case INVOICE_ACTIONS.CREATE_APPOINTMENT_INVOICE:
            return {
                ...state,
            }
        case INVOICE_ACTIONS.CREATE_APPOINTMENT_INVOICE_SUCCESS:
            const { appointment } = payload
            return update(state, {
                appointmentInvoiceList: { $push: [appointment] }
            })
        case INVOICE_ACTIONS.CREATE_APPOINTMENT_INVOICE_FAIL:
            return {
                ...state,
            }

        // Cancel Invoice Status
        case INVOICE_ACTIONS.UPDATE_INVOICE_STATUS:
            return {
                ...state,
                loading: true,
            }
        case INVOICE_ACTIONS.UPDATE_INVOICE_STATUS_SUCCESS:
            const tempList = state.data.result.map(item => {
                if (item.id === payload.id) {
                    item.status = payload.status
                }
                return item
            })
            return {
                data: { ...state.data, result: tempList },
                loading: false,
            }
        case INVOICE_ACTIONS.UPDATE_INVOICE_STATUS_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
