const actions = {
    LOGIN: 'LOGIN',
    LOGIN_SUCCESS: 'LOGIN_SUCCESS',
    LOGIN_FAIL: 'LOGIN_FAIL',
    LOGIN_AFTER_REGISTER: 'LOGIN_AFTER_REGISTER',
    LOGOUT: 'LOGOUT',
    LOGOUT_EXPIRE: 'LOGOUT_EXPIRE',
    TURN_OFF_EXPIRE_ALERT: 'TURN_OFF_EXPIRE_ALERT',
    REFRESH_TOKEN: 'REFRESH_TOKEN',
    REFRESH_TOKEN_SUCCESS: 'REFRESH_TOKEN_SUCCESS',
    AUTH_UPDATE_FIRST_LOGIN_STATUS: 'AUTH_UPDATE_FIRST_LOGIN_STATUS'
}

export default actions

export const logout = () => ({ type: actions.LOGOUT })
export const updateFirstLoginStatus = (status) => ({ type: actions.AUTH_UPDATE_FIRST_LOGIN_STATUS, payload: { status } })