import actions from '../actions/venuesActions'

let initState = {
    loading: false,
    data: {}
}

export default function authReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        case actions.GET_VENUES:
            return {
                ...state,
                loading: true
            }
        case actions.GET_VENUES_SUCCESS:
            return {
                data: payload.data,
                loading: false
            }
        case actions.GET_VENUES_FAIL:
            return {
                ...state,
                loading: false
            }
        case actions.REMOVE_VENUE:
            const listVenue = state.data.result.filter(item => item.id !== payload.venueId)
            return {
                data: { ...state.data, result: listVenue }
            }
        default:
            return state;
    }
}
