const actionTypes = {
    CLIENT_GET_INVOICES: "CLIENT_GET_INVOICES",
    CLIENT_GET_INVOICES_SUCCESS: 'CLIENT_GET_INVOICES_SUCCESS',
    CLIENT_GET_INVOICES_FAIL: 'CLIENT_GET_INVOICES_FAIL',

    CLIENT_GET_INVOICES_DETAIL: "CLIENT_GET_INVOICES_DETAIL",
    CLIENT_GET_INVOICES_DETAIL_SUCCESS: 'CLIENT_GET_INVOICES_DETAIL_SUCCESS',
    CLIENT_GET_INVOICES_DETAIL_FAIL: 'CLIENT_GET_INVOICES_DETAIL_FAIL',

    PORTAL_CLIENT_ADD_PAYMENT: "PORTAL_CLIENT_ADD_PAYMENT",
    PORTAL_CLIENT_ADD_PAYMENT_SUCCESS: 'PORTAL_CLIENT_ADD_PAYMENT_SUCCESS',
    PORTAL_CLIENT_ADD_PAYMENT_FAIL: 'PORTAL_CLIENT_ADD_PAYMENT_FAIL',
}

export default actionTypes

export const clientGetInvoice = (filters) => ({ type: actionTypes.CLIENT_GET_INVOICES, payload: { filters } })
export const clientGetInvoiceDetail = (id) => ({ type: actionTypes.CLIENT_GET_INVOICES_DETAIL, payload: { id } })

export const reloadData = (invoiceId) => ({ type: actionTypes.PORTAL_CLIENT_ADD_PAYMENT_SUCCESS, payload: { invoiceId } })
