import React from "react";
import { connect } from "react-redux";
import { Link } from 'react-router-dom';
// nodejs library that concatenates classes
import classnames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// reactstrap components
import {
  Collapse,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  DropdownToggle,
  Media,
  Navbar,
  Nav,
  Container,
  NavItem
} from "reactstrap";

import EnterPaymentMethodModal from './EnterPaymentMethodModal';

import { logout } from "../../redux/actions/auth.action";
import { getAdminDetail, getProviderDetail, getClientDetail } from "../../redux/actions/navBarsAction";
import { getUserFullName } from '../../helpers/UtilsHelper'
import defaultAvatar from '../../assets/img/theme/default-avatar.png';
import AuthHelper from 'helpers/AuthHelper';

class AdminNavbar extends React.Component {
  constructor(prop) {
    super(prop);
    this.state = {
      avatar: ''
    }
  }

  componentDidMount() {
    const user = AuthHelper.getUserInfo()
    if (user.is_staff) {
      this.props.getAdminDetail(user.user_id)
    }
    if (user.is_provider) {
      this.props.getProviderDetail(user.user_id)
    }
    else if (user.is_client) {
      this.props.getClientDetail(user.user_id)
    }
  }

  Logout = () => {
    AuthHelper.destroyToken();
    this.props.logout();
  }

  getConfigObject = pathName => {
    switch (pathName) {
      case '/admin/register':
        return {
          loginPath: '/admin/login',
          headerTitle: 'Admin'
        }
      case '/provider/register':
        return {
          loginPath: '/provider/login',
          headerTitle: 'Provider'
        }
      case '/client/register':
        return {
          loginPath: '/client/login',
          headerTitle: 'Client'
        }
      default:
        return null;
    }
  }

  render() {
    const { data } = this.props
    const { avatar, isProvider, isStaff, isClient } = data
    const fullName = getUserFullName(data)
    return (
      <>
        <Navbar
          className={classnames(
            "navbar-top navbar-expand border-bottom-0",
            { "navbar-dark bg-info": this.props.theme === "dark" },
            { "navbar-light bg-secondary": this.props.theme === "light" }
          )}
        >
          <Container fluid>
            <Collapse navbar isOpen={true}>
              <Nav className="align-items-center ml-md-auto" navbar>
                <NavItem className="d-xl-none">
                  <div
                    className={classnames(
                      "pr-3 sidenav-toggler",
                      { active: this.props.sidenavOpen },
                      { "sidenav-toggler-dark": this.props.theme === "dark" }
                    )}
                    onClick={this.props.toggleSidenav}
                  >
                    <div className="sidenav-toggler-inner">
                      <i className="sidenav-toggler-line" />
                      <i className="sidenav-toggler-line" />
                      <i className="sidenav-toggler-line" />
                    </div>
                  </div>
                </NavItem>
              </Nav>
              <Nav className="align-items-center ml-auto ml-md-0" navbar>
                <UncontrolledDropdown nav>
                  <DropdownToggle className="nav-link pr-0" color="" tag="a">
                    <Media className="align-items-center">
                      {(isProvider || isStaff) && (
                        <span>
                          <img
                            alt="..."
                            className="avatar avatar-sm rounded-circle cursor-pointer"
                            style={{ objectFit: 'cover' }}
                            src={avatar ? avatar : defaultAvatar}
                          />
                        </span>
                      )}
                      <Media className="ml-2 d-none d-lg-block">
                        <span className="mb-0 text-sm font-weight-bold cursor-pointer">
                          {fullName}
                        </span>
                      </Media>
                    </Media>
                  </DropdownToggle>
                  <DropdownMenu right>
                    <DropdownItem className="noti-title" header tag="div">
                      <h6 className="text-overflow m-0">Welcome!</h6>
                    </DropdownItem>
                    <DropdownItem className={isProvider ? '' : 'd-none'} onClick={e => e.preventDefault()}>
                      <i className="ni ni-single-02" />
                      <Link to={`/provider/profile`}><span>My profile</span></Link>
                    </DropdownItem>
                    <DropdownItem className={isStaff ? '' : 'd-none'} onClick={e => e.preventDefault()}>
                      <i className="ni ni-single-02" />
                      <Link to={`/admin/profile`}><span>My profile</span></Link>
                    </DropdownItem>
                    <DropdownItem className={isProvider ? '' : 'd-none'} onClick={e => e.preventDefault()}>
                      <i className="fas fa-money-bill" />
                      <Link to={`/provider/billing`}><span>Billing</span></Link></DropdownItem>
                    {/* <DropdownItem
                      href="#pablo"
                      onClick={e => e.preventDefault()}
                    >
                      <i className="ni ni-settings-gear-65" />
                      <span>Settings</span>
                    </DropdownItem>
                    <DropdownItem
                      href="#pablo"
                      onClick={e => e.preventDefault()}
                    >
                      <i className="ni ni-calendar-grid-58" />
                      <span>Activity</span>
                    </DropdownItem>
                    <DropdownItem
                      href="#pablo"
                      onClick={e => e.preventDefault()}
                    >
                      <i className="ni ni-support-16" />
                      <span>Support</span>
                    </DropdownItem> */}
                    <DropdownItem className={isClient ? '' : 'd-none'} onClick={e => e.preventDefault()}>
                      <i className="ni ni-badge" />
                      <Link to={`/client/detail`}><span>My profile</span></Link>
                    </DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem href="#pablo" onClick={this.Logout}>
                      <i className="ni ni-user-run" />
                      <span>Logout</span>
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </Nav>
            </Collapse>
          </Container>
        </Navbar>
        <EnterPaymentMethodModal data={{ fullName: fullName }} />
      </>
    );
  }
}
AdminNavbar.defaultProps = {
  toggleSidenav: () => { },
  sidenavOpen: false,
  theme: "dark"
};
AdminNavbar.propTypes = {
  toggleSidenav: PropTypes.func,
  sidenavOpen: PropTypes.bool,
  theme: PropTypes.oneOf(["dark", "light"])
};

const mapStateToProps = state => {
  return {
    data: state.navBars.data
  }
}

const mapActionToProps = { logout, getAdminDetail, getProviderDetail, getClientDetail }

export default connect(mapStateToProps, mapActionToProps)(AdminNavbar);
