import { combineReducers } from 'redux';
import commonReducer from './commonReducer';
import authReducer from './authReducer';
import postReducer from './postReducer';
import venuesReducer from './venuesReducer';
import providerDashboardReducer from './providerDashboardReducer';
import adminDashboardReducer from './adminDashboardReducer';
import clientReducer from './clientReducer';
import providerReducer from './providerReducer';
import bookingReducer from './bookingReducer';
import exportIBookingReducer from './exportIBookingReducer';
import noteReducer from './noteReducer';
import billingReducer from './billingReducer';
import navBarsReducer from './navBarsReducer';
import invoiceReducer from './invoiceReducer';
import appointmentReducer from './appointmentReducer';
import notificationReducer from './notificationReducer';
import messageReducer from './messageReducer';
import clientInvoiceReducer from './clientInvoiceReducer';
import resetPasswordReducer from './resetPasswordReducer';
import createPasswordReducer from './createPasswordReducer';
import locksReducer from './locksReducer';

export default combineReducers({
  common: commonReducer,
  auth: authReducer,
  post: postReducer,
  venues: venuesReducer,
  providerDashboard: providerDashboardReducer,
  adminDashboard: adminDashboardReducer,
  client: clientReducer,
  provider: providerReducer,
  booking: bookingReducer,
  exportBooking: exportIBookingReducer,
  note: noteReducer,
  billing: billingReducer,
  navBars: navBarsReducer,
  invoice: invoiceReducer,
  appointment: appointmentReducer,
  notification: notificationReducer,
  message: messageReducer,
  clientInvoice: clientInvoiceReducer,
  resetPassword: resetPasswordReducer,
  createPassword: createPasswordReducer,
  locks: locksReducer
});
