import {
    POST_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {}
};

export default function postReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Post List
        case POST_ACTIONS.GET_POST_LIST:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.GET_POST_LIST_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case POST_ACTIONS.GET_POST_LIST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Add Post
        case POST_ACTIONS.ADD_POST:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.ADD_POST_SUCCESS:
            const listPost = state.data.result;
            listPost.unshift(payload);
            state.data.result = listPost;
            return {
                data: { ...state.data, result: listPost },
                loading: false,
            }
        case POST_ACTIONS.ADD_POST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update Post
        case POST_ACTIONS.UPDATE_POST:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.UPDATE_POST_SUCCESS:
            const listPostNew = [...state.data.result];
            let postUpdate = listPostNew.map(item => item.id === payload.id ? payload : item);
            return {
                data: { ...state.data, result: postUpdate },
                loading: false,
            }
        case POST_ACTIONS.UPDATE_POST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Delete Post
        case POST_ACTIONS.DELETE_POST:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.DELETE_POST_SUCCESS:
            const newPostList = state.data.result.filter(item => item.id !== payload.postId);
            return {
                data: { ...state.data, result: newPostList },
                loading: false,
            }
        case POST_ACTIONS.DELETE_POST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Add Comment Post
        case POST_ACTIONS.ADD_POST_COMMENT:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.ADD_POST_COMMENT_SUCCESS:
            const newResult = [...state.data.result];
            const post = newResult.filter(item => item.id === payload.post);
            const newCommentList = post[0].comments;
            newCommentList.push(payload);
            return {
                data: { ...state.data, result: newResult },
                loading: false,
            }
        case POST_ACTIONS.ADD_POST_COMMENT_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Update Comment
        case POST_ACTIONS.UPDATE_POST_COMMENT:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.UPDATE_POST_COMMENT_SUCCESS:
            const postsListUpdate = [...state.data.result];
            const postsUpdate = postsListUpdate.find(item => item.id === payload.post);
            const commentList = postsUpdate.comments;
            postsUpdate.comments = commentList.map(item => item.id === payload.id ? payload : item);
            return {
                data: { ...state.data, result: postsListUpdate },
                loading: false,
            }
        case POST_ACTIONS.UPDATE_POST_COMMENT_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Delete Comment
        case POST_ACTIONS.DELETE_POST_COMMENT:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.DELETE_POST_COMMENT_SUCCESS:
            const postsListDelete = [...state.data.result];
            const postDelete = postsListDelete.find(item => item.id === payload.postId);
            const commentListDelete = postDelete.comments;
            postDelete.comments = commentListDelete.filter(item => item.id !== payload.commentId);
            return {
                data: { ...state.data, result: postsListDelete },
                loading: false,
            }
        case POST_ACTIONS.DELETE_POST_COMMENT_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Load More
        case POST_ACTIONS.LOAD_MORE_POST:
            return {
                ...state,
                loading: true,
            }
        case POST_ACTIONS.LOAD_MORE_POST_SUCCESS:
            const newListLoad = [...state.data.result]
            payload.result.map(item => newListLoad.push(item))
            return {
                ...state,
                data: { ...state.data, result: newListLoad },
                loading: false,
            }
        case POST_ACTIONS.LOAD_MORE_POST_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
