import client from "../helpers/HttpClient";
import { urlQueryFromObject } from "../helpers/UtilsHelper";
const endpoint = "/bookings";

export default {
  async create(data) {
    let response = await client.post(endpoint, data);
    return response.data;
  },
  async detail(id) {
    const path = `${endpoint}/${id}`;
    let response = await client.get(path);
    return response.data;
  },
  async update(payload, partial = true) {
    const { bookingId, data } = payload;
    const path = `${endpoint}/${bookingId}`;
    let response = partial
      ? await client.patch(path, data)
      : await client.put(path, data);
    return response.data;
  },
  async delete(id) {
    const path = `${endpoint}/${id}`;
    let response = await client.delete(path);
    return response.data;
  },
  async list(filters) {
    const query = urlQueryFromObject(filters);
    let path = `${endpoint}${query}`;
    let response = await client.get(path);
    return response.data;
  },
  async globalList(filters) {
    const query = urlQueryFromObject(filters);
    let path = `${endpoint}-spaces${query}`;
    let response = await client.get(path);
    return response.data;
  },
  async addAppointment(data) {
    const { bookingId } = data;
    let path = `${endpoint}/${bookingId}/appointments`;
    let response = await client.post(path, data);
    return response.data;
  },
  async editAppointment(data) {
    const { bookingId, id } = data;
    let path = `${endpoint}/${bookingId}/appointments/${id}`;
    let response = await client.patch(path, data);
    return response.data;
  },
  async deleteAppointment(bookingId, appointmentId) {
    let path = `${endpoint}/${bookingId}/appointments/${appointmentId}`;
    let response = await client.delete(path);
    return response.data;
  },
  async getMinTierByBookingId(bookingId) {
    let path = `${endpoint}/${bookingId}/min-tier`;
    let response = await client.get(path);
    return response.data;
  },
};
