import {
    MESSAGE_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {}
};

export default function messageReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Message List
        case MESSAGE_ACTIONS.GET_PROVIDER_MESSAGE_LIST:
            return {
                ...state,
                loading: true,
            }
        case MESSAGE_ACTIONS.GET_PROVIDER_MESSAGE_LIST_SUCCESS:
            const reverseData = payload.result.reverse()
            return {
                ...state,
                data: { result: reverseData },
                loading: false,
            }
        case MESSAGE_ACTIONS.GET_PROVIDER_MESSAGE_LIST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Add Message
        case MESSAGE_ACTIONS.CLIENT_SEND_MESSAGE:
            return {
                ...state,
                loading: true,
            }
        case MESSAGE_ACTIONS.CLIENT_SEND_MESSAGE_SUCCESS:
            const newList = [...state.data.result]
            newList.push(payload)
            return {
                ...state,
                data: { result: newList },
                loading: false,
            }
        case MESSAGE_ACTIONS.CLIENT_SEND_MESSAGE_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
