import { CLIENT_ACTIONS } from "../actions";

const initState = {
  loading: false,
  loadingInvoices: false,
  data: {},
  list: {},
  invoiceData: {},
};

export default function clientReducer(state = initState, action) {
  let { type, payload } = action;
  switch (type) {
    // Get Client List
    case CLIENT_ACTIONS.GET_CLIENT_LIST:
      return {
        ...state,
        loading: true,
      };
    case CLIENT_ACTIONS.GET_CLIENT_LIST_SUCCESS:
      return {
        ...state,
        list: payload,
        loading: false,
      };
    case CLIENT_ACTIONS.GET_CLIENT_LIST_FAIL:
      return {
        ...state,
        loading: false,
      };

    // Get Client Detail
    case CLIENT_ACTIONS.GET_CLIENT_DETAIL:
      return {
        ...state,
        loading: true,
      };
    case CLIENT_ACTIONS.GET_CLIENT_DETAIL_SUCCESS:
      return {
        ...state,
        data: payload,
        loading: false,
      };
    case CLIENT_ACTIONS.GET_CLIENT_DETAIL_FAIL:
      return {
        ...state,
        loading: false,
      };

    // Get Client Invoice List
    case CLIENT_ACTIONS.GET_CLIENT_INVOICES:
      return {
        ...state,
        loadingInvoices: true,
      };
    case CLIENT_ACTIONS.GET_CLIENT_INVOICES_SUCCESS:
      return {
        ...state,
        invoiceData: payload,
        loadingInvoices: false,
      };
    case CLIENT_ACTIONS.GET_CLIENT_INVOICES_FAIL:
      return {
        ...state,
        loadingInvoices: false,
      };

    default:
      return state;
  }
}
