import { call, put, takeLatest } from "redux-saga/effects";
import { APPOINTMENT_ACTIONS } from "../actions";
import appointmentApi from '../../api/appointmentApi';

function* getListAppointment({ payload }) {
    const { filters } = payload;
    try {
        const data = yield call(_getListAppointment.bind(this, filters))
        yield put({ type: APPOINTMENT_ACTIONS.GET_CLIENT_APPOINTMENT_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: APPOINTMENT_ACTIONS.GET_CLIENT_APPOINTMENT_FAIL })
    }
}

function _getListAppointment(filters) {
    return appointmentApi.list(filters).then(response => {
        return response
    })
}

export function* watchAppointmentAsync() {
    yield takeLatest(APPOINTMENT_ACTIONS.GET_CLIENT_APPOINTMENT, getListAppointment);

}
