const locksActions = {
    GET_LOCKS: "GET_LOCKS",
    GET_LOCKS_SUCCESS: "GET_LOCKS_SUCCESS",
    GET_LOCKS_FAIL: "GET_LOCKS_FAIL",
    
    ADD_LOCK: "ADD_LOCK",
    ADD_LOCK_SUCCESS: "ADD_LOCK_SUCCESS",
    ADD_LOCK_FAIL: "ADD_LOCK_FAIL",

    UPDATE_LOCK: "UPDATE_LOCK",
    UPDATE_LOCK_SUCCESS: "UPDATE_LOCK_SUCCESS",
    UPDATE_LOCK_FAIL: "UPDATE_LOCK_FAIL",

    DELETE_LOCK: "DELETE_LOCK",
    DELETE_LOCK_SUCCESS: "DELETE_LOCK_SUCCESS",
    DELETE_LOCK_FAIL: "DELETE_LOCK_FAIL",

    TEST_LOCK: "TEST_LOCK",
    TEST_LOCK_SUCCESS: "TEST_LOCK_SUCCESS",
    TEST_LOCK_FAIL: "TEST_LOCK_FAIL",

    UNLOCK_LOCK: "UNLOCK_LOCK",
    UNLOCK_LOCK_SUCCESS: "UNLOCK_LOCK_SUCCESS",
    UNLOCK_LOCK_FAIL: "UNLOCK_LOCK_FAIL"
}

export default locksActions

export const getLocks = (venueId, filters) => ({ type: locksActions.GET_LOCKS, payload: { venueId, filters } })
export const addLock = (data) => ({ type: locksActions.ADD_LOCK, payload: { data } })
export const updateLock = (data) => ({ type: locksActions.UPDATE_LOCK, payload: { data } })
export const deleteLock = (data) => ({ type: locksActions.DELETE_LOCK, payload: { data } })
export const testLock = (venueId, token) => ({ type: locksActions.TEST_LOCK, payload: { venueId, token } })
export const unlock = (venueId, lockId) => ({ type: locksActions.UNLOCK_LOCK, payload: { venueId, lockId } })

