import { call, put, takeLatest, select } from "redux-saga/effects";
import { toast } from 'react-toastify';
import { getDate } from '../../helpers/UtilsHelper';
import { ADMIN_DASHBOARD_ACTIONS as actions } from '../actions';
import venueApi from '../../api/venueApi';
import bookingApi from '../../api/bookingApi';
import bookingConstants from '../../constants/booking'
import constant from '../../constants/index'

function* requestInit() {
    try {
        const venues = yield call(_getVenues.bind(this));
        if (!venues || venues.length < 0) {
            return;
        }

        const spaces = yield call(_getSpaces.bind(this, venues[0].id));
        if (!spaces || spaces.length < 0) {
            yield put({ type: actions.SET_VENUES, payload: { venues } });
            return;
        }

        const state = yield select(state => state.adminDashboard);
        const { bookings } = state;
        yield put({ type: actions.INIT_DATA_SUCCESS, payload: { venues, spaces, bookings } });
    } catch (error) {
        yield put({ type: actions.INIT_DATA_FAIL, payload: { error } });
    }
}

async function _getVenues(filters) {
    const response = await venueApi.list(filters);
    return response.result;
}

async function _getSpaces(venueId) {
    const response = await venueApi.spaces(venueId);
    return response.result;
}

async function _getBookings(state, venueId="", selectedSpaces=[]) {
    if (venueId === constant.DEFAULT_VENUE.ID ) {
        return [];
    }

    const { activeStart, activeEnd } = state;
    let filters = {
        start_date: getDate(activeStart),
        end_date: getDate(activeEnd),
        limit: 1000000,
    }

    if (venueId) {
        filters.venue = venueId
    }
    if (selectedSpaces && selectedSpaces.length > 0) {
        filters.space = selectedSpaces[0]
    }

    const response = await bookingApi.list(filters);
    return response.result.filter(booking => booking.status !== bookingConstants.BOOKING_STATUS.CANCELED);
}

function* showDetailBooking({ payload }) {
    const bookingId = payload.event.id;
    const tier = yield call(_getTier.bind(this, bookingId));
    yield put({ type: actions.SHOW_DETAIL_BOOKING_SUCCESS, payload: { tier } })
}

async function _getTier(bookingId) {
    const response = await bookingApi.getMinTierByBookingId(bookingId);
    return response.tier;
}

function* cancelBooking({ payload }) {
    try {
        yield call(_cancelBooking.bind(this, payload))
        yield put({ type: actions.CANCEL_BOOKING_BY_ADMIN_SUCCESS, payload: payload })
        toast.success("Cancel Booking success !");

    } catch (error) {
        yield put({ type: actions.CANCEL_BOOKING_BY_ADMIN_FAIL })
        toast.error("Cancel Booking fail !");
    }
}

function _cancelBooking(payload) {
    return bookingApi.update(payload).then(response => {
        return response
    })
}

function* deleteBooking({ payload }) {
    try {
        yield call(_deleteBooking.bind(this, payload.bookingId))
        yield put({ type: actions.DELETE_BOOKING_BY_ADMIN_SUCCESS, payload: payload })
        toast.success("Delete Booking success !");
    } catch (error) {
        yield put({ type: actions.DELETE_BOOKING_BY_ADMIN_FAIL })
        toast.error("Delete Booking fail !");
    }
}

function _deleteBooking(bookingId) {
    return bookingApi.delete(bookingId).then(response => {
        return response
    })
}

function* updateBooking({ payload }) {
    try {
        const state = yield select(state => state.adminDashboard);
        const { selectedSpaces, selectedVenue, calendarViewType } = state;

        yield call(_updateBooking.bind(this, payload));

        const bookings = yield call(_getBookings.bind(this, state, selectedVenue,
            calendarViewType === constant.CALENDAR_VIEW_TYPE.DAY ? null : selectedSpaces));

        yield put({ type: actions.UPDATE_BOOKING_BY_ADMIN_SUCCESS, payload: { bookings } });
        toast.success('Booking space successfully');
    } catch (error) {
        toast.error('Booking space failed');
        yield put({ type: actions.UPDATE_BOOKING_BY_ADMIN_FAIL, payload: { error } })
    }
}

async function _updateBooking(data) {
    const response = await bookingApi.update(data);
    return response;
}

function* changeVenue({ payload }) {
    const { venueId, callbackFn } = payload;
    try {
        const state = yield select(state => state.adminDashboard);
        let bookings = [];
        let spaces = [];
        spaces = yield call(_getSpaces.bind(this, venueId));
        bookings = yield call(_getBookings.bind(this, state, venueId, null));

        yield put({ type: actions.CHANGE_VENUE_SUCCESS, payload: { spaces, venueId, bookings } });
        callbackFn(spaces, state.calendar);
    } catch (error) {
        yield put({ type: actions.INIT_DATA_FAIL, payload: { error } });
    }
}

function* changeSpaces({ payload }) {
    try {
        const { spaceIds } = payload;
        let bookings = [];
        const state = yield select(state => state.adminDashboard);
        const { selectedVenue } = state;

        bookings = yield call(_getBookings.bind(this, state, selectedVenue, spaceIds));

        yield put({ type: actions.CHANGE_SPACES_SUCCESS, payload: { spaceIds, bookings } });
    } catch (error) {
        toast.error('Have error when load data!');
        yield put({ type: actions.CHANGE_SPACES_FAIL, payload: { error } })
    }
}

function* onCalendarChange() {
    let bookings = [];
    const state = yield select(state => state.adminDashboard);
    const { selectedVenue, calendarViewType, selectedSpaces } = state;
    bookings = yield call(_getBookings.bind(this, state, selectedVenue,
        calendarViewType === constant.CALENDAR_VIEW_TYPE.DAY ? null : selectedSpaces));

    yield put({ type: actions.ON_CALENDAR_CHANGE_SUCCESS, payload: { selectedSpaces, selectedVenue, bookings } });
}

export function* watchAdminDashboardAsync() {
    yield takeLatest(actions.INIT_DATA, requestInit);
    yield takeLatest(actions.CHANGE_VENUE, changeVenue);
    yield takeLatest(actions.CANCEL_BOOKING_BY_ADMIN, cancelBooking);
    yield takeLatest(actions.DELETE_BOOKING_BY_ADMIN, deleteBooking);
    yield takeLatest(actions.UPDATE_BOOKING_BY_ADMIN, updateBooking);
    yield takeLatest(actions.CHANGE_SPACES, changeSpaces);
    yield takeLatest(actions.ON_CALENDAR_CHANGE, onCalendarChange);
    yield takeLatest(actions.SHOW_DETAIL_BOOKING, showDetailBooking);
}
