import client from '../helpers/HttpClient'
import { urlQueryFromObject } from '../helpers/UtilsHelper';
const endpoint = '/posts'
export default {
    async list(payload) {
        const { filters } = payload
        const query = urlQueryFromObject(filters);
        const path = `${endpoint}${query}`;
        let response = await client.get(path)
        return response.data;
    },
    async addPost(payload) {
        const path = `${endpoint}`;
        let formData = new FormData();
        Object.keys(payload).forEach(key => {
            if (key === "files") {
                payload[key].forEach((item) => {
                    formData.append("files", item);
                });
            } else {
                formData.append([key], payload[key]);
            }
        });
        let response = await client.post(`${path}`, formData);
        return response.data;
    },
    async updatePost(payload) {
        const { postId, content } = payload;
        const path = `${endpoint}/${postId}`;
        let response = await client.patch(`${path}`, { content });
        return response.data;
    },
    async deletePost(payload) {
        const { postId } = payload;
        const path = `${endpoint}/${postId}`;
        let response = await client.delete(`${path}`);
        return response.data;
    },

    async addComment(payload) {
        const { postId, comment } = payload;
        const path = `${endpoint}/${postId}/comments`;
        let response = await client.post(`${path}`, { comment });
        return response.data;
    },
    async updateComment(payload) {
        const { postId, commentId, comment } = payload;
        const path = `${endpoint}/${postId}/comments/${commentId}`;
        let response = await client.patch(`${path}`, { comment });
        return response.data;
    },
    async deleteComment(payload) {
        const { postId, commentId } = payload;
        const path = `${endpoint}/${postId}/comments/${commentId}`;
        let response = await client.delete(`${path}`);
        return response.data;
    },
}