import {
  call,
  put,
  takeLatest,
  select,
  delay,
  throttle,
} from "redux-saga/effects";
import _ from "lodash";
import { toast } from "react-toastify";
import AuthHelper from "../../helpers/AuthHelper";
import { getAvailableSlots } from "../../helpers/BookingHelper";
import { getDate } from "../../helpers/UtilsHelper";
import { PROVIDER_DASHBOARD_ACTIONS as actions } from "../actions";
import bookingConstants from "../../constants/booking";
import venueApi from "../../api/venueApi";
import bookingApi from "../../api/bookingApi";
import clientApi from "../../api/clientApi";
import providerApi from "../../api/providerApi";
import moment from "moment";

function* requestInit() {
  const state = yield select((state) => state.providerDashboard);
  try {
    const [{ venues, spaces }, clients, bookings, globalBookings] = yield call(
      _getInitData.bind(this, state)
    );
    yield put({
      type: actions.INIT_DATA_SUCCESS,
      payload: { venues, spaces, bookings, globalBookings, clients },
    });
    _refreshBookings(state.calendar, bookings);
  } catch (error) {
    yield put({ type: actions.INIT_DATA_FAIL, payload: { error } });
  }
}

async function _getInitData(state) {
  const promises = [
    _getVenuesAndSpaces(),
    _getClients(),
    _getBookings(state),
    _getGlobalBookings(state),
  ];

  return Promise.all(promises);
}

async function _getVenuesAndSpaces() {
  const venues = await _getVenues();
  let spaces = [];
  if (venues.length > 0) {
    spaces = await _getSpaces(venues[0].id);
  }

  return { venues, spaces };
}

async function _getVenues(filters) {
  const response = await venueApi.list(filters);
  return response.result;
}

async function _getSpaces(venueId) {
  const response = await venueApi.spaces(venueId);
  return response.result;
}

async function _getSpacesAndVenue(venueId) {
  const response = await Promise.all([
    venueApi.spaces(venueId),
    venueApi.detail(venueId),
  ]);
  return response;
}

async function _getClients() {
  const response = await clientApi.list();
  return response.result;
}

async function _getBookings(state) {
  const { activeStart, activeEnd } = state;
  const filters = {
    start_date: getDate(activeStart),
    end_date: getDate(activeEnd),
    limit: 1000000,
  };
  const response = await bookingApi.list(filters);
  return response.result.filter(
    (booking) => booking.status !== bookingConstants.BOOKING_STATUS.CANCELED
  );
}

async function _getGlobalBookings(state) {
  const { activeStart, activeEnd } = state;
  const filters = {
    start_date: getDate(activeStart),
    end_date: getDate(activeEnd),
    limit: 1000000,
  };
  const response = await bookingApi.globalList(filters);
  return response.result.map((booking) => {
    return {
      ...booking,
      space: booking.space ? { id: booking.space } : null,
    };
  });
}

function* changeVenue({ payload }) {
  const { venueId } = payload;
  try {
    const state = yield select((state) => state.providerDashboard);
    const spaces = yield call(_getSpaces.bind(this, venueId));

    yield put({
      type: actions.CHANGE_VENUE_SUCCESS,
      payload: { spaces, venueId },
    });
    _removeAllAvailableSlot(state.calendar);
  } catch (error) {
    yield put({ type: actions.INIT_DATA_FAIL, payload: { error } });
  }
}

function* changeVenueForModal({ payload }) {
  const { venueId } = payload;
  try {
    const result = yield call(_getSpacesAndVenue.bind(this, venueId));

    yield put({
      type: actions.CHANGE_VENUE_FOR_MODAL_SUCCESS,
      payload: { spaces: result[0].result, venue: result[1], venueId },
    });
  } catch (error) {
    yield put({ type: actions.INIT_DATA_FAIL, payload: { error } });
  }
}

function* bookSpace({ payload }) {
  const { data, onSuccess } = payload;
  try {
    const newBooking = yield call(_performBook.bind(this, data));
    yield put({ type: actions.BOOK_SPACE_SUCCESS, payload: { newBooking } });
    yield put({ type: actions.RELOAD_BOOKINGS });
    toast.success("Booking space successfully");
    onSuccess();
  } catch (error) {
    toast.error("Booking space failed");
    let errorData = error.response.data;
    if (error.response.status === 403) {
      errorData = {
        permission: [
          "Your account is pending activation. Please contact support@myethera.com for help in activating your account.",
        ],
      };
    }
    yield put({ type: actions.BOOK_SPACE_FAIL, payload: { error: errorData } });
  }
}

async function _performBook(data) {
  const response = await bookingApi.create(data);
  return response;
}

function* changeSpaces({ payload }) {
  const { spaceIds } = payload;
  yield put({ type: actions.CHANGE_SPACES_SUCCESS, payload: { spaceIds } });
}

function* lookupAvailableSlot() {
  yield delay(100);
  const state = yield select((state) => state.providerDashboard);
  const availableSlots = yield call(getAvailableSlots.bind(this, state));
  yield put({
    type: actions.LOOKUP_AVAILABLE_SLOT_SUCCESS,
    payload: { availableSlots },
  });
  _refreshAvailableSlot(availableSlots, state.calendar);
}

function _refreshAvailableSlot(availableSlots, calendar) {
  _removeAllAvailableSlot(calendar);

  availableSlots.forEach((item, index) => {
    const { start, end } = item;
    let event = {
      id: "available-slo",
      start: start.format(),
      end: end.format(),
      rendering: "background",
      type: "AVAILABLE_SLOT",
      editable: true,
    };
    calendar.addEvent(event);
  });
}

function _removeAllAvailableSlot(calendar) {
  const events = calendar.getEvents();
  events.forEach((e) => {
    if (e.extendedProps.type === "AVAILABLE_SLOT") {
      e.remove();
    }
  });
}

function* onCalendarChange() {
  yield put({ type: actions.RELOAD_BOOKINGS });
}

function* onReloadBooking() {
  const state = yield select((state) => state.providerDashboard);
  const { calendar } = state;
  try {
    const [bookings, globalBookings] = yield call(
      _getAllBookings.bind(this, state)
    );
    yield put({
      type: actions.RELOAD_BOOKINGS_SUCCESS,
      payload: { bookings, globalBookings },
    });
    _refreshBookings(calendar, bookings);
  } catch (error) {
    yield put({ type: actions.RELOAD_BOOKINGS_FAIL, payload: { error } });
  }
}

async function _getAllBookings(state) {
  const promises = [_getBookings(state), _getGlobalBookings(state)];

  return Promise.all(promises);
}

function _refreshBookings(calendar, bookings) {
  const user = AuthHelper.getUserInfo();
  const myBookings = bookings.filter((b) => b.createdBy === user.user_id);
  calendar.removeAllEventSources();
  calendar.addEventSource(myBookings.map(_addBookingToCalendar));
  calendar.render();
}

function _addBookingToCalendar(booking) {
  const spaceName = _.get(booking, "space.name", " Alternate space");
  let event = {
    id: 1,
    start: booking.startDate + "T" + booking.startTime,
    end: booking.startDate + "T" + booking.endTime,
    title: spaceName,
    type: "BOOKING",
    data: booking,
  };
  return event;
}

function* onClearAvailableSlot() {
  // yield put({ type: actions.LOOKUP_AVAILABLE_SLOT });
  const state = yield select((state) => state.providerDashboard);
  _refreshAvailableSlot([], state.calendar);
}

function* afterOpenBookingModal() {
  const tier = yield call(_getTier.bind(this));
  yield put({ type: actions.SET_USER_TIER, payload: { tier } });
}

async function _getTier() {
  const response = await providerApi.tier();
  return response.tier;
}

function* addAppointment({ payload }) {
  const { data, onSuccess } = payload;
  try {
    const newAppointment = yield call(_performAppointment.bind(this, data));
    yield put({
      type: actions.ADD_APPOINTMENT_SUCCESS,
      payload: { newAppointment },
    });
    toast.success("Add appointment successfully");
    const state = yield select((state) => state.providerDashboard);
    yield call(_refreshBookings.bind(this, state.calendar, state.bookings));
    onSuccess && onSuccess(data);
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: actions.ADD_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

function* editAppointment({ payload }) {
  const { data, onSuccess } = payload;
  try {
    const editedAppointment = yield call(
      _performEditAppointment.bind(this, data)
    );
    yield put({
      type: actions.EDIT_APPOINTMENT_SUCCESS,
      payload: { editedAppointment },
    });
    toast.success("Edited appointment successfully");
    const state = yield select((state) => state.providerDashboard);
    yield call(_refreshBookings.bind(this, state.calendar, state.bookings));
    onSuccess && onSuccess(data);
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: actions.EDIT_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

async function _performAppointment(data) {
  const response = await bookingApi.addAppointment(data);
  return response;
}

async function _performEditAppointment(data) {
  const response = await bookingApi.editAppointment(data);
  return response;
}

function* deleteAppointment({ payload }) {
  const { appointment, onSuccess } = payload;
  const state = yield select((state) => state.providerDashboard);
  const { selectedBookingId } = state;
  try {
    yield call(
      _performDeleteAppointment.bind(this, appointment.id, selectedBookingId)
    );
    yield put({
      type: actions.DELETE_APPOINTMENT_SUCCESS,
      payload: { appointment },
    });
    toast.success("Delete appointment successfully");
    const state = yield select((state) => state.providerDashboard);
    yield call(_refreshBookings.bind(this, state.calendar, state.bookings));
    onSuccess && onSuccess();
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: actions.DELETE_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

async function _performDeleteAppointment(appointmentId, bookingId) {
  const response = await bookingApi.deleteAppointment(bookingId, appointmentId);
  return response;
}

function* getDayBookings({ payload }) {
  const { date } = payload;

  const requestedDate = yield select(
    (state) => state.providerDashboard.dayBookings.date
  );
  if (!date || moment(date).format("YYYY-MM-DD") === requestedDate) {
    return;
  }

  try {
    const bookings = yield call(_getGlobalBookings, {
      activeStart: date,
      activeEnd: date,
    });
    yield put({
      type: actions.DAY_BOOKINGS_SUCCESS,
      payload: { bookings, date: moment(date).format("YYYY-MM-DD") },
    });
  } catch (error) {
    console.log(error);
    yield put({
      type: actions.DAY_BOOKINGS_FAILED,
      payload: { error: error.response.data },
    });
  }
}

export function* watchProviderDashboardAsync() {
  yield takeLatest(actions.INIT_DATA, requestInit);
  yield takeLatest(actions.CHANGE_VENUE, changeVenue);
  yield takeLatest(actions.CHANGE_VENUE_FOR_MODAL, changeVenueForModal);
  yield takeLatest(actions.CHANGE_SPACES, changeSpaces);
  yield takeLatest(actions.LOOKUP_AVAILABLE_SLOT, lookupAvailableSlot);
  yield takeLatest(actions.ON_CALENDAR_CHANGE, onCalendarChange);
  yield takeLatest(actions.RELOAD_BOOKINGS, onReloadBooking);
  yield takeLatest(actions.CLEAR_LOOKUP_AVAILABLE_SLOT, onClearAvailableSlot);
  yield takeLatest(actions.ADD_NEW_BOOKING, afterOpenBookingModal);
  yield throttle(500, actions.BOOK_SPACE, bookSpace);
  yield throttle(500, actions.ADD_APPOINTMENT, addAppointment);
  yield throttle(500, actions.EDIT_APPOINTMENT, editAppointment);
  yield throttle(500, actions.DELETE_APPOINTMENT, deleteAppointment);

  yield throttle(500, actions.DAY_BOOKINGS, getDayBookings);
}
