import { call, put, takeLatest } from "redux-saga/effects";
import { BOOKING_ACTIONS, PROVIDER_DASHBOARD_ACTIONS } from "../actions";
import bookingApi from "../../api/bookingApi";
import clientApi from "../../api/clientApi";
import appointmentApi from "../../api/appointmentApi";
import providerApi from "../../api/providerApi";
import venueApi from "../../api/venueApi";
import { toast } from "react-toastify";

function* getListExport({ payload }) {
  const { filters, callback } = payload;
  try {
    const data = yield call(_getList.bind(this, filters));
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST_EXPORT_SUCCESS });
    callback(data);
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST_EXPORT_FAIL });
  }
}

function* getList({ payload }) {
  const { filters } = payload;

  try {
    const data = yield call(_getList.bind(this, filters));

    yield put({
      type: BOOKING_ACTIONS.GET_BOOKING_LIST_SUCCESS,
      payload: data,
    });
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST_FAIL });
  }
}

function _getList(filters) {
  return bookingApi.list(filters).then((response) => {
    return response;
  });
}

function* getVenue({ payload }) {
  const { filters } = payload;
  try {
    const venueData = yield call(_getVenue.bind(this, filters));
    yield put({
      type: BOOKING_ACTIONS.GET_VENUE_LIST_SUCCESS,
      payload: venueData,
    });
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.GET_VENUE_LIST_FAIL });
  }
}

function _getVenue(filters) {
  return venueApi.list(filters).then((response) => {
    return response;
  });
}

function* getClients() {
  try {
    const clients = yield call(_getClients.bind(this));
    yield put({
      type: BOOKING_ACTIONS.GET_CLIENT_LIST_SUCCESS,
      payload: { clients },
    });
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.GET_CLIENT_LIST_FAIL });
  }
}

async function _getClients() {
  const response = await clientApi.list();
  return response.result;
}

function* getSpace({ payload }) {
  const { venueId } = payload;
  try {
    const spaceData = yield call(_getSpace.bind(this, venueId));
    yield put({
      type: BOOKING_ACTIONS.GET_SPACE_LIST_SUCCESS,
      payload: spaceData,
    });
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.GET_SPACE_LIST_FAIL });
  }
}

function* getSpaceForModal({ payload }) {
  const { venueId } = payload;
  if (!venueId) {
    return;
  }
  try {
    const spaceData = yield call(_getSpacesAndVenue, venueId);
    yield put({
      type: BOOKING_ACTIONS.GET_SPACE_LIST_FOR_MODAL_SUCCESS,
      payload: spaceData,
    });
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.GET_SPACE_LIST_FOR_MODAL_FAIL });
  }
}

async function _getSpace(venueId) {
  const response = await venueApi.spaces(venueId);
  return response;
}

async function _getSpacesAndVenue(venueId) {
  const response = await Promise.all([
    venueApi.spaces(venueId),
    venueApi.detail(venueId),
  ]);
  return response;
}

function* deleteBooking({ payload }) {
  const { bookingId } = payload;
  try {
    yield call(_deleteBooking.bind(this, bookingId));
    yield put({
      type: BOOKING_ACTIONS.DELETE_BOOKING_SUCCESS,
      payload: bookingId,
    });
    toast.success("Delete Booking success !");
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.DELETE_BOOKING_FAIL });
    toast.error("Delete Booking fail !");
  }
}

function _deleteBooking(bookingId) {
  return bookingApi.delete(bookingId).then((response) => {
    return response;
  });
}

function* cancelBooking({ payload }) {
  try {
    const bookingData = yield call(_cancelBooking.bind(this, payload));
    yield put({
      type: BOOKING_ACTIONS.CANCEL_BOOKING_SUCCESS,
      payload: bookingData,
    });
    toast.success("Cancel Booking success !");
  } catch (error) {
    yield put({ type: BOOKING_ACTIONS.CANCEL_BOOKING_FAIL });
    toast.error("Cancel Booking fail !");
  }
}

function _cancelBooking(payload) {
  return bookingApi.update(payload).then((response) => {
    return response;
  });
}

function* updateAppointment({ payload }) {
  try {
    const appointment = yield call(_updateAppointment.bind(this, payload));
    yield put({
      type: BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT_SUCCESS,
      payload: { appointment },
    });
    toast.success("Update Booking appointment success !");
    payload.callback && payload.callback();
  } catch (error) {
    toast.error("Update Booking appointment fail !");
    let errorData = error.response.data.message;
    yield put({
      type: BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT_FAIL,
      payload: { error: errorData },
    });
  }
}

function _updateAppointment(payload) {
  return appointmentApi.update(payload).then((response) => {
    return response;
  });
}

function* getProviderData({ payload }) {
  const { id } = payload;
  try {
    const data = yield call(_getProviderData.bind(this, id));
    yield put({
      type: BOOKING_ACTIONS.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_SUCCESS,
      payload: data,
    });
  } catch (error) {
    yield put({
      type: BOOKING_ACTIONS.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR_FAIL,
    });
  }
}

function _getProviderData(id) {
  return providerApi.detail(id).then((response) => {
    return response;
  });
}

function* addBooking({ payload }) {
  const { booking, filters } = payload;
  try {
    const newBooking = yield call(_performBook.bind(this, booking));
    yield put({
      type: BOOKING_ACTIONS.ADD_BOOKING_SUCCESS,
      payload: { newBooking },
    });
    toast.success("Booking space successfully");
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.BOOK_SPACE_SUCCESS,
      payload: { newBooking },
    });
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST, payload: { filters } });
  } catch (error) {
    toast.error("Booking space failed");
    let errorData = error.response.data;
    if (error.response.status === 403) {
      errorData = {
        permission: [
          "Your account is pending activation. Please contact support@myethera.com for help in activating your account.",
        ],
      };
    }
    yield put({
      type: BOOKING_ACTIONS.ADD_BOOKING_FAIL,
      payload: { error: errorData },
    });
  }
}

async function _performBook(data) {
  const response = await bookingApi.create(data);
  return response;
}

function* addAppointment({ payload }) {
  const { appointmentData, filters, onSuccess } = payload;
  try {
    const newAppointment = yield call(
      _performAppointment.bind(this, appointmentData)
    );
    yield put({
      type: BOOKING_ACTIONS.ADD_APPOINTMENT_SUCCESS,
      payload: { newAppointment },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.ADD_APPOINTMENT_SUCCESS,
      payload: { newAppointment },
    });
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST, payload: { filters } });
    toast.success("Add appointment successfully");
    onSuccess && onSuccess();
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: BOOKING_ACTIONS.ADD_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.ADD_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

function* editAppointment({ payload }) {
  const { appointmentData, filters, onSuccess } = payload;
  try {
    const editedAppointment = yield call(
      _performEditAppointment.bind(this, appointmentData)
    );
    yield put({
      type: BOOKING_ACTIONS.EDIT_APPOINTMENT_SUCCESS,
      payload: { editedAppointment },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.EDIT_APPOINTMENT_SUCCESS,
      payload: { editedAppointment },
    });
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST, payload: { filters } });
    toast.success("Edited appointment successfully");
    onSuccess && onSuccess();
  } catch (error) {
    toast.error("Request failed");
    yield put({
      type: BOOKING_ACTIONS.EDIT_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.EDIT_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

function* deleteAppointment({ payload }) {
  const { appointmentData, filters, onSuccess } = payload;
  try {
    yield call(
      _performDeleteAppointment.bind(
        this,
        appointmentData.id,
        appointmentData.booking
      )
    );
    yield put({
      type: BOOKING_ACTIONS.DELETE_APPOINTMENT_SUCCESS,
      payload: { appointmentData },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.DELETE_APPOINTMENT_SUCCESS,
      payload: { appointment: appointmentData },
    });
    yield put({ type: BOOKING_ACTIONS.GET_BOOKING_LIST, payload: { filters } });
    toast.success("Delete appointment successfully");
    onSuccess && onSuccess();
  } catch (error) {
    console.log(error);
    toast.error("Request failed");
    yield put({
      type: BOOKING_ACTIONS.DELETE_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
    yield put({
      type: PROVIDER_DASHBOARD_ACTIONS.DELETE_APPOINTMENT_FAIL,
      payload: { error: error.response.data },
    });
  }
}

async function _performAppointment(data) {
  const response = await bookingApi.addAppointment(data);
  return response;
}

async function _performEditAppointment(data) {
  const response = await bookingApi.editAppointment(data);
  return response;
}

async function _performDeleteAppointment(appointmentId, bookingId) {
  const response = await bookingApi.deleteAppointment(bookingId, appointmentId);
  return response;
}

export function* watchBookingAsync() {
  yield takeLatest(BOOKING_ACTIONS.GET_BOOKING_LIST_EXPORT, getListExport);
  yield takeLatest(BOOKING_ACTIONS.GET_BOOKING_LIST, getList);
  yield takeLatest(BOOKING_ACTIONS.GET_VENUE_LIST, getVenue);
  yield takeLatest(BOOKING_ACTIONS.GET_CLIENT_LIST, getClients);
  yield takeLatest(BOOKING_ACTIONS.GET_SPACE_LIST, getSpace);
  yield takeLatest(BOOKING_ACTIONS.GET_SPACE_LIST_FOR_MODAL, getSpaceForModal);
  yield takeLatest(BOOKING_ACTIONS.ADD_BOOKING, addBooking);
  yield takeLatest(BOOKING_ACTIONS.DELETE_BOOKING, deleteBooking);
  yield takeLatest(BOOKING_ACTIONS.CANCEL_BOOKING, cancelBooking);
  yield takeLatest(
    BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT,
    updateAppointment
  );
  yield takeLatest(
    BOOKING_ACTIONS.GET_PROVIDER_DATA_ON_BOOKING_SIDE_BAR,
    getProviderData
  );
  // yield takeLatest(BOOKING_ACTIONS.UPDATE_BOOKING_APPOINTMENT, updateAppointment);
  yield takeLatest(BOOKING_ACTIONS.ADD_APPOINTMENT, addAppointment);
  yield takeLatest(BOOKING_ACTIONS.EDIT_APPOINTMENT, editAppointment);
  yield takeLatest(BOOKING_ACTIONS.DELETE_APPOINTMENT, deleteAppointment);
}
