import React, { Suspense } from "react";
import { connect } from "react-redux";
// react library for routing
import { Route, Switch, Redirect } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
// core components
import AdminNavbar from "components/Navbars/AdminNavbar.jsx";
import AdminFooter from "components/Footers/AdminFooter.jsx";
import Sidebar from "components/Sidebar/Sidebar.jsx";

import routes from "routes.js";

import './Admin.scss'

const loading = () => <div className="animated fadeIn pt-3 text-center">Loading...</div>;

class Admin extends React.Component {
  state = {
    sidenavOpen: true
  };

  componentDidMount(e) {
    // Close sidebar in md size
    if (window.innerWidth < 1200) {
      this.forceCloseSidebar()
    }
  }

  componentDidUpdate(e) {
    if (e.history.pathname !== e.location.pathname) {
      document.documentElement.scrollTop = 0;
      document.scrollingElement.scrollTop = 0;
      this.refs.mainContent.scrollTop = 0;
    }
  }

  getRoutes = routes => {
    return routes.map((route, key) => {
      if (route.collapse) {
        return this.getRoutes(route.views);
      }
      if (route.layout === "/admin") {
        return (
          <Route
            path={route.path}
            component={route.component}
            exact={!!route.exact}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  getBrandText = path => {
    for (let i = 0; i < routes.length; i++) {
      if (
        this.props.location.pathname.indexOf(
          routes[i].layout + routes[i].path
        ) !== -1
      ) {
        return routes[i].name;
      }
    }
    return "Brand";
  };

  forceCloseSidebar = () => {
    if (document.body.classList.contains("g-sidenav-pinned")) {
      document.body.classList.remove("g-sidenav-pinned");
      document.body.classList.add("g-sidenav-hidden");
      document.body.classList.remove("g-sidenav-show");
    }
    this.setState({ sidenavOpen: false });
  }

  // toggles collapse between mini sidenav and normal
  toggleSidenav = () => {
    if (document.body.classList.contains("g-sidenav-pinned")) {
      document.body.classList.remove("g-sidenav-pinned");
      document.body.classList.add("g-sidenav-hidden");
      document.body.classList.remove("g-sidenav-show");
    } else {
      document.body.classList.add("g-sidenav-pinned");
      document.body.classList.remove("g-sidenav-hidden");
    }
    this.setState({
      sidenavOpen: !this.state.sidenavOpen
    });
  };

  getNavbarTheme = () => {
    return this.props.location.pathname.indexOf(
      "admin/alternative-dashboard"
    ) === -1
      ? "dark"
      : "light";
  };

  getMapRouter = (path) => {
    switch (path) {
      case "/admin":
        return routes.admin
      case "/provider":
        return routes.provider
      case "/client":
        return routes.client
      default:
        return routes.admin
    }
  }

  getRidirectRoute = (path) => {
    switch (path) {
      case "/admin":
        return <Redirect from="/admin" to="/admin/dashboard" />
      case "/provider":
        return <Redirect from="/provider" to="/provider/calendar" />
      case "/client":
        return <Redirect from="/client" to="/client/appointments" />
      default:
        return null
    }
  }

  render() {
    const { role, privacy } = this.props;
    const inputRoutes = this.getMapRouter(role);
    return (
      <>
        <ToastContainer autoClose={2000} position={toast.POSITION.TOP_RIGHT} progressClassName="Toastify__progress-bar--rtl" />
        <Sidebar
          {...this.props}
          routes={inputRoutes}
          toggleSidenav={this.toggleSidenav}
          sidenavOpen={this.state.sidenavOpen}
          logo={{
            innerLink: "/",
            imgSrc: require("assets/img/brand/Ethera-Logo.png"),
            imgAlt: "..."
          }}
        />
        <div
          className="main-content"
          ref="mainContent"
          onClick={this.closeSidenav}
          key={privacy} // Trick to reload content when change privacy
        >
          <div className="content-body">
            <AdminNavbar
              {...this.props}
              theme={this.getNavbarTheme()}
              toggleSidenav={this.toggleSidenav}
              sidenavOpen={this.state.sidenavOpen}
              brandText={this.getBrandText(this.props.location.pathname)}
            />
            <Suspense fallback={loading()}>
              <Switch>
                {this.getRoutes(inputRoutes, role)}
                {this.getRidirectRoute(role)}
              </Switch>
            </Suspense>
          </div>
          <AdminFooter />
        </div>
        {this.state.sidenavOpen ? (
          <div className="backdrop d-xl-none" onClick={this.toggleSidenav} />
        ) : null}
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return { privacy: state.common.privacy }
}

export default connect(mapStateToProps, {})(Admin);
