
const actionTypes = {
    GET_NOTE_LIST: "GET_NOTE_LIST",
    GET_NOTE_LIST_SUCCESS: 'GET_NOTE_LIST_SUCCESS',
    GET_NOTE_LIST_FAIL: 'GET_NOTE_LIST_FAIL',

    ADD_NOTE: "ADD_NOTE",
    ADD_NOTE_SUCCESS: 'ADD_NOTE_SUCCESS',
    ADD_NOTE_FAIL: 'ADD_NOTE_FAIL',

    LOAD_MORE_NOTE: "LOAD_MORE_NOTE",
    LOAD_MORE_NOTE_SUCCESS: 'LOAD_MORE_NOTE_SUCCESS',
    LOAD_MORE_NOTE_FAIL: 'LOAD_MORE_NOTE_FAIL',

    DELETE_NOTE: "DELETE_NOTE",
    DELETE_NOTE_SUCCESS: 'DELETE_NOTE_SUCCESS',
    DELETE_NOTE_FAIL: 'DELETE_NOTE_FAIL',

    GET_BOOKING: "note.GET_BOOKING",
    GET_BOOKING_SUCCESS: "note.GET_BOOKING_SUCCESS",
    GET_BOOKING_FAIL: "note.GET_BOOKING_FAIL",

    CLEAR_BOOKING: "note.CLEAR_BOOKING",

    GET_VENUES: "note.GET_VENUES",
    GET_VENUES_SUCCESS: "note.GET_VENUES_SUCCESS",
    GET_VENUES_FAIL: "note.GET_VENUES_FAIL",

    ADD_APPOINTMENT: "note.ADD_APPOINTMENT",
    ADD_APPOINTMENT_SUCCESS: "note.ADD_APPOINTMENT_SUCCESS",
    ADD_APPOINTMENT_FAIL: "note.ADD_APPOINTMENT_FAIL",

    EDIT_APPOINTMENT: "note.EDIT_APPOINTMENT",
    EDIT_APPOINTMENT_SUCCESS: "note.EDIT_APPOINTMENT_SUCCESS",
    EDIT_APPOINTMENT_FAIL: "note.EDIT_APPOINTMENT_FAIL",

    DELETE_APPOINTMENT: "note.DELETE_APPOINTMENT",
    DELETE_APPOINTMENT_SUCCESS: "note.DELETE_APPOINTMENT_SUCCESS",
    DELETE_APPOINTMENT_FAIL: "note.DELETE_APPOINTMENT_FAIL",
}

export default actionTypes

export const getNoteList = (clientId, filters) => ({ type: actionTypes.GET_NOTE_LIST, payload: { clientId, filters } })
export const addNote = (clientId, data) => ({ type: actionTypes.ADD_NOTE, payload: { clientId, data } })
export const loadMore = (clientId, filters) => ({ type: actionTypes.LOAD_MORE_NOTE, payload: { clientId, filters } })
export const deleteNote = (clientId, noteId) => ({ type: actionTypes.DELETE_NOTE, payload: { clientId, noteId } })
export const getBookingFromAppointment = appointment => ({type: actionTypes.GET_BOOKING, payload: {appointment}});
export const clearBooking = () => ({type: actionTypes.CLEAR_BOOKING});
export const getVenues = () => ({type: actionTypes.GET_VENUES});
export const getClients = () => ({type: actionTypes.GET_CLIENTS});

export const addAppointment = (appointmentData, callback) => ({type: actionTypes.ADD_APPOINTMENT, payload: {appointmentData, onSuccess: callback}});
export const editAppointment = (appointmentData, callback) => ({type: actionTypes.EDIT_APPOINTMENT, payload: {appointmentData, onSuccess: callback}});
export const deleteAppointment = (appointmentData, callback) => ({type: actionTypes.DELETE_APPOINTMENT, payload: {appointmentData, onSuccess: callback}});