import client from '../helpers/HttpClient'
const endpoint = '/admins'
export default {
    async getListAdmin() {
        let response = await client.get(endpoint)
        return response.data;
    },
    async getAdminDetail(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.get(`${path}`)
        return response.data;
    },
    async updateAdminDetail(payload) {
        const { id } = payload;
        const path = `${endpoint}/${id}`;
        let formData = new FormData();
        Object.keys(payload).forEach(key => {
            formData.append([key], payload[key]);
        });
        let response = await client.patch(`${path}`, formData);
        return response.data;
    },
    async addAdminAccount(payload) {
        let formData = new FormData();
        Object.keys(payload).forEach(key => {
            formData.append([key], payload[key]);
        });
        let response = await client.post(endpoint, formData);
        return response.data;
    },
    async deleteAdminAccount(id) {
        const path = `${endpoint}/${id}`;
        let response = await client.delete(`${path}`);
        return response.data;
    },
}