import { call, put, takeLatest } from "redux-saga/effects";
import { MESSAGE_ACTIONS } from "../actions";
import messageApi from '../../api/messageApi';

function* getProviderMessage({ payload }) {
    const { filters, callback } = payload;
    try {
        const data = yield call(_getProviderMessage.bind(this, filters))
        yield put({ type: MESSAGE_ACTIONS.GET_PROVIDER_MESSAGE_LIST_SUCCESS, payload: data })
        callback && callback()
    } catch (error) {
        yield put({ type: MESSAGE_ACTIONS.GET_PROVIDER_MESSAGE_LIST_FAIL })
    }
}

function _getProviderMessage(filters) {
    return messageApi.list(filters).then(response => {
        return response
    })
}

function* clientSendMessage({ payload }) {
    try {
        const data = yield call(_clientSendMessage.bind(this, payload))
        yield put({ type: MESSAGE_ACTIONS.CLIENT_SEND_MESSAGE_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: MESSAGE_ACTIONS.CLIENT_SEND_MESSAGE_FAIL })
    }
}

function _clientSendMessage(payload) {
    return messageApi.create(payload).then(response => {
        return response
    })
}

export function* watchMessageAsync() {
    yield takeLatest(MESSAGE_ACTIONS.GET_PROVIDER_MESSAGE_LIST, getProviderMessage);
    yield takeLatest(MESSAGE_ACTIONS.CLIENT_SEND_MESSAGE, clientSendMessage);
}
