import React from "react";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// reactstrap components
import { Container, Row, Col } from "reactstrap";
import logo from '../../assets/img/brand/Ethera-Logo.png';

class AuthHeader extends React.Component {
  render() {
    return (
      <>
        <div className="header py-7 py-lg-5 pt-lg-3">
          <Container>
            <div className="header-body text-center mb-7">
              <Row className="justify-content-center">
                <Col className="text-content" sm={12}>
                  <div className="logo" style={{borderBottom: "1px solid #808080", paddingBottom: 35 , marginBottom: 25}}>
                    <img src={logo} alt="logo" style={{background: "#fff"}}/>
                  </div>
                  {this.props.title ? (
                    <p className="auth-header-title">{this.props.title}</p>
                  ) : null}
                  {this.props.lead ? (
                    <h1 className="text-lead auth-header-lead display-3" style={{fontWeight: "normal", fontFamily: "'Century Gothic', 'Open Sans', sans-serif"}}>{this.props.lead}</h1>
                  ) : null}
                  {this.props.subtitle ? (
                  <p className="auth-header-subtitle">{this.props.subtitle}</p>
                  ) : null}
                </Col>
              </Row>
            </div>
          </Container>
          {/* <div className="separator separator-bottom separator-skew zindex-100">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              preserveAspectRatio="none"
              version="1.1"
              viewBox="0 0 2560 100"
              x="0"
              y="0"
            >
              <polygon
                className="fill-default"
                points="2560 0 2560 100 0 100"
              />
            </svg>
          </div> */}
        </div>
      </>
    );
  }
}

AuthHeader.propTypes = {
  title: PropTypes.string,
  lead: PropTypes.string,
  subtitle: PropTypes.string,
  xl: PropTypes.number,
  lg: PropTypes.number
};

export default AuthHeader;
