import {
    NOTE_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {},
    bookings: {},
    venues: null
};

export default function noteReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {

        // Get Note List
        case NOTE_ACTIONS.GET_NOTE_LIST:
            return {
                ...state,
                loading: true,
            }
        case NOTE_ACTIONS.GET_NOTE_LIST_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case NOTE_ACTIONS.GET_NOTE_LIST_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Add Note
        case NOTE_ACTIONS.ADD_NOTE:
            return {
                ...state,
                loading: true,
            }
        case NOTE_ACTIONS.ADD_NOTE_SUCCESS:
            const newList = [...state.data.result]
            newList.unshift(payload)
            return {
                ...state,
                data: { result: newList },
                loading: false,
            }
        case NOTE_ACTIONS.ADD_NOTE_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Load More
        case NOTE_ACTIONS.LOAD_MORE_NOTE:
            return {
                ...state,
                loading: true,
            }
        case NOTE_ACTIONS.LOAD_MORE_NOTE_SUCCESS:
            const newListLoad = [...state.data.result]
            payload.result.map(item => newListLoad.push(item))
            return {
                ...state,
                data: { ...state.data, result: newListLoad },
                loading: false,
            }
        case NOTE_ACTIONS.LOAD_MORE_NOTE_FAIL:
            return {
                ...state,
                loading: false,
            }

        // Delete Note
        case NOTE_ACTIONS.DELETE_NOTE:
            return {
                ...state,
                loading: true,
            }
        case NOTE_ACTIONS.DELETE_NOTE_SUCCESS:
            const tempList = state.data.result.filter(item => item.id !== payload.noteId);
            return {
                ...state,
                data: { ...state.data, result: tempList },
                loading: false,
            }
        case NOTE_ACTIONS.DELETE_NOTE_FAIL:
            return {
                ...state,
                loading: false,
            }

        case NOTE_ACTIONS.GET_BOOKING_SUCCESS:
            const {appointment, booking} = action.payload;
            const bk = JSON.parse(JSON.stringify(state.bookings));
            bk[appointment.id] = booking;
            return {
                ...state,
                bookings: bk
            }

        case NOTE_ACTIONS.CLEAR_BOOKING:
            return {
                ...state,
                bookings: {}
            }

        case NOTE_ACTIONS.GET_VENUES_SUCCESS:
            return {
                ...state,
                venues: action.payload.venues.result.map(venue => ({id: venue.id, name: venue.name}))
            }

        default:
            return state;
    }
}
