import React, { useEffect } from "react";

// react library for routing
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";
import jwtDecode from "jwt-decode";
import "./assets/css/fonts.css";

// Pages
import AdminLayout from "layouts/Admin.jsx";
import AuthLayout from "layouts/Auth.jsx";
import StripeNotification from "pages/others/StripeNotification.jsx";
import ResetPassword from "pages/shared/ResetPassword/ResetPassword.jsx";
import CreateNewPassword from "pages/shared/CreateNewPassword/CreateNewPassword.jsx";
import { useSelector } from "react-redux";

const App = () => {
  const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);
  const token = useSelector((state) => state.auth.token);

  let getRolePath = () => {
    const tokenParser = token && jwtDecode(token);
    if (tokenParser) {
      const { is_staff, is_client, is_provider } = tokenParser;
      if (is_staff) return "/admin";
      if (is_client) return "/client";
      if (is_provider) return "/provider";
    }
    return "/provider";
  };

  // useEffect(() => {
  //   if (!isLatestVersion) {
  //     emptyCacheStorage();
  //   }
  // }, []);

  return (
    <>
      <HashRouter>
        {isAuthenticated ? (
          <Switch>
            <Route
              path="/stripe-connect-result"
              render={(props) => <StripeNotification {...props} />}
            />
            <Route
              path={getRolePath()}
              render={(props) => (
                <AdminLayout {...props} role={getRolePath()} />
              )}
            />
            <Redirect from="/" to={getRolePath()} />
          </Switch>
        ) : (
          <Switch>
            <Route
              path="/reset-password"
              render={(props) => <ResetPassword {...props} />}
            />
            <Route
              path="/create-new-password"
              render={(props) => <CreateNewPassword {...props} />}
            />
            <Route
              path="/stripe-connect-result"
              render={(props) => <StripeNotification {...props} />}
            />
            <Route
              path="/admin"
              render={(props) => <AuthLayout {...props} />}
            />
            <Route
              path="/provider"
              render={(props) => <AuthLayout {...props} />}
            />
            <Route
              path="/client"
              render={(props) => <AuthLayout {...props} />}
            />
            <Redirect from="/" to={getRolePath()} />
          </Switch>
        )}
      </HashRouter>
    </>
  );
};

export default App;
