import moment from "moment";
import _ from "lodash";
import cons from "../constants";

export const getMomentObject = (data, format = "YYYY-MM-DD HH:mm:ss") =>
  data && moment(data, format);
export const getDate = (data, format = "YYYY-MM-DD") =>
  data && moment(data).format(format);
export const getDateFormat = (data, format = "MM/DD/YYYY") =>
  data && moment(data).format(format);
export const getTime = (data, format = "LT") =>
  data && moment(data).format(format);
export const getTimeFull = (data, format = "HH:mm:ss") =>
  data && moment(data).format(format);
export const getDateTime = (data, format = "MM/DD/YYYY , hh:mm a") =>
  data && moment(data).format(format);
export const getYearOld = (data, format) =>
  data && moment().diff(moment(data), "year");
export const getDayInCurrentWeek = (day, format = "YYYY-MM-DD") =>
  moment().weekday(day).format(format);
export const getStartOfMonth = (format = "YYYY-MM-DD") =>
  moment().startOf("month").format(format);
export const getEndOfMonth = (format = "YYYY-MM-DD") =>
  moment().endOf("month").format(format);

export const uppercaseFirstLetter = (data) =>
  data.charAt(0).toUpperCase() + data.slice(1);

const toCamel = (s) => {
  return s.replace(/([-_][a-z])/gi, ($1) => {
    return $1.toUpperCase().replace("-", "").replace("_", "");
  });
};

const isArray = function (a) {
  return Array.isArray(a);
};

const isObject = function (o) {
  return o === Object(o) && !isArray(o) && typeof o !== "function";
};

const getSpaceName = (space, venueData) => {
  if (!space || !space.venue || venueData.length === 0) {
    return "Alternate space";
  } else {
    const venue = venueData.find((v) => v.id === space.venue);
    return `${venue.name} | ${space.name}`;
  }
};

const getClientAppointments = (appointments, startDate) => {
  let apm_result =
    appointments && appointments.length > 0
      ? appointments.map((elm, i) => {
          let startTime = getTime(`${startDate} ${elm.startTime}`);
          let endTime = getTime(`${startDate} ${elm.endTime}`);
          return `${getUserFullName(elm.client)}(${startTime}-${endTime})`;
        })
      : "Other";
  return apm_result;
};

export const keysToCamel = function (o) {
  if (isObject(o)) {
    const n = {};

    Object.keys(o).forEach((k) => {
      n[toCamel(k)] = keysToCamel(o[k]);
    });

    return n;
  } else if (isArray(o)) {
    return o.map((i) => {
      return keysToCamel(i);
    });
  }

  return o;
};

export const isGTENowDay = (current) => {
  var yesterday = moment().subtract(1, "day");
  return current.isAfter(yesterday);
};

export const convertToDecimal = (data, length) => {
  const weight = length ? 10 * length : 1;
  return Math.round(data * weight) / weight;
};

export const getUserFullName = (user, defaultName = "Other") => {
  if (!user) {
    return defaultName;
  }
  const preferredName = user.preferredName ? `"${user.preferredName}"` : "";
  let fullName = `${preferredName} ${user.firstName || ""} ${
    user.middleName || ""
  } ${user.lastName || ""}`
    .trim()
    .replace(/ +(?= )/g, "");
  const fullNameRs = _.isEmpty(fullName) ? defaultName : fullName;

  return fullNameRs;
};

export const getFirstWordOfName = (user, defaultName = "OT") => {
  if (!user) {
    return defaultName;
  }
  let fullName = `${user.firstName.slice(0, 1) || ""}${
    user.lastName.slice(0, 1) || ""
  }`
    .trim()
    .replace(/ +(?= )/g, "");
  return _.isEmpty(fullName) ? defaultName : fullName;
};

export const getModalUserFullName = (user, defaultName = "Other") => {
  if (!user) {
    return defaultName;
  }
  const preferred_name = user.preferred_name ? `"${user.preferred_name}"` : "";
  let fullName = `${preferred_name} ${user.first_name || ""} ${
    user.middle_name || ""
  } ${user.last_name || ""}`
    .trim()
    .replace(/ +(?= )/g, "");
  return _.isEmpty(fullName) ? defaultName : fullName;
};

export const urlQueryFromObject = (filters) => {
  if (!_.isObject || _.isNull(filters)) {
    return "";
  }

  for (let key in filters) {
    const value = String(filters[key]);

    if (_.isEmpty(value) || _.isNull(value)) {
      delete filters[key];
    }
  }

  filters = { limit: 100000, offset: 0, ...filters };

  if (!_.isUndefined(filters.desc) && !filters.desc) {
    filters.ordering = `-${filters.ordering}`.replace(/,/g, ",-");
  }

  const searchParams = new URLSearchParams(filters);
  searchParams.delete("desc");
  return "?" + searchParams.toString();
};

export const getPortalType = () => {
  const hash = window.location.hash;
  if (hash.startsWith("#/admin")) return "admin";

  if (hash.startsWith("#/provider")) return "provider";

  if (hash.startsWith("#/client")) return "client";
};

export const getDurationTime = (startTime, endTime, unit = "hours") => {
  const duration = moment.duration(endTime.diff(startTime));
  switch (unit) {
    case "days":
      return duration.asDays();
    case "hours":
      return duration.asHours();
    case "minutes":
      return duration.asMinutes();
    default:
      return duration.asMilliseconds();
  }
};

export const getTotalHoursOfBookings = (bookings) => {
  let result = [];
  bookings.forEach((item) => {
    let changed = false;
    result.forEach((day, i) => {
      if (day.startDate === item.startDate) {
        changed = true;
        result[i] = {
          id: item.id,
          startDate: item.startDate,
          totalHoursBooked:
            result[i].totalHoursBooked +
            getDurationTime(
              moment(item.startTime, "HH:mm:ss"),
              moment(item.endTime, "HH:mm:ss")
            ),
        };
      }
    });

    if (!changed) {
      result.push({
        id: item.id,
        startDate: item.startDate,
        totalHoursBooked: getDurationTime(
          moment(item.startTime, "HH:mm:ss"),
          moment(item.endTime, "HH:mm:ss")
        ),
      });
    }
  });

  return result;
};

export const getStatusInfo = (statusInfo, status) => {
  return statusInfo.find((elm) => elm.id === status);
};

export const getExportData = (data, code) => {
  const exportArray = [];
  data.forEach((item) => {
    const itemObject = _getObjectItem(item, code);
    if (itemObject) {
      exportArray.push(itemObject);
    }
  });
  return exportArray;
};

export const _getObjectItem = (item, code) => {
  if (item.id) {
    const {
      status,
      invoiceNumber,
      provider,
      createdAt,
      dueDate,
      totalAmount,
      comment,
      amount,
    } = item;
    const _createdDate = getDateTime(createdAt, "MMM DD,YYYY HH:mm:ss");
    const _invoiceNumber = invoiceNumber;
    const _status = getStatusInfo(cons.BILLING_STATUS_INFO, status).status;
    const _providerFullName = getUserFullName(provider);
    const _dueDate = getDateTime(dueDate, "MMM DD,YYYY HH:mm:ss");
    const _totalAmount = totalAmount;
    const _comment = comment;
    const _amount = amount;
    switch (Number(code)) {
      case cons.BILLING_EXPORT_CSV_CODE.ADMIN_BOOKING_INVOICE:
        return {
          created_date: _createdDate,
          invoice_number: _invoiceNumber,
          provider_name: _providerFullName,
          DUE_date: _dueDate,
          status: _status || "",
          total: _totalAmount,
        };
      case cons.BILLING_EXPORT_CSV_CODE.PROVIDER_BOOKING_INVOICE:
        return {
          created_date: _createdDate,
          invoice_number: _invoiceNumber,
          DUE_date: _dueDate,
          status: _status || "",
          total: _totalAmount,
        };
      case cons.BILLING_EXPORT_CSV_CODE.ADMIN_MANUAL_INVOICE:
        return {
          created_date: _createdDate,
          invoice_number: _invoiceNumber,
          provider_name: _providerFullName,
          comment: _comment,
          status: _status || "",
          amount: _amount,
        };
      case cons.BILLING_EXPORT_CSV_CODE.PROVIDER_MANUAL_INVOICE:
        return {
          created_date: _createdDate,
          invoice_number: _invoiceNumber,
          comment: _comment,
          status: _status || "",
          amount: _amount,
        };
      default:
        return {};
    }
  }
  return false;
};

export const getExportDataMyBookingProvider = (data, venueData) => {
  const exportArray = [];
  data.forEach((item) => {
    if (item.id) {
      const { space, startDate, startTime, endTime, status, appointments } =
        item;
      const _vunue_space_name = getSpaceName(space, venueData);
      const _date = getDate(startDate, "Do MMM YYYY");
      const _start_time = getTime(`${startDate} ${startTime}`);
      const _end_time = getTime(`${startDate} ${endTime}`);
      const _duration = getDurationTime(
        moment(startTime, "HH:mm:ss"),
        moment(endTime, "HH:mm:ss")
      );
      const _unit = _duration > 1 ? "hours" : "hour";
      const _duration_full = `${_duration} ${_unit}`;
      const _status = getStatusInfo(cons.BOOKING_STATUS_INFO, status).status;
      const _client_appointments = getClientAppointments(
        appointments,
        startDate
      );
      exportArray.push({
        venue_space_name: _vunue_space_name,
        date: _date,
        start_time: _start_time,
        end_time: _end_time,
        duration: _duration_full,
        status: _status,
        client_appointments: _client_appointments,
      });
    }
  });
  return exportArray;
};

export const fixNumber = (number, amount = 2) => {
  return Number(number).toFixed(amount);
};

/**
 * get array of bookings information at admin dasdboard
 * @param {*} data data of bookings
 * @param {*} venueData all venue in this app
 */
export const getBookingsExportData = (data, venueData) => {
  const exportArray = [];
  data &&
    data.forEach((item) => {
      const itemObject = _getBookingsObjectItem(item, venueData);
      if (itemObject) {
        exportArray.push(itemObject);
      }
    });
  return exportArray;
};

/**
 * Data analysis and return the corresponding information
 * @param {*} item each item of bookings
 * @param {*} venueData all venue in this app
 */
const _getBookingsObjectItem = (item, venueData) => {
  if (item.id) {
    const { space, provider, startDate, startTime, endTime, spacePrice } = item;
    let venueOfSpace = {};
    let spaceItem = {};
    if (space) {
      spaceItem = space;
      venueOfSpace = venueData && venueData.find((v) => v.id === space.venue);
    } else {
      venueOfSpace.name = "Non-Ethera";
      spaceItem.name = "";
    }
    const _duration = getDurationTime(
      moment(startTime, "HH:mm:ss"),
      moment(endTime, "HH:mm:ss")
    );
    const _unit = _duration > 1 ? "hours" : "hour";
    const _venueName = venueOfSpace.name || "Non-Ethera";
    const _nameSpace = spaceItem.name || "";
    const _providerFullName = getUserFullName(provider);
    const _startDate = getDateTime(startDate, "MMM DD,YYYY");
    const _startTime = startTime;
    const _endTime = endTime;
    const _spacePrice = spacePrice;

    return {
      venue: _venueName,
      space: _nameSpace,
      provider_name: _providerFullName,
      start_date: _startDate,
      startTime: _startTime,
      endTime: _endTime,
      duration: `${_duration} ${_unit}`,
      cost: _spacePrice,
    };
  }
  return false;
};

export const stripHtml = (content) => {
  const tmp = document.createElement("DIV");
  tmp.innerHTML = content;
  const textContent = tmp.textContent || tmp.innerText || "";
  return textContent.trim();
};

export const splitContent = (content, amount = 5) => {
  const splitContent = content.split(" ").slice(0, amount).join(" ");
  const showMore = content.split(" ").length > amount ? "..." : "";
  return splitContent.concat(showMore);
};

export function getBookingDate(booking, stringDate) {
  if (stringDate.match("T")) {
    return stringDate;
  }
  return `${booking.startDate}T${stringDate}`;
}

export const settingCalendarUIWithDayView = (spaces, calendar) => {
  if (
    spaces &&
    spaces.length > 0 &&
    calendar.state.viewType === cons.CALENDAR_VIEW_TYPE.DAY
  ) {
    const widthOfOneSpace = 150; // px
    const sumSpaces = spaces.length;
    const calContainer = document.getElementById("calendar");
    const widthCal = calContainer.scrollWidth;
    const fc_timeGrid_view =
      calContainer.getElementsByClassName("fc-timeGrid-view")[0];
    const widthTimeLine =
      fc_timeGrid_view.ownerDocument.getElementsByClassName("fc-axis")[0]
        .scrollWidth;
    const widthAllRoom = widthCal - widthTimeLine;

    if (widthAllRoom && widthAllRoom > 0) {
      if (sumSpaces * widthOfOneSpace > widthAllRoom) {
        const percentTable =
          sumSpaces * (widthOfOneSpace / (widthAllRoom / 100));
        calContainer.getElementsByTagName("table")[0].style.width =
          percentTable + "%";
        fc_timeGrid_view.style.overflow = "auto";
      }
    }
  }
};

export const numericCompare = new Intl.Collator(undefined, {
  numeric: true,
  sensitivity: "base",
}).compare;

export const getImageMeta = (url) => {
  return new Promise((resolve, reject) => {
    let img = new Image();
    img.onload = () => resolve(img);
    img.onerror = reject;
    img.src = url;
  });
};

export const getTextValue = (text) => {
  if (!text || text === "null" || text === "undefined") {
    return "";
  }
  return text || "";
};

export const formatPhoneNumber = (phoneNumber) => {
  var cleaned = ("" + phoneNumber).replace(/\D/g, "");
  var match = cleaned.match(/^(\d{3})(\d{3})(\d{4})$/);
  if (match) {
    return "(" + match[1] + ") " + match[2] + "-" + match[3];
  }
  return phoneNumber;
};
