const actionTypes = {
    GET_CLIENT_APPOINTMENT: "GET_CLIENT_APPOINTMENT",
    GET_CLIENT_APPOINTMENT_SUCCESS: 'GET_CLIENT_APPOINTMENT_SUCCESS',
    GET_CLIENT_APPOINTMENT_FAIL: 'GET_CLIENT_APPOINTMENT_FAIL',
}

export default actionTypes

export const getListAppointment = (filters) => ({ type: actionTypes.GET_CLIENT_APPOINTMENT, payload: { filters } })
