import {
    NOTIFICATION_ACTIONS
} from "../actions";

const initState = {
    loading: false,
    data: {}
};

export default function notificationReducer(state = initState, action) {
    let { type, payload } = action;
    switch (type) {
        // Get NOTIFICATION List
        case NOTIFICATION_ACTIONS.GET_PROVIDER_NOTIFICATIONS:
            return {
                ...state,
                loading: true,
            }
        case NOTIFICATION_ACTIONS.GET_PROVIDER_NOTIFICATIONS_SUCCESS:
            return {
                ...state,
                data: payload,
                loading: false,
            }
        case NOTIFICATION_ACTIONS.GET_PROVIDER_NOTIFICATIONS_FAIL:
            return {
                ...state,
                loading: false,
            }

        // update NOTIFICATION 
        case NOTIFICATION_ACTIONS.UPDATE_PROVIDER_NOTIFICATIONS:
            return {
                ...state,
                loading: true,
            }
        case NOTIFICATION_ACTIONS.UPDATE_PROVIDER_NOTIFICATIONS_SUCCESS:
            if (state.data.result) {
                const listNotification = [...state.data.result];
                let notificationUpdate = listNotification.map(item => item.id === payload.id ? payload : item);
                return {
                    data: { ...state.data, result: notificationUpdate },
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case NOTIFICATION_ACTIONS.UPDATE_PROVIDER_NOTIFICATIONS_FAIL:
            return {
                ...state,
                loading: false,
            }

        // MARK ALL READ NOTIFICATION 
        case NOTIFICATION_ACTIONS.MARK_ALL_READ_NOTIFICATIONS:
            return {
                ...state,
                loading: true,
            }
        case NOTIFICATION_ACTIONS.MARK_ALL_READ_NOTIFICATIONS_SUCCESS:
            if (state.data.result) {
                let notificationUpdate = state.data.result.map(item => {
                    return {
                        ...item,
                        read: true
                    }
                });
                return {
                    data: { ...state.data, result: notificationUpdate },
                    loading: false,
                }
            }
            else {
                return {
                    ...state,
                    data: payload,
                    loading: false,
                }
            }
        case NOTIFICATION_ACTIONS.MARK_ALL_READ_NOTIFICATIONS_FAIL:
            return {
                ...state,
                loading: false,
            }

        default:
            return state;
    }
}
