import { call, put, takeLatest } from "redux-saga/effects";
import { LOCKS_ACTIONS } from '../actions'
import lockApi from '../../api/lockApi'
import { toast } from 'react-toastify';

function* getLocks({payload}) {
    const { venueId, filters } = payload;
    try {
        const data = yield call(lockApi.list.bind(this, venueId, filters));
        yield put({ type: LOCKS_ACTIONS.GET_LOCKS_SUCCESS, payload: { data } })
    } catch (error) {
        yield put({ type: LOCKS_ACTIONS.GET_LOCKS_FAIL, payload: { error } })
    }
}

function* addLock({payload}) {
    const { data } = payload;
    try {
        const result = yield call(lockApi.create.bind(this, data));
        yield put({ type: LOCKS_ACTIONS.ADD_LOCK_SUCCESS, payload: { result } })
        toast.success(`Lock "${data.name}" added successfully`);
        yield put({ type: LOCKS_ACTIONS.GET_LOCKS, payload: { venueId: data.venue } });
    } catch (error) {
        toast.error(`Couldn't add lock ${data.name}`);
        yield put({ type: LOCKS_ACTIONS.ADD_LOCK_FAIL, payload: { error } })
    }
}

function* updateLock({payload}) {
    const { data } = payload;
    try {
        const result = yield call(lockApi.update.bind(this, data));
        yield put({ type: LOCKS_ACTIONS.UPDATE_LOCK_SUCCESS, payload: { result } })
        toast.success(`Lock "${data.name}" updated successfully`);
        yield put({ type: LOCKS_ACTIONS.GET_LOCKS, payload: { venueId: data.currentVenueId } });
    } catch (error) {
        toast.error(`Couldn't update lock ${data.name}`);
        yield put({ type: LOCKS_ACTIONS.UPDATE_LOCK_FAIL, payload: { error } })
    }
}

function* deleteLock({payload}) {
    const { data } = payload;
    try {
        const result = yield call(lockApi.delete.bind(this, data.venue, data.id));
        yield put({ type: LOCKS_ACTIONS.DELETE_LOCK_SUCCESS, payload: { result } })
        toast.success(`Lock "${data.name}" deleted successfully`);
        yield put({ type: LOCKS_ACTIONS.GET_LOCKS, payload: { venueId: data.venue } });
    } catch (error) {
        toast.error(`Couldn't delete lock ${data.name}`);
        yield put({ type: LOCKS_ACTIONS.DELETE_LOCK_FAIL, payload: { error } })
    }
}

function* testLock({payload}) {
    const { venueId, token } = payload;
    try {
        const result = yield call(lockApi.test.bind(this, venueId, token));
        yield put({ type: LOCKS_ACTIONS.TEST_LOCK_SUCCESS, payload: { result } })
        toast.success("Lock command successfully sent")
    } catch (error) {
        yield put({ type: LOCKS_ACTIONS.TEST_LOCK_FAIL, payload: { error } })
        toast.error("Error on lock test command")
    }
}

function* unlockLock({payload}) {
    const { venueId, lockId } = payload;
    try {
        const result = yield call(lockApi.unlock.bind(this, venueId, lockId));
        yield put({ type: LOCKS_ACTIONS.UNLOCK_LOCK_SUCCESS, payload: { result } })
        toast.success("Lock command successfully sent")
    } catch (error) {
        yield put({ type: LOCKS_ACTIONS.UNLOCK_LOCK_FAIL, payload: { error } })
        toast.error("Error on lock test command")
    }
}


export function* watchLocksAsync() {
    yield takeLatest(LOCKS_ACTIONS.GET_LOCKS, getLocks);
    yield takeLatest(LOCKS_ACTIONS.ADD_LOCK, addLock);
    yield takeLatest(LOCKS_ACTIONS.UPDATE_LOCK, updateLock);
    yield takeLatest(LOCKS_ACTIONS.DELETE_LOCK, deleteLock);
    yield takeLatest(LOCKS_ACTIONS.TEST_LOCK, testLock);
    yield takeLatest(LOCKS_ACTIONS.UNLOCK_LOCK, unlockLock);
}
