import { call, put, takeLatest } from "redux-saga/effects";
import jwtDecode from 'jwt-decode';
import { AUTH_ACTIONS } from '../actions'
import authApi from '../../api/authApi'
import authHelper from '../../helpers/AuthHelper'
import { toast } from 'react-toastify';

function* requestAuth(payload) {
    const { email, password, type } = payload.data;
    try {
        const data = yield call(postRequestAuth.bind(this, { email, password }))

        if (!isValidPortal(type, data.token)) {
            throw (new Error("Invalid portal"))
        }

        authHelper.saveToken(data.token);
        toast.success('Login successfully');
        yield put({ type: AUTH_ACTIONS.LOGIN_SUCCESS, payload: data })
    } catch (error) {
        toast.error('Login failed');
        yield put({ type: AUTH_ACTIONS.LOGIN_FAIL, payload: { error } })
    }
}

function isValidPortal(type, token) {
    const userInfo = jwtDecode(token)
    return (type === "admin" && userInfo.is_staff)
        || (type === "provider" && userInfo.is_provider)
        || (type === "client" && userInfo.is_client);
}

async function postRequestAuth(payload) {
    return await authApi.login(payload);
}

function* requestNewToken(actionData) {
    try {
        const data = yield call(postNewToken.bind(this, actionData.token))
        authHelper.saveToken(data.token);
        yield put({ type: AUTH_ACTIONS.REFRESH_TOKEN_SUCCESS, payload: data })
    } catch (error) {
    }
}

function postNewToken(token) {
    return authApi.refreshToken(token).then(response => {
        return response
    })
}

export function* watchAuthAsync() {
    yield takeLatest(AUTH_ACTIONS.LOGIN, requestAuth);
    yield takeLatest(AUTH_ACTIONS.REFRESH_TOKEN, requestNewToken);
}
