import React from "react";
import { Input } from "reactstrap";
import FormInput from './FormInput';

export default class FormInputCorrected extends FormInput {

  render() {
    const { type, name, errors, defaultValue, disabled, maxlengths, autoComplete, placeholder, value } = this.props;
    const { internalErrors } = this.state;
    const fieldErrors = this.getArrayError(internalErrors, errors || []);
    const isError = fieldErrors && fieldErrors.length > 0;
    return (
      <div className={`form__controller ${isError ? 'form__controller--error' : ''}`}>
        {
          value !== null && value !== undefined ?
            <Input
              type={type}
              name={name}
              onChange={e => this.handleOnChange(e)}
              onBlur={e => this.handleOnBlur(e)}
              value={value}
              disabled={disabled}
              maxlengths={maxlengths}
              autoComplete={autoComplete || "off"}
              placeholder={placeholder}
              required={this.props.required}
            /> :
            <Input
              type={type}
              name={name}
              onChange={e => this.handleOnChange(e)}
              onBlur={e => this.handleOnBlur(e)}
              defaultValue={defaultValue}
              disabled={disabled}
              maxlengths={maxlengths}
              autoComplete={autoComplete || "off"}
              placeholder={placeholder}
              required={this.props.required}
            />
        }

        {
          isError &&
          <div className="form__errors">
            <div className="form__errors__list">
              <div className="form__errors__item">
                {fieldErrors[0]}
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}
