import actions from '../actions/adminDashboardActions'
import constant from '../../constants/index'

let initState = () => ({
    calendar: null,
    loading: false,
    venues: [],
    spaces: [],
    bookings: [],

    selectedVenue: null,
    selectedSpaces: [],
    currentBooking: null,
    isShowEditBooking: false,
    validSlotDuration: 1 * 60 * 60 * 1000,
    tier: 1,
    isGetedTier: false,

    activeStart: new Date(),
    activeEnd: new Date(),
    dateTitle: "",
    calendarViewType: constant.CALENDAR_VIEW_TYPE.MONTH,
})

export default function providerDashboardReducer(state = initState(), action) {
    let { type, payload } = action;
    switch (type) {
        case actions.SET_VENUES:
            return {
                ...state,
                venues: payload.venues
            }
        case actions.SET_SPACES:
            return {
                ...state,
                spaces: payload.spaces
            }
        case actions.INIT_DATA: {
            const { calendar } = payload;
            const { title, activeStart, activeEnd } = calendar.view;

            return {
                ...state,
                loading: true,
                activeStart,
                activeEnd,
                calendar,
                dateTitle: title,
            }
        }
        case actions.INIT_DATA_SUCCESS: {
            const { venues, bookings } = payload;
            const selectedVenue = constant.DEFAULT_VENUE.ID;
            venues.unshift({ id: constant.DEFAULT_VENUE.ID, name: constant.DEFAULT_VENUE.NAME})
            return {
                ...state,
                venues,
                selectedVenue,
                bookings,
                loading: false,
            }
        }
        case actions.CHANGE_SPACES: {
            const { spaceIds } = payload;
            return {
                ...state,
                selectedSpaces: spaceIds,
                bookings: [],
                loading: true,
            }
        }
        case actions.CHANGE_SPACES_SUCCESS: {
            const { spaceIds, bookings} = payload;
            return {
                ...state,
                selectedSpaces: spaceIds,
                loading: false,
                bookings
            }
        }
        case actions.CHANGE_SPACES_FAIL: {
            return {
                ...state,
                loading: false
            }
        }
        case actions.CHANGE_VENUE: {
            return {
                ...state,
                bookings: [],
                loading: true
            }
        }
        case actions.CHANGE_VENUE_SUCCESS: {
            const { venueId, spaces, bookings } = payload;

            // Remove all resource (room), before re-render new resource
            const allResource = state.calendar.getResources()
            allResource.forEach(r => {
                r.remove();
            })

            spaces.forEach(item => {
                state.calendar.addResource({ id: item.id, title: item.name });
            });

            return {
                ...state,
                spaces,
                selectedSpaces: [],
                selectedVenue: venueId,
                loading: false,
                bookings
            }
        }
        case actions.SHOW_DETAIL_BOOKING: {
            const { event } = payload;
            const currentBooking = {
                startTime: event.end,
                endTime: event.start,
                id: event.id,
                providerId: event.extendedProps.providerId,
                space: event.extendedProps.space,
                spacePrice: event.extendedProps.spacePrice,
                provider: event.extendedProps.provider,
            };
            return {
                ...state,
                currentBooking,
                isShowEditBooking: true,
                loading: true
            }
        }
        case actions.SHOW_DETAIL_BOOKING_SUCCESS: {
            const { tier } = payload;
            return {
                ...state,
                tier,
                isGetedTier: true,
                loading: false
            }
        }
        case actions.CLOSE_EDIT_BOOKING_MODAL: {
            return {
                ...state,
                isShowEditBooking: false,
                isGetedTier: false,
            }
        }
        case actions.CANCEL_BOOKING_BY_ADMIN: {
            return {
                ...state,
                loading: true
            }
        }
        case actions.CANCEL_BOOKING_BY_ADMIN_SUCCESS: {
            const { bookingId, bookings } = payload;
            const bookingsUpdated = bookings.filter(function (item) {
                return item.id !== bookingId
            })
            return {
                ...state,
                isShowEditBooking: false,
                loading: false,
                bookings: bookingsUpdated,
            }
        }
        case actions.CANCEL_BOOKING_BY_ADMIN_FAIL: {
            return {
                ...state,
                loading: false
            }
        }
        case actions.DELETE_BOOKING_BY_ADMIN: {
            return {
                ...state,
                loading: true
            }
        }
        case actions.DELETE_BOOKING_BY_ADMIN_SUCCESS: {
            const { bookingId, bookings } = payload;
            const bookingsUpdated = bookings.filter(function (item) {
                return item.id !== bookingId
            })
            return {
                ...state,
                isShowEditBooking: false,
                loading: false,
                bookings: bookingsUpdated,
            }
        }
        case actions.DELETE_BOOKING_BY_ADMIN_FAIL: {
            return {
                ...state,
                loading: false
            }
        }
        case actions.UPDATE_BOOKING_BY_ADMIN: {
            return {
                ...state,
                loading: true
            }
        }
        case actions.UPDATE_BOOKING_BY_ADMIN_SUCCESS: {
            const { bookings } = payload
            return {
                ...state,
                isShowEditBooking: false,
                loading: false,
                bookings: bookings,
            }
        }
        case actions.UPDATE_BOOKING_BY_ADMIN_FAIL: {
            return {
                ...state,
                loading: false
            }
        }
        case actions.ON_CALENDAR_CHANGE: {
            const { title, activeStart, activeEnd } = state.calendar.view;
            return {
                ...state,
                activeStart,
                activeEnd,
                dateTitle: title,
                loading: true,
            }
        }
        case actions.ON_CALENDAR_CHANGE_SUCCESS: {
            const { activeStart, activeEnd } = state.calendar.view;
            const { selectedSpaces, selectedVenue, bookings } = payload;

            return {
                ...state,
                activeStart,
                activeEnd,
                loading: false,
                selectedSpaces,
                selectedVenue,
                bookings,
                calendarViewType: state.calendar.state.viewType,
            }
        }
        case actions.ON_CALENDAR_CHANGE_FAIL: {
            const { activeStart, activeEnd } = state.calendar.view;
            return {
                ...state,
                activeStart,
                activeEnd,
                loading: false,
            }
        }
        case actions.RELOAD_BOOKINGS: {
            return {
                ...state,
                loading: true,
            }
        }
        case actions.RELOAD_BOOKINGS_SUCCESS: {
            const { bookings } = payload;
            return {
                ...state,
                bookings,
                loading: false,
            }
        }
        case actions.RELOAD_BOOKINGS_FAIL: {
            // TODO
            return {
                ...state,
                loading: false,
            }
        }
        default:
            return state;
    }
}
