import { call, put, takeLatest } from "redux-saga/effects";
import { INVOICE_ACTIONS } from "../actions";
import invoiceApi from '../../api/invoiceApi';
import { toast } from 'react-toastify'

function* getListInvoice({ payload }) {
    try {
        const data = yield call(invoiceApi.listInvoice.bind(this, payload))
        yield put({ type: INVOICE_ACTIONS.GET_CLIENT_INVOICES_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: INVOICE_ACTIONS.GET_CLIENT_INVOICES_FAIL })
    }
}

function* getBookingInvoices({ payload }) {
    const { bookingId } = payload
    try {
        const data = yield call(invoiceApi.listBookingInvoices.bind(this, bookingId))
        yield put({ type: INVOICE_ACTIONS.GET_BOOKING_INVOICES_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: INVOICE_ACTIONS.GET_BOOKING_INVOICES_FAIL })
    }
}

function* createAppointmentInvoice({ payload }) {
    try {
        const data = yield call(invoiceApi.createInvoice.bind(this, payload))
        yield put({ type: INVOICE_ACTIONS.CREATE_APPOINTMENT_INVOICE_SUCCESS, payload: data })
        toast.success("Create invoice success")
    } catch (error) {
        toast.error("Create invoice fail")
        yield put({ type: INVOICE_ACTIONS.CREATE_APPOINTMENT_INVOICE_FAIL })
    }
}

function* updateInvoiceStatus({ payload }) {
    try {
        const data = yield call(invoiceApi.updateInvoice.bind(this, payload))
        yield put({ type: INVOICE_ACTIONS.UPDATE_INVOICE_STATUS_SUCCESS, payload: data })
        toast.success("Cancel invoice success")
    } catch (error) {
        toast.error("Cancel invoice fail")
        yield put({ type: INVOICE_ACTIONS.UPDATE_INVOICE_STATUS_FAIL })
    }
}

function* addPayment({ payload }) {
    try {
        const data = yield call(_addPayment.bind(this, payload))
        yield put({ type: INVOICE_ACTIONS.CLIENT_ADD_PAYMENT_SUCCESS, payload: data })
    } catch (error) {
        yield put({ type: INVOICE_ACTIONS.CLIENT_ADD_PAYMENT_FAIL })
    }
}

function _addPayment(payload) {
    return invoiceApi.addPayment(payload).then(response => {
        toast.success("Add payment success")
        return response
    }).catch(error => {
        toast.error(error.response.data.nonFieldErrors[0])
    })
}

export function* watchInvoiceAsync() {
    yield takeLatest(INVOICE_ACTIONS.GET_CLIENT_INVOICES, getListInvoice);
    yield takeLatest(INVOICE_ACTIONS.GET_BOOKING_INVOICES, getBookingInvoices);
    yield takeLatest(INVOICE_ACTIONS.CREATE_APPOINTMENT_INVOICE, createAppointmentInvoice);
    yield takeLatest(INVOICE_ACTIONS.UPDATE_INVOICE_STATUS, updateInvoiceStatus);

    yield takeLatest(INVOICE_ACTIONS.CLIENT_ADD_PAYMENT, addPayment);
}
