const actionTypes = {
    INIT_DATA: "INIT_DATA",
    INIT_DATA_SUCCESS: "INIT_DATA_SUCCESS",
    INIT_DATA_FAIL: "INIT_DATA_FAIL",
    SET_VENUES: "SET_VENUES",
    SET_SPACES: "SET_SPACES",
    CHANGE_VENUE: "CHANGE_VENUE",
    CHANGE_VENUE_FOR_MODAL: "CHANGE_VENUE_FOR_MODAL",
    CHANGE_VENUE_FOR_MODAL_SUCCESS: "CHANGE_VENUE_FOR_MODAL_SUCCESS",
    CHANGE_VENUE_SUCCESS: "CHANGE_VENUE_SUCCESS",
    CHANGE_SPACES: "CHANGE_SPACES",
    CHANGE_SPACES_SUCCESS: "CHANGE_SPACES_SUCCESS",
    ADD_NEW_BOOKING: "ADD_NEW_BOOKING",
    ADD_NEW_BOOKING_CANCEL: "ADD_NEW_BOOKING_CANCEL",
    BOOK_SPACE: "BOOK_SPACE",
    BOOK_SPACE_SUCCESS: "BOOK_SPACE_SUCCESS",
    BOOK_SPACE_FAIL: "BOOK_SPACE_FAIL",
    LOOKUP_AVAILABLE_SLOT: "LOOKUP_AVAILABLE_SLOT",
    LOOKUP_AVAILABLE_SLOT_SUCCESS: "LOOKUP_AVAILABLE_SLOT_SUCCESS",
    CLEAR_LOOKUP_AVAILABLE_SLOT: "CLEAR_LOOKUP_AVAILABLE_SLOT",
    CHANGE_AVAILABLE_SLOT: "CHANGE_AVAILABLE_SLOT",
    CHANGE_AVAILABLE_SLOT_SUCCESS: "CHANGE_AVAILABLE_SLOT_SUCCESS",
    ON_CALENDAR_CHANGE: "ON_CALENDAR_CHANGE",
    ON_CALENDAR_CHANGE_SUCCESS: "ON_CALENDAR_CHANGE_SUCCESS",
    ON_CALENDAR_CHANGE_FAIL: "ON_CALENDAR_CHANGE_FAIL",
    RELOAD_BOOKINGS: "RELOAD_BOOKINGS",
    RELOAD_BOOKINGS_SUCCESS: "RELOAD_BOOKINGS_SUCCESS",
    RELOAD_BOOKINGS_FAIL: "RELOAD_BOOKINGS_FAIL",
    CLEAR_PROVIDER_DASHBOARD_STATE: "CLEAR_PROVIDER_DASHBOARD_STATE",
    SET_USER_TIER: "SET_USER_TIER",
    ADD_APPOINTMENT: "ADD_APPOINTMENT",
    ADD_APPOINTMENT_SUCCESS: "ADD_APPOINTMENT_SUCCESS",
    ADD_APPOINTMENT_FAIL: "ADD_APPOINTMENT_FAIL",
    EDIT_APPOINTMENT_MODAL: "EDIT_APPOINTMENT_MODAL",
    EDIT_APPOINTMENT: "EDIT_APPOINTMENT",
    EDIT_APPOINTMENT_SUCCESS: "EDIT_APPOINTMENT_SUCCESS",
    EDIT_APPOINTMENT_FAIL: "EDIT_APPOINTMENT_FAIL",
    DELETE_APPOINTMENT: "DELETE_APPOINTMENT",
    DELETE_APPOINTMENT_SUCCESS: "DELETE_APPOINTMENT_SUCCESS",
    DELETE_APPOINTMENT_FAIL: "DELETE_APPOINTMENT_FAIL",
    CLEAR_APPOINTMENt_ERROR: "CLEAR_APPOINTMENt_ERROR",
    TOGGLE_ALL_ROOM: "TOGGLE_ALL_ROOM",

    DAY_BOOKINGS: "DAY_BOOKINGS",
    DAY_BOOKINGS_SUCCESS: "DAY_BOOKINGS_SUCCESS",
    DAY_BOOKINGS_FAILED: "DAY_BOOKINGS_FAILED",
}

export default actionTypes

export const initData = (filters, calendar) => ({ type: actionTypes.INIT_DATA, payload: { filters, calendar } })
export const changeVenue = (venueId) => ({ type: actionTypes.CHANGE_VENUE, payload: { venueId } })
export const changeVenueForModal = (venueId) => ({ type: actionTypes.CHANGE_VENUE_FOR_MODAL, payload: { venueId } })
export const changeSpaces = (spaceIds) => ({ type: actionTypes.CHANGE_SPACES, payload: { spaceIds } })
export const addNewBooking = (dateTimeInfo) => ({ type: actionTypes.ADD_NEW_BOOKING, payload: { dateTimeInfo } })
export const cancelAddNewBooking = () => ({ type: actionTypes.ADD_NEW_BOOKING_CANCEL })
export const bookSpace = (data, onSuccess) => ({ type: actionTypes.BOOK_SPACE, payload: { data, onSuccess } })
export const onCalendarChange = (calendar) => ({ type: actionTypes.ON_CALENDAR_CHANGE, payload: { calendar } })
export const lookupAvailableSlot = (duration) => ({ type: actionTypes.LOOKUP_AVAILABLE_SLOT, payload: { duration } })
export const clearLookupAvailableSlot = (duration) => ({ type: actionTypes.CLEAR_LOOKUP_AVAILABLE_SLOT, payload: { duration } })
export const clearState = () => ({ type: actionTypes.CLEAR_PROVIDER_DASHBOARD_STATE })
export const addAppointment = (data, onSuccess) => ({ type: actionTypes.ADD_APPOINTMENT, payload: { data, onSuccess } })
export const editAppointment = (data, onSuccess) => ({ type: actionTypes.EDIT_APPOINTMENT, payload: { data, onSuccess } })
export const deleteAppointment = (appointment, onSuccess) => ({ type: actionTypes.DELETE_APPOINTMENT, payload: { appointment, onSuccess } })
export const editAppointmentModal = (data, onSuccess) => ({ type: actionTypes.EDIT_APPOINTMENT_MODAL, payload: { data, onSuccess } });

export const getDayBookings = (date) => ({type: actionTypes.DAY_BOOKINGS, payload: {date}});

