import client from '../helpers/HttpClient'
import { urlQueryFromObject } from '../helpers/UtilsHelper';
const endpoint = '/client-invoices';
const manual_invoice_endpoint = '/manual-invoices';

export default {
    async listInvoice(payload) {
        const { filters } = payload
        const query = urlQueryFromObject(filters);
        let path = `${endpoint}${query}`;
        let response = await client.get(path);
        return response.data;
    },
    async addPayment(payload) {
        const { invoiceId, data } = payload
        let path = `${endpoint}/${invoiceId}/client-payment`;
        let response = await client.post(path, data);
        return response.data;
    },
    async listPayment(payload) {
        const { invoiceId, data } = payload
        let path = `${endpoint}/${invoiceId}/client-payment`;
        let response = await client.get(path, data);
        return response.data;
    },
    async invoiceDetail(id) {
        let path = `${endpoint}/${id}`;
        let response = await client.get(path);
        return response.data;
    },
    async listBookingInvoices(bookingId) {
        let path = `${endpoint}?booking=${bookingId}`;
        let response = await client.get(path);
        return response.data;
    },
    async createInvoice(payload) {
        let path = `${endpoint}`;
        let response = await client.post(path, payload);
        return response.data;
    },
    async updateInvoice({ status, invoiceId }) {
        let path = `${endpoint}/${invoiceId}`;
        let response = await client.patch(path, { status });
        return response.data;
    },

    async createManualInvoice(payload) {
        let path = `${manual_invoice_endpoint}`;
        let response = await client.post(path, payload);
        return response.data;
    },
    async listManualInvoice(filters) {
        const query = urlQueryFromObject(filters);
        let path = `${manual_invoice_endpoint}${query}`;
        let response = await client.get(path);
        return response.data;
    },
    async manualInvoiceDetail(id) {
        let path = `${manual_invoice_endpoint}/${id}`;
        let response = await client.get(path);
        return response.data;
    },
    async update(data, partial = true) {
        const { id } = data;
        const path = `${manual_invoice_endpoint}/${id}`;
        let response = partial ? await client.patch(path, data) : await client.put(path, data)
        return response.data;
    },
    async chargeInvoice(invoiceId) {
        let path = `${manual_invoice_endpoint}/${invoiceId}/charge`;
        let response = await client.post(path);
        return response.data;
    },
}