import React, { useState } from 'react';
import stripeApi from '../../api/stripeApi';
import { Card, Container } from 'reactstrap';

function StripeNotification(props) {

    const [errContent, setErrContent] = useState(null);

    const urlParams = new URLSearchParams(props.location.search);
    const stripeErr = urlParams.get('error')
    const errDescription = urlParams.get('error_description')
    const code = urlParams.get('code')
    if (!stripeErr) {
        stripeApi.connectAccount(code).then(res => {
            if (res.error) {
                setErrContent(res.errorDescription);
            }
            else {
                window.close()
            }
        }).catch(err => {
            console.error(err);
        })
    }

    return (
        <Container>
            <Card className="text-center my-3" size="sm">
                {
                    stripeErr ? <span className="text-danger font-weight-bold m-3">{errDescription}</span> :
                        errContent ? <span className="text-danger font-weight-bold m-3">{errContent}</span>
                            : <span className="text-info font-weight-bold m-3">Connect Account Stripe Successful</span>
                }
            </Card>
        </Container>
    );
}

export default StripeNotification;


