const actionTypes = {
  GET_CLIENT_LIST: "GET_CLIENT_LIST",
  GET_CLIENT_LIST_SUCCESS: "GET_CLIENT_LIST_SUCCESS",
  GET_CLIENT_LIST_FAIL: "GET_CLIENT_LIST_FAIL",

  GET_CLIENT_DETAIL: "GET_CLIENT_DETAIL",
  GET_CLIENT_DETAIL_SUCCESS: "GET_CLIENT_DETAIL_SUCCESS",
  GET_CLIENT_DETAIL_FAIL: "GET_CLIENT_DETAIL_FAIL",
};

export default actionTypes;

export const getList = (filters) => ({
  type: actionTypes.GET_CLIENT_LIST,
  payload: { filters },
});
export const getClientDetail = (clientId) => ({
  type: actionTypes.GET_CLIENT_DETAIL,
  payload: { clientId },
});
