const actionTypes = {
    INIT_DATA: "aINIT_DATA",
    INIT_DATA_SUCCESS: "aINIT_DATA_SUCCESS",
    INIT_DATA_FAIL: "aINIT_DATA_FAIL",
    SET_VENUES: "aSET_VENUES",
    SET_SPACES: "aSET_SPACES",
    CHANGE_VENUE: "aCHANGE_VENUE",
    CHANGE_VENUE_SUCCESS: "aCHANGE_VENUE_SUCCESS",
    CHANGE_SPACES: "aCHANGE_SPACES",
    CHANGE_SPACES_SUCCESS: "aCHANGE_SPACES_SUCCESS",
    CHANGE_SPACES_FAIL: "aCHANGE_SPACES_FAIL",
    SHOW_DETAIL_BOOKING: "SHOW_DETAIL_BOOKING",
    SHOW_DETAIL_BOOKING_SUCCESS: "SHOW_DETAIL_BOOKING_SUCCESS",
    CLOSE_EDIT_BOOKING_MODAL: "CLOSE_EDIT_BOOKING_MODAL",
    CANCEL_BOOKING_BY_ADMIN: "CANCEL_BOOKING_BY_ADMIN",
    CANCEL_BOOKING_BY_ADMIN_SUCCESS: "CANCEL_BOOKING_BY_ADMIN_SUCCESS",
    CANCEL_BOOKING_BY_ADMIN_FAIL: "CANCEL_BOOKING_BY_ADMIN_FAIL",
    DELETE_BOOKING_BY_ADMIN: "DELETE_BOOKING_BY_ADMIN",
    DELETE_BOOKING_BY_ADMIN_SUCCESS: "DELETE_BOOKING_BY_ADMIN_SUCCESS",
    DELETE_BOOKING_BY_ADMIN_FAIL: "DELETE_BOOKING_BY_ADMIN_FAIL",
    UPDATE_BOOKING_BY_ADMIN: "UPDATE_BOOKING_BY_ADMIN",
    UPDATE_BOOKING_BY_ADMIN_SUCCESS: "UPDATE_BOOKING_BY_ADMIN_SUCCESS",
    UPDATE_BOOKING_BY_ADMIN_FAIL: "UPDATE_BOOKING_BY_ADMIN_FAIL",
    ON_CALENDAR_CHANGE: "aON_CALENDAR_CHANGE",
    ON_CALENDAR_CHANGE_SUCCESS: "aON_CALENDAR_CHANGE_SUCCESS",
    ON_CALENDAR_CHANGE_FAIL: "aON_CALENDAR_CHANGE_FAIL",
}

export default actionTypes

export const initData = (filters, calendar) => ({ type: actionTypes.INIT_DATA, payload: { filters, calendar } })
export const changeVenue = (venueId, callbackFn) => ({ type: actionTypes.CHANGE_VENUE, payload: { venueId, callbackFn } })
export const changeSpaces = (spaceIds) => ({ type: actionTypes.CHANGE_SPACES, payload: { spaceIds } })
export const showDetailBooking = (event) => ({ type: actionTypes.SHOW_DETAIL_BOOKING, payload: { event } })
export const closeEditBookingModal = () => ({ type: actionTypes.CLOSE_EDIT_BOOKING_MODAL })
export const cancelBooking = (bookingId, data, bookings) => ({ type: actionTypes.CANCEL_BOOKING_BY_ADMIN, payload: { bookingId, data, bookings} })
export const deleteBooking = (bookingId, bookings) => ({ type: actionTypes.DELETE_BOOKING_BY_ADMIN, payload: { bookingId, bookings}})
export const updateBooking = (bookingId, data, bookings) => ({ type: actionTypes.UPDATE_BOOKING_BY_ADMIN, payload: { bookingId, data, bookings } })
export const onCalendarChange = () => ({ type: actionTypes.ON_CALENDAR_CHANGE })
